.sg-btn {
	cursor: pointer;
	font-family: @sgFontFamilyCta;
	display: inline-block;
	outline: none;
    position: relative;
	text-align: center;
	text-decoration: none;
	text-transform: @sgTextTransformCta;

	.box-sizing(border-box);
	.remBorderRadiusAll(@sgMiscBorderRadiusDefault);
	.remFontSize(@sgFontSizeCta);
	.remMinHeight(@sgMiscHeightElements);
	.remPadding(@sgPaddingButtonTop, @sgPaddingButtonRight, @sgPaddingButtonBottom, @sgPaddingButtonLeft);
	.transition(@sgMiscDurationTransitionDefault);

    .sg-give-color-to-element(@sgColorButtonBackground, @sgColorButtonBackgroundHover, @sgColorButtonText, @sgColorButtonTextHover, @sgColorButtonBorder, @sgColorButtonBorderHover);
	
	&.sg-background-animation-slide-horizontally {
	    background-size: 200% 100%;
	    
	    &:hover {
	    	background-position: -100% 0;
	    }
	}

	&:hover {
		text-decoration: none;
	}
	
	&.sg-rotate-right {
		.transform(rotate(90deg));
	}

	&.sg-rotate-left {
		.transform(rotate(-90deg));
	}
	
	&.sg-rotate-half {
		.transform(rotate(180deg));
	}

	&.sg-flip {
		.transform(scale(-1));
	}		

	&.sg-inverted {
		.sg-give-color-to-element(@sgColorButtonBackgroundHover, @sgColorButtonBackground, @sgColorButtonTextHover, @sgColorButtonText, @sgColorButtonBorderHover, @sgColorButtonBorder);
	}
	
	&.sg-animated {
		overflow: hidden;
		padding: 0;
		position: relative;
		text-transform: none;

		.remHeight(@sgMiscHeightElements);

		.sg-caption,
		.sg-caption-hover {
			position: relative;

			.remHeight(@sgMiscHeightElements);
			.remLineHeight(@sgMiscHeightElements);
			.transition(margin-top @sgMiscDurationTransitionDefault);
		}

		&:hover {
			.sg-caption {
				.remMarginTop(-4.8);
			}

			.sg-animated-icon {
				.transition(margin-top @sgMiscDurationTransitionDefault);
				.remMarginTop(-5.1);
			}
		}
	}

	&.sg-btn-icon {
		.remHeight(@sgMiscHeightElements);
		.remWidth(@sgMiscHeightElements);

        padding: 0;
	}
	
    .sg-btn-icon-icon {
        left: 0;
        line-height: 1;
        position: absolute;
        top: 0;
        width: 100%;

        .remFontSize(@sgFontSizeCtaIcon);
        .remTop((@sgMiscHeightElements - @sgFontSizeCtaIcon - 0.2) / 2);
    }

	&.sg-disabled,
	&:disabled,
	&[disabled=disabled] {
		cursor: default;

        .sg-give-color-to-element(@sgColorTextfieldBackgroundDisabled, @sgColorTextfieldBackgroundDisabled, @sgColorTextfieldTextDisabled, @sgColorTextfieldTextDisabled, @sgColorTextfieldBorderDisabled, @sgColorTextfieldBorderDisabled);
	}

    &.sg-btn-file-input {
        &> input[type="file"] {
            cursor: pointer;
            height: 100%;
            margin: 0;
            padding: 0;
            position: absolute;
            right: 0;
            top: 0;
            width: 100%; 

            .opacity(0);
        }

        &.sg-disabled {
            &> input[type="file"] {
                display: none;
            }
        }
    }
}

.sg-btn-file-input-label {
    font-family: @sgFontFamilyFileInputLabel;

    .remFontSize(@sgFontSizeFileInputLabel);
    .remMarginLeft(@sgPaddingFileInputLabelLeft);
}

input[type="button"].sg-btn,
input[type="submit"].sg-btn,
button.sg-btn {
	vertical-align: baseline;

	.remPaddingTop(0);
}

// any icon within an Anchor or heading will show its hover state
a:hover,
.sg-header-1:hover,
.sg-header-2:hover,
.sg-header-3:hover,
.sg-header-4:hover {
	.sg-btn {
		&.sg-site-small {
			.sg-caption {
				.remMarginTop(-2.6);
			}
		}
		
		&.sg-site {
			.sg-caption {
				.remMarginTop(-4.8);
			}
		}
	}
}

