.container {
	.row {
		.remMarginBottom(1.5);
		.remMarginTop(1.5);
	}

	.ep-titles-row {
		.remMarginBottom(3);
	}
}

@epColorMarginAnnotations: #cf0072;

.ep-show-margin-annotation-left(@height, @paddingLeft) {
	position: relative;

	&:before,
	&:after {
		.sg-legend;

		color: @epColorMarginAnnotations;
		position: absolute;

		.box-sizing(content-box);
		.remHeight(@height);
		.remTop(-1 * @height + 0.4);
	}

	&:before {
        border-right: 1px solid @epColorMarginAnnotations;
        content: "@{height} rem";
        text-align: right;

		.remLeft(-8 - @paddingLeft);
		.remLineHeight(@height);
		.remPaddingRight(1);
		.remWidth(6);
	}

	&:after {
        border-bottom: 1px solid @epColorMarginAnnotations;
        border-top: 1px solid @epColorMarginAnnotations;
        content: "";

        .box-sizing(border-box);
		.remLeft(-1 - @paddingLeft);
		.remWidth(0.3);
	}
}

h1 {
	&.ep-styleguide {
		.remMarginBottom(9);
	}

	&.ep-with-margin-annotation {
		.ep-show-margin-annotation-left(@sgMarginTopH1, @sgPaddingLeftRightGridItemTextContent);
	}
}

h2 {
	&.ep-with-margin-annotation {
		.ep-show-margin-annotation-left(@sgMarginTopH2, @sgPaddingLeftRightGridItemTextContent);
	}
}

h3 {
	&.ep-with-margin-annotation {
		.ep-show-margin-annotation-left(@sgMarginTopH3, @sgPaddingLeftRightGridItemTextContent);
	}
}

h4 {
	&.ep-with-margin-annotation {
