body.ep-example-page {
	/*
		Fix for typography section:

		In the example page the styling of the legend should take precedence over any other styling in the typography section.
		The following lines accomplish this.
	*/
	.sg-legend {
		.sg-legend;
	}
}

p,
.sg-paragraph {
	&.ep-with-margin-annotation {
		.ep-show-margin-annotation-left(@sgMarginTopParagraph, @sgPaddingLeftRightGridItemTextContent);
	}
}

.sg-text-content {
	&.ep-with-margin-annotation {
		.remMarginTop(@sgMarginTopH1 + 2);
		.ep-show-margin-annotation-left(@sgPaddingBottomGridItemTextContent, @sgPaddingLeftRightGridItemTextContent);

		&:before,
		&:after {
			top: inherit;

			.remBottom(0.4);
		}
	}
}

