.hpbx-help-button-container {
    background-color: @sgColorGrey7;
    border: 1px solid @sgColorGrey4;
    overflow: hidden;

    .remBorderRadiusAll(0.5);
    .remMarginTop(2);
    .remPaddingAll(2);
}

.hpbx-help-category-container {
    border: 1px solid @sgColorGrey4;
    overflow: hidden;

    .box-shadow(0 5px 10px rgba(204,208,218,.5));
    .remBorderRadiusAll(0.5);
    .remMarginBottom(2);
    .remMarginTop(2);
}

.hpbx-help-category-image {
    background-color: @sgColorGrey7;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    border-bottom: 1px solid @sgColorGrey4;

    .remHeight(21.9);

    &.hpbx-help-category-call-scenario {
        background-image: url("../../dist/img/help/home/@{hpbxStyleguide}-category-call-scenario.png");
    }

    &.hpbx-help-category-device-help {
        background-image: url("../../dist/img/help/home/@{hpbxStyleguide}-category-device-help.png");
    }

    &.hpbx-help-category-faq {
        background-image: url("../../dist/img/help/home/@{hpbxStyleguide}-category-faq.png");
    }

    &.hpbx-help-category-first-time-user {
        background-image: url("../../dist/img/help/home/@{hpbxStyleguide}-category-first-time-user.png");
    }

    &.hpbx-help-category-installation {
        background-image: url("../../dist/img/help/home/@{hpbxStyleguide}-category-installation.png");
    }
}

.hpbx-help-category-content {
    .remPadding(2.1, 1.7, 2.1, 1.7);
}

.hpbx-help-category-title {
    .sg-header-4;

    color: @hpbxColorPrimary;
    margin: 0;
}

.hpbx-help-category-item {
    color: @sgColorGrey3;
    display: block;
    font-family: @sgFontFamilyHeadings;
    text-decoration: none;

    .remFontSize(@sgFontSizeH6);
    .remLineHeight(2.2);
    .remMarginTop(1.3);

    &:hover {
        color: @hpbxColorPrimary;
    }
}

.hpbx-help-button-container-button(@styleguide) when (@styleguide = 'upc-business') {
    .sg-btn { .sg-btn-secondary(); }
}

.hpbx-help-button-container-button(@styleguide) when (@styleguide = 'unitymedia') {
    .sg-btn { .sg-btn-secondary(); }
}

.hpbx-help-button-container-button(@styleguide) when (@styleguide = 'ziggo') {
    .sg-btn { .sg-btn-secondary(); }
}

.hpbx-help-button-container-button(@styleguide) when (@styleguide = 'virgin') {
    .sg-btn { .sg-btn-virgin-purple(); }
}

.hpbx-help-button-container-button(@styleguide) when (@styleguide = 'vtr') {
    .sg-btn { .sg-btn-secondary(); }
}

.hpbx-help-button-container-button {
    .hpbx-help-button-container-button(@hpbxStyleguide);
}

.hpbx-help-home-xxs() {
    .hpbx-help-button-container {
        .remMarginLeft(2);
        .remMarginRight(2);
    }

    .hpbx-help-button-container-button {
        .remMarginBottom(2);

        &.hpbx-last {
            margin-bottom: 0;
        }
    }

    .hpbx-help-category-container {
        .remMargin(2, 2, 4, 2);
    }
}

@media @XXS_MAX {
    .hpbx-help-home-xxs();
}

