.hpbx-help-header {
    background-color: @sgColorGrey7;
    border-bottom: 1px solid @sgColorGrey4;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;

    .remHeight(8.1);

    .hpbx-search-input-container {
        .remMarginTop(2.1);
    }
}

.hpbx-help-header-title {
    .sg-header-2;

    position: relative;

    .remMarginTop(2.3);
    .remPaddingLeft(4.3);
}

.hpbx-help-header-title-icon {
    position: absolute;
    top: 0;

    .remFontSize(3.6);
    .remLeft(0.1);
}

.hpbx-help-header-link {
    .sg-header-6;

    color: @sgColorGrey3;
    display: block;
    position: relative;
    text-decoration: none;

    .remMarginTop(3.1);
    .remPaddingLeft(2.3);

    &:hover {
        text-decoration: underline;
    }
}

.hpbx-help-header-link-icon {
    left: 0;
    position: absolute;

    .remFontSize(1.6);
    .remTop(0.2);
}

.hpbx-help-close-handle {
    color: @sgColorGrey3;
    cursor: pointer;
    position: absolute;

    .remFontSize(1.6);
    .remRight(1.2);
    .remTop(2.9);

    &:hover {
        color: @sgColorGrey2;
    }

    & when not(@hpbxStyleguide = 'upc-business') and not(@hpbxStyleguide = 'unitymedia') {
       .LGI-iconcross {
            &:before {
                content: '\e623';
            }
        }
    }
}

.hpbx-help-header-xxs() {
    .hpbx-help-header-title {
        .remMarginLeft(2);
    }

    body.hpbx-help-search-open {
        .hpbx-help-header-title,
        .hpbx-help-header-link {
            display: none;
        }

        .hpbx-header-search-input-container {
            position: absolute;
            right: 0;
            top: 0;
            width: 100%;
        }

        .hpbx-search-input-container {
            .remPaddingLeft(2);
        }

        input[type="text"].hpbx-search-input {
            display: inline-block;
        }
    }

    .hpbx-search-input-container {
        .remMarginRight(2);
    }

    input[type="text"].hpbx-search-input {
        display: none;
    }

    .hpbx-help-close-handle {
        .remRight(0.7);
    }
}

@media @XXS_MAX {
    .hpbx-help-header-xxs();
}

// Not extremely pretty, but this is the only way to get the close handle to be hidden in desktop mode
@media screen and (width: 561px) {
    .hpbx-help-close-handle {
        display: none;
    }
}
