.hpbx-initialize-variables-tabs-content(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorTabsContentFormRequiredAsterisk: @sgColorPurple2;
}

.hpbx-initialize-variables-tabs-content(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-tabs-content('upc-business');
}

.hpbx-initialize-variables-tabs-content(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorTabsContentFormRequiredAsterisk: @sgColorGrey1;
}

.hpbx-initialize-variables-tabs-content(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-tabs-content('ziggo');
}

.hpbx-initialize-variables-tabs-content(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-tabs-content('ziggo');
}

.hpbx-initialize-variables-tabs-content(@hpbxStyleguide);

.hpbx-tab-form {
    .remPadding(1.2, 1, 1.9, 1);

    .row {
        margin: 0;
    }

    div:nth-last-child(2) {
        border: none;
    }
}

.hpbx-tab-form-foldable-list {
    .remPadding(0, 0, 1.9, 0);

    .hpbx-tab-form-label,
    .hpbx-tab-form-field {
        .remPaddingLeft(1);
        .remPaddingRight(1);
    }
}

.hpbx-tab-form-label-field-row {
   .remMarginTop(1.7);

    .hpbx-tab-audio-container {
        border-bottom: 1px solid @sgColorGrey5;
        border-top: 1px solid @sgColorGrey5;

        .remHeight(5);

        .hpbx-audio-background-active {
            display: none;
        }
    }

    .hpbx-audio-background-active {
        z-index: 1;
    }

    .hpbx-audio-progress-bar {
        z-index: 2;
    }
}

.hpbx-tab-form-buttons-row {
    .remMarginLeft(1);
    .remMarginTop(2.9);
}

.hpbx-tab-form-link-button {
    font-family: @sgFontFamilyHeadings;
    color: @sgColorGrey2;

    .remFontSize(1.8);
    .remMarginRight(1.9);
}

.hpbx-tab-form-label {
    color: @sgColorGrey2;
    font-weight: bold;

    .remMarginBottom(0.4);
}

.hpbx-tab-form-sublabel {
    font-weight: normal;

    .remFontSize(1.2);
}

.hpbx-tab-form-field {

    .chzn-container {
        width: 100% !important;
    }

    .chzn-drop {
        width: 99% !important;
        width: ~"calc(100% - 2px)" !important;
    }

    &.hpbx-tab-form-field-align-center {
        text-align: center;
    }

    &.hpbx-tab-form-field-align-right {
        text-align: right;
    }
}

.hpbx-tab-form-explanation {
    color: @sgColorGrey2;
    font-style: italic;

    .remFontSize(1.2);
}

.hpbx-required {
    .hpbx-tab-form-label:after {
        color: @hpbxColorTabsContentFormRequiredAsterisk;
        content: "*";
    }
}


@media @MD {
    .hpbx-tabs-content-md();
}

.hpbx-tabs-content-md() {
    .hpbx-tab-form-label-field-row {
       .remMarginTop(1.2);
    }

    .hpbx-tab-form-label {
        .remLineHeight(2.2);
        .remPaddingTop(0.9);

        &.hpbx-tab-form-label-checkbox {
            padding-top: 0;
        }

        &.hpbx-tab-form-label-multiple-lines {
            padding-top: 0;

            .remMarginTop(-0.4);
        }
    }

    .hpbx-tab-form-field {
        &.hpbx-tab-form-field-text {
            .remPaddingTop(0.9);
        }
    }
}

.ie8 {
    .hpbx-tabs-content-md();
}
