.hpbx-initialize-variables-hunting-group(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxLeftHuntingGroupUserCheckbox: 20.5;
    @hpbxLeftOffsetHuntingGroupSelectAllCheckbox: 6px;
    @hpbxTopHuntingGroupUserCheckbox: 2.2;
}

.hpbx-initialize-variables-hunting-group(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-hunting-group('upc-business');
}

.hpbx-initialize-variables-hunting-group(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxLeftHuntingGroupUserCheckbox: 20;
    @hpbxLeftOffsetHuntingGroupSelectAllCheckbox: 0px;
    @hpbxTopHuntingGroupUserCheckbox: 1.7;
}

.hpbx-initialize-variables-hunting-group(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-hunting-group('ziggo');
}

.hpbx-initialize-variables-hunting-group(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-hunting-group('ziggo');
}

.hpbx-initialize-variables-hunting-group(@hpbxStyleguide);

.hpbx-hunting-group-search-container {
    position: relative;

    .box-sizing(border-box);
    .remHeight(4.5);
}

.hpbx-hunting-group-search-icon {
    color: @sgColorGrey6;
    position: absolute;

    .remMarginLeft(2);
    .remMarginTop(1.5);
}

.hpbx-hunting-group-search-input {
    background-color: @sgColorGrey3 !important;
    border: none !important;
    border-bottom: 1px solid @sgColorGrey5 !important;
    color: @sgColorGrey6 !important;

    .remBorderRadiusAll(0) !important;
    .placeholder(@sgColorGrey5) !important;
    .remPaddingLeft(5) !important;
}

.hpbx-hunting-group-select-all {
    background-color: @sgColorGrey6;
    border-bottom: 1px solid @sgColorGrey5 !important;

    .sg-custom-check {
        margin-bottom: 0;
    }

    label {
        width: 100%;

        .remPaddingBottom(1);
        .remPaddingLeft(2);
        .remPaddingRight(6);
        .remPaddingTop(1.3);
    }

    .sg-alternative-display {
        left: ~"calc(100% - 42px)";
        position: absolute;

        .remTop(1.3);
        .translate(@hpbxLeftOffsetHuntingGroupSelectAllCheckbox, 0px);
    }
}

.hpbx-hunting-group-user {
    background-color: @sgColorGrey6;
    border-bottom: 1px solid @sgColorGrey5;
    margin-bottom: 0;
    position: relative;

    .box-sizing(content-box);

    &:before {
        content: '';
    }

    &.hpbx-selected {
        background-color: @sgColorWhite;

        .hpbx-hunting-group-user-name {
            color: @hpbxColorPrimary;
        }

        &:before {
            color: @sgColorGrey3;
            content: counter(hpbx-hunting-group-user);
            counter-increment: hpbx-hunting-group-user;
            font-family: @sgFontFamilyText;
            position: absolute;

            .remFontSize(1.2);
            .remLeft(2.7);
            .remTop(1.3);
        }

        .sg-alternative-display {
            .remRight(1.8);
        }
    }

    &.ui-sortable-helper {
        .box-shadow(0 5px 10px rgba(0,0,0,.1));
    }

    &.ui-sortable-placeholder {
        background-color: @sgColorGrey5;
        visibility: visible !important;

        &:before {
            content: '';
        }
    }

    .sg-custom-check-and-radio {
        label {
            .remPadding(0, 5, 0, 4);
        }
    }

    .sg-alternative-display {
        left: ~"calc(100% - 42px)";
        position: absolute !important;

        .remTop(@hpbxTopHuntingGroupUserCheckbox) !important;
        .translate(@hpbxLeftOffsetHuntingGroupSelectAllCheckbox, 0px);
    }
}

.hpbx-hunting-group-user-information {
    .remPaddingBottom(0.4);
    .remPaddingTop(1.3);
}

.hpbx-hunting-group-user-name {
    color: @sgColorGrey2;

    .remFontSize(1.6);
    .remLineHeight(1.8);
}

.hpbx-hunting-group-user-phone {
    color: @sgColorGrey3;
    
    .remFontSize(1.4);
    .remLineHeight(1.8);
}

.hpbx-hunting-group-md() {
    .hpbx-foldable-heading.hpbx-hunting-group-members,
    .hpbx-foldable-content.hpbx-hunting-group-members {
        display: none;
    }
}

@media @MD {
    .hpbx-hunting-group-md();
}

.ie8 {
    .hpbx-hunting-group-md();

    .hpbx-hunting-group-search-input {
        .remLineHeight(4);
    }

    .hpbx-hunting-group-user {
        .sg-custom-check-and-radio input {
            position: absolute;
            margin-top: 22px;
            right: 10px;
        }
    }
}