    src:url('../webfonts/OpenSans/OpenSans-Regular.eot?#iefix-o59ca3') format('embedded-opentype'),
    url('../webfonts/OpenSans/OpenSans-Regular.woff?-o59ca3') format('woff'),
    url('../webfonts/OpenSans/OpenSans-Regular.ttf?-o59ca3') format('truetype');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: NeoSans700;
    src: url('../webfonts/NeoSans/NeoSans.ttf');
    src:url('../webfonts/NeoSans/NeoSans.eot?#iefix-o59ca3') format('embedded-opentype'),
