    url('../webfonts/NeoSans/NeoSans.ttf?-o59ca3') format('truetype');
    font-weight: 400;
    font-style: normal;
}

/********** LGI Icon font **********/
@font-face {
    font-family: 'LGI-Iconfont';
    src:url('../webfonts/lgi-iconfont/fonts/LGI-Iconfont.eot?-o59ca3');
    src:url('../webfonts/lgi-iconfont/fonts/LGI-Iconfont.eot?#iefix-o59ca3') format('embedded-opentype'),
        url('../webfonts/lgi-iconfont/fonts/LGI-Iconfont.woff?-o59ca3') format('woff'),
        url('../webfonts/lgi-iconfont/fonts/LGI-Iconfont.ttf?-o59ca3') format('truetype'),
        url('../webfonts/lgi-iconfont/fonts/LGI-Iconfont.svg?-o59ca3#LGI-Iconfont') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="LGI-icon"], [class*=" LGI-icon"] {
    line-height: 1;
    
    .sg-lgi-icon-font();
}

.sg-lgi-icon-font() {
    font-family: 'LGI-Iconfont';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

