/******************************* INFORMATION ***************************

Project-specific styling of the radio button.
     
************************************************************************/

/********************************** START ******************************/ 

.custom-radio {
	.sg-alternative-display {
		.remBorderRadiusAll(@sgMiscSizeCheckbox / 2);
		.transform(translateZ(0));

		.sg-outer {
			background-color: @sgColorCheckRadioBackgroundHover;
			height: 100%;
			width: 100%;

			.opacity(0);
			.remBorderRadiusAll(1);
			.remPaddingAll((@sgMiscSizeCheckbox - 0.2 - @sgMiscSizeRadioButtonDot) / 2);

			.sg-inner {
				background-color: @sgColorRadioDotHover;
				height: 100%;
				width: 100%;

				.remBorderRadiusAll(1);
			}
		}

	}

	input:checked ~ .sg-alternative-display {
		background-color: @sgColorCheckRadioBackgroundChecked;
        border-width: 1px;

		.remPaddingAll((@sgMiscSizeCheckbox - 0.2 - @sgMiscSizeRadioButtonDot) / 2);
		.transition(@sgMiscDurationTransitionDefault);

		.sg-outer {
            background-color: @sgColorRadioDotChecked;
            
			.opacity(1);
			.remPaddingAll(@sgMiscSizeRadioButtonDot / 2);
		}
	}

	&:hover {
		.sg-alternative-display {
            border-color: @sgColorCheckRadioBorderHover;

			.transition(@sgMiscDurationTransitionDefault);

			.sg-outer {
				.opacity(1);
				.transition(@sgMiscDurationTransitionDefault);
			}
		}

        &.sg-disabled {
            .sg-alternative-display {
                border-color: @sgColorCheckRadioBorderDisabled;

                .sg-outer {
                    background-color: @sgColorCheckRadioBackgroundDisabled;
        
                    .sg-inner {
                        background-color: @sgColorRadioDotDisabled;
                    }
                }   
            }

            input:checked ~ .sg-alternative-display {
                .sg-outer {
                    background-color: @sgColorRadioDotChecked;
                }   
            }
        }
	}
}