.chzn-container {
    .chzn-drop {
        .chzn-results {
            .group-option {
                .remPaddingLeft(2.5 * @sgPaddingSelectLeft);
            }

            .group-result {
                background-color: @sgColorSelectResultBackground;
                color: @sgColorSelectResultText;
                height: 0;
                font-style: italic;

                .remBorderTop(0.1, solid, @sgColorSelectBorderItem);
                .remFontSize(1.2);
                .remMinHeight(3);
                .remPaddingLeft(@sgPaddingSelectLeft);
                .remPaddingRight(@sgPaddingSelectRight);
                .remPaddingTop(0.7);
                .transition(@sgMiscDurationTransitionDefault);

                @innerRadius: @sgMiscBorderRadiusDefault - 0.1;

                &:first-child {
                    border-top: none;

                    .remBorderTopRadius(@innerRadius);
                }

                &:last-child {
                    .remBorderBottomRadius(@innerRadius);
                }

                &:before {
                    content: none;
                }
            }
        }

        &.sg-animate-up {
            .chzn-results {
                .active-result,
                .group-result {
                    border-width: 0;
                    height: 0;
                    min-height: 0;
                }
            }
        }
    }
}

