.hpbx-loader {
    display: none;
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2000;
}

.hpbx-loader-box {
    background-color: @sgColorWhite;
    border: 1px solid @sgColorGrey4;
    left: 50%;
    overflow: hidden;
    position: absolute;
    top: 50%;


    .box-shadow(0 0 20px rgba(0,0,0,.2));
    .box-sizing(border-box);
    .remBorderRadiusAll(0.3);
    .remMarginLeft(-11);
    .remWidth(22);
}

.hpbx-loader-animation {
    // background-color: @sgColorGrey7;
    background-color: #F7F8FD;
    background-image: url("../../dist/img/loader/loader-@{hpbxStyleguide}.gif");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;

    .remHeight(10.8);
}

.hpbx-loader-progress {
    background-color: @sgColorGrey4;
    height: 1px;
    position: relative;
}

.hpbx-loader-progress-line {
    background-color: @hpbxColorPrimary;
    height: 100%;
    width: 0;

    .transition(0.5s);
}

.hpbx-loader-progress-dot {
    background-color: @hpbxColorPrimary;
    left: 0;
    position: absolute;

    .remBorderRadiusAll(1);
    .remHeight(0.5);
    .remTop(-0.2);
    .remWidth(0.5);
    .transition(0.5s);
}

.hpbx-loader-content {
    text-align: center;

    .remPadding(2.8, 1, 2.9, 1);
}

.hpbx-loader-title {
    color: @sgColorGrey1;
    font-family: @sgFontFamilyHeadings;
    
    .remFontSize(@sgFontSizeH4);
    .remLineHeight(3.1);
}

.hpbx-loader-subtitle {
    color: @hpbxColorPrimary;

    .remFontSize(1.4);
}


