.hpbx-initialize-variables-search-results(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorSearchResultsTitle: @sgColorPurple1;
}

.hpbx-initialize-variables-search-results(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-search-results('upc-business');
}


.hpbx-initialize-variables-search-results(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorSearchResultsTitle: @hpbxColorPrimary;
}

.hpbx-initialize-variables-search-results(@styleguide) when (@styleguide = 'virgin') {
    @hpbxColorSearchResultsTitle: @hpbxColorPrimary;
}

.hpbx-initialize-variables-search-results(@styleguide) when (@styleguide = 'vtr') {
    @hpbxColorSearchResultsTitle: @hpbxColorPrimary;
}

.hpbx-initialize-variables-search-results(@hpbxStyleguide);

.hpbx-help-search-results-amount {
    color: @sgColorGrey3;
    font-family: @sgFontFamilyHeadings;

    .remFontSize(@sgFontSizeH6);
    .remMarginTop(-2);
}

.hpbx-help-search-result-container {
    border-top: 1px solid @sgColorGrey4;

    .remPaddingTop(2.5);

    &:first-child {
        border: none;
    
        .remPaddingTop(1.5);
    }
}

.hpbx-help-search-result-title {
    color: @hpbxColorSearchResultsTitle;
    display: block;
    font-family: @sgFontFamilyHeadings;
    line-height: 1.25;
    text-decoration: none;

    .remFontSize(@sgFontSizeH4);
    .remMarginBottom(1.2);
}

.hpbx-help-search-result-text {
    color: @sgColorGrey2;
    overflow: hidden;
 
    .remFontSize(@sgFontSizeText);
    .remHeight(4.4);
    .remMarginBottom(2.2);
}

.hpbx-help-search-result-keyword {
    font-weight: bold;
}