.hpbx-initialize-variables-notification(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorNotificationIconBackgroundError: @sgColorRed;
    @hpbxColorNotificationIconBackgroundSuccess: @sgColorGreen2;
}

.hpbx-initialize-variables-notification(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-notification('upc-business');
}


.hpbx-initialize-variables-notification(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorNotificationIconBackgroundError: @sgColorSignal1;
    @hpbxColorNotificationIconBackgroundSuccess: @sgColorSignal2;
}

.hpbx-initialize-variables-notification(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-notification('ziggo');
}

.hpbx-initialize-variables-notification(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-notification('ziggo');
}

.hpbx-initialize-variables-notification(@hpbxStyleguide);


.hpbx-notification {
    background-color: @sgColorGrey7;
    border: 1px solid @sgColorGrey4;
    border-spacing: 0;
    color: @sgColorGrey1;
    position: fixed;
    text-align: center;
    z-index: 1200;

    .box-shadow(0 0 20px rgba(0,0,0,.2));
    .box-sizing(border-box);
    .remBorderRadiusAll(0.5);
    .remBottom(2);
    .remLeft(2);
    .remPadding(2.9, 2, 2.5, 2);
    .remWidth(22);
}

.hpbx-notification-icon {
    display: inline-block;
    position: relative;

    .remBorderRadiusAll(100);
    .remHeight(5);
    .remWidth(5);

    &:after {
        color: #fff;
        position: absolute;

        .remFontSize(1.6);
        .remLeft(1.7);
        .remTop(1.3);
        .sg-lgi-icon-font();
    }
}

.hpbx-notification-title {
    font-family: @sgFontFamilyHeadings;

    .remFontSize(2.4);
    .remMarginTop(-0.5);
}

.hpbx-notification-message {
    .remMarginTop(0.5);

    ul {
        li {
            &:before {
                content: none;
            }
        }
    }
}

.hpbx-notification-error {
    .hpbx-notification-icon {
        background-color: @sgColorGrey2;
        background: url("../../dist/img/icon-error.svg") center top no-repeat;
        background-size: auto;


        &:after {
            content: "\e623";
        }
    }
}

.hpbx-notification-warning {
    .hpbx-notification-icon {
        background-color: @sgColorGrey2;

        &:after {
            content: "\e623";
        }
    }
}

.hpbx-notification-info {
    .hpbx-notification-icon {
        background-color: @hpbxColorPrimary;

        &:after {
            content: "\e61e";
            font-family: "trimm-icons";
        }
    }
}

.hpbx-notification-success {
    .hpbx-notification-icon {
        background-color: @hpbxColorNotificationIconBackgroundSuccess;

        &:after {
            content: "\e625";
        }
    }
}

