.hpbx-page-fax {
    .hpbx-tab-form-buttons-row {
        margin-left: 0;
    }

    .hpbx-table-call-log {
        margin-top: 0;
    }

    .hpbx-table-selector-caption {
        border-left: none;
        border-right: none;
        border-top: none;
    }

    .hpbx-main-content-inner {
        .remPaddingTop(1.4);
    }

    .hpbx-tabs>div:nth-last-child(2) {
        border-bottom: 1px solid @sgColorGrey4;
        overflow: hidden;
    }

    .hpbx-tabs>div:nth-last-child(3) {
        border-bottom: 1px solid @sgColorGrey4;
    }
}

.hpbx-foldable-heading.hpbx-fax-members {
    border-top: 1px solid @sgColorGrey4;

    .remMarginTop(2);

}

.hpbx-fax-md() {
    .hpbx-foldable-heading.hpbx-fax-members,
    .hpbx-foldable-content.hpbx-fax-members {
        display: none;
    }
}

@media @MD {
    .hpbx-fax-md();
}



