.hpbx-form-login {
    .remPaddingAll(2);

}

.hpbx-form-login-row {
    color: @sgColorGrey2;

    .remMarginTop(1.2);

    &:first-child {
        margin-top: 0;
    }
}

.hpbx-form-login-caption {
    display: none;
}

.hpbx-form-login-addition {
    .remMarginTop(1.5);
    .remPaddingLeft(4);

    a {
        color: @sgColorGrey2;
    }
}

@media @SM {
    .hpbx-login-sm();
}

.hpbx-login-sm() {
    .hpbx-form-login {
        .remPaddingAll(4);
    }
}

.ie8, .ie9 {
    .hpbx-login-sm();

    .hpbx-form-login-caption {
        display: block;
    }
}