.hpbx-initialize-variables-pie-chart(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorProgressCircleTitle: @sgColorPurple1;
    @hpbxColorProgressCircleTitleAmount: @sgColorPurple1;

    @hpbxColorPieChart1: @sgColorPurple1;
    @hpbxColorPieChart2: #1766af;
    @hpbxColorPieChart3: #0c87c6;
}

.hpbx-initialize-variables-pie-chart(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-pie-chart('upc-business');
}

.hpbx-initialize-variables-pie-chart(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorProgressCircleTitle: @sgColorGrey1;
    @hpbxColorProgressCircleTitleAmount: @hpbxColorPrimary;

    @hpbxColorPieChart1: @sgColorGrey3;
    @hpbxColorPieChart2: @sgColorGrey1;
    @hpbxColorPieChart3: @sgColorOrangeExtra;
}

.hpbx-initialize-variables-pie-chart(@styleguide) when (@styleguide = 'virgin') {
    @hpbxColorProgressCircleTitle: @sgColorGrey1;
    @hpbxColorProgressCircleTitleAmount: @hpbxColorPrimary;

    @hpbxColorPieChart1: @sgColorGrey3;
    @hpbxColorPieChart2: @sgColorGrey1;
    @hpbxColorPieChart3: @sgColorRedHover;
}

.hpbx-initialize-variables-pie-chart(@styleguide) when (@styleguide = 'vtr') {
    @hpbxColorProgressCircleTitle: @sgColorGrey1;
    @hpbxColorProgressCircleTitleAmount: @hpbxColorPrimary;

    @hpbxColorPieChart1: @sgColorGrey5;
    @hpbxColorPieChart2: @sgColorGrey4;
    @hpbxColorPieChart3: @sgColorPinkExtra;
}

.hpbx-initialize-variables-pie-chart(@hpbxStyleguide);

.hpbx-pie-chart-container {
    text-align: center;

    .remMarginTop(4);

    .hpbx-percentage-display {
        .remMarginTop(0.7);
        .remMinHeight(2.1);
    }

    .hpbx-pie-chart-subtitle {
        .remMarginTop(0.2);
    }
}

.hpbx-pie-chart {
    display: inline-block;
    position: relative;

    .remMarginBottom(0.6);

    &.hpbx-pie-chart-small {
        .hpbx-pie-chart-svg {
            .remHeight(8);
            .remWidth(8);
        }

        .hpbx-pie-chart-inner {
            font-family: @sgFontFamilyHeadings;
            
            .remFontSize(@sgFontSizeH2);
            .remTop(1);
        }
    }
}

.hpbx-pie-chart-svg {
    fill: @sgColorBodyBackground;
    overflow: visible;
    stroke: @hpbxColorPrimary;
    stroke-width: 2px;

    .remHeight(15);
    .remWidth(15);
}

.hpbx-pie-chart-color-1 {
    color: @hpbxColorPieChart1;
    stroke: @hpbxColorPieChart1;
}

.hpbx-pie-chart-color-2 {
    color: @hpbxColorPieChart2;
    stroke: @hpbxColorPieChart2;
}

.hpbx-pie-chart-color-3 {
    color: @hpbxColorPieChart3;
    stroke: @hpbxColorPieChart3;
}

.hpbx-pie-chart-color-4 {
    color: @hpbxColorPrimary;
    stroke: @hpbxColorPrimary;
}

.hpbx-pie-chart-background {
    fill: none;
    stroke: @sgColorGrey4;
    stroke-width: 1;
}

.hpbx-pie-chart-arc {
    cursor: pointer;
    fill: none;
    stroke-width: 21px;
}

.hpbx-pie-chart-edge-arc {
    stroke: @sgColorGrey7;
    fill: none;
    stroke-width: 2px;
}

.hpbx-pie-chart-inner {
    color: @sgColorGrey3;
    position: absolute;

    .remFontSize(3.6);
    .remLeft(5.8);
    .remTop(5.1);
}

.hpbx-pie-chart-tooltip {
    position: absolute;
    white-space: nowrap;

    .remFontSize(@sgFontSizeLegend);
    .remBottom(-0.1);
}

.hpbx-pie-chart-title {
    color: @hpbxColorProgressCircleTitle;
    font-family: @sgFontFamilyHeadings;
    text-align: center;

    .remFontSize(@sgFontSizeH4);
    .remMarginBottom(-0.8);
}

.hpbx-pie-chart-title-amount {
    color: @hpbxColorProgressCircleTitleAmount;
}

.hpbx-pie-chart-subtitle {
    color: @sgColorGrey2;
    font-family: @sgFontFamilyHeadings;
    text-align: center;

    .remFontSize(@sgFontSizeH6);
}

.hpbx-pie-chart-md() {
    .hpbx-pie-chart-container {
        margin-top: 0;
    }
}

@media @MD {
    .hpbx-pie-chart-md();
}


.ie8 {
    .hpbx-pie-chart-md();

    .hpbx-pie-chart-title {
        .remMarginTop(-0.5);
    }
}
