.hpbx-grid-padding(10px, 10px);

.hpbx-grid-padding-all(@padding-left, @padding-right) {
    .row {
        margin-left: -1 * @padding-left;
        margin-right: -1 * @padding-right;
    }

    div[class^='col-'], div[class*=' col-'] {
        padding-left: @padding-left;
        padding-right: @padding-right;
    }
}

.hpbx-grid-padding(@padding-left, @padding-right) {
    .hpbx-grid-padding-all(10px, 10px);

    .container, 
    div.hpbx-main-content {
        padding-left: @padding-left;
        padding-right: @padding-right;
        margin-top: 2rem;
    }

    .container > .row {
        margin-left: -1 * @padding-left;
        margin-right: -1 * @padding-right;
    }
}

@media @XS {
    .hpbx-global-xs();
}

.hpbx-global-xs() {
    .hpbx-grid-padding(10px, 60px);
    .container, 
    div.hpbx-main-content {
        margin-top: 0.5rem;
    }
    .container {
        width: 100%;

        &.hpbx-no-menu {
            padding-right: 10px;

            .hpbx-grid-padding(10px, 10px);
        }
    }
}

@media @SM {
    .hpbx-global-sm();
}

.hpbx-global-sm() {
    .hpbx-grid-padding(20px, 70px);

    .container, 
    div.hpbx-main-content {
        margin-top: 1rem;
    }
    .container {
        &.hpbx-no-menu {
            padding-right: 20px;
        }
    }
}

@media @MD {
    .hpbx-global-md();
}

.hpbx-global-md() {
    .hpbx-grid-padding(20px, 270px);
    .container, 
    div.hpbx-main-content {
        margin-top: 1.5rem;
    }
}

@media @LG {
    .hpbx-global-lg();
}

.hpbx-global-lg() {
    .container {
        width: 1150px;
    }
}


