.hpbx-initialize-variables-pickadate(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorPickadateHoverBackground: @sgColorGrey5;
    @hpbxColorPickadateClear: @sgColorRed;
}

.hpbx-initialize-variables-pickadate(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-pickadate('upc-business')
}

.hpbx-initialize-variables-pickadate(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorPickadateHoverBackground: @sgColorGrey4;
    @hpbxColorPickadateClear: @sgColorSignal1;
}

.hpbx-initialize-variables-pickadate(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-pickadate('ziggo');
}

.hpbx-initialize-variables-pickadate(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-pickadate('ziggo');
}

.hpbx-initialize-variables-pickadate(@hpbxStyleguide);

.picker {
    color: @sgColorGrey1;
}

.picker__day {
    border: none !important;
}

.picker--focused .picker__day--highlighted,
.picker__day--highlighted:hover,
.picker__day--infocus:hover,
.picker__day--outfocus:hover,
.picker__button--clear:hover,
.picker__button--close:hover,
.picker__button--today:hover,
.picker__nav--next:hover,
.picker__nav--prev:hover {
    background: @hpbxColorPickadateHoverBackground;
    color: @sgColorGrey1;
}

.picker--focused .picker__day--selected, 
.picker__day--selected, 
.picker__day--selected:hover {
    background: @hpbxColorPrimary;
    color: @sgColorWhite;
}

.picker__day--today:before,
.picker__button--today:before {
    border-top-color: @hpbxColorPrimary;
}

.picker__button--clear:before {
    border-top-color: @hpbxColorPickadateClear;
}

.picker__button--close:before {
    color: @sgColorGrey2;
}

.picker__nav--next:before {
    border-left-color: @sgColorGrey1;
}

.picker__nav--next:before, .picker__nav--prev:before {
    border-right-color: @sgColorGrey1;
}

.picker__year,
.picker__weekday {
    color: @sgColorGrey3;
}

.picker__day--outfocus {
    color: @sgColorGrey5;
}

.picker__button--clear, 
.picker__button--close, 
.picker__button--today {
    color: @sgColorGrey1;
}

.picker__table {
    td[role="presentation"] {
        padding-bottom: 0rem;
        padding-top: 0.2rem;
    }
}

@media @XS {
    .hpbx-audio-xs();
}

.hpbx-audio-xs() {
    .picker__frame {
        width: ~"calc(100% - 70px)";

        .translate(-25px, 100%);
    }

    .picker--opened {
        .picker__frame {
            .translate(-25px, 0);
        }
    }
}

@media @MD {
    .hpbx-audio-md();
}

.hpbx-audio-md() {
    .picker__frame {
        .translate(-125px, 100%);
    }

    .picker--opened {
        .picker__frame {
            .translate(-125px, 0);
        }
    }
}

