.hpbx-apply-styleguide-specific-styling-body(@styleguide) when (@styleguide = 'upc-business') {
    .hpbx-body-sm() {
        .hpbx-body-background-image {
            background: url("../../dist/img/background-@{hpbxStyleguide}.png") no-repeat center top;
            display: block !important;
            height: 630px;
            left: 0;
            position: fixed;
            top: 0;
            width: 100%;
            z-index: -2;

            .transition(0.5s ease-out);
        }
    }

    @media @SM {
        .hpbx-body-sm();
    }
}

.hpbx-apply-styleguide-specific-styling-body(@styleguide) when (@styleguide = 'ziggo') {
}

.hpbx-apply-styleguide-specific-styling-body(@styleguide) when (@styleguide = 'virgin') {
}

.hpbx-apply-styleguide-specific-styling-body(@styleguide) when (@styleguide = 'vtr') {
}

.hpbx-apply-styleguide-specific-styling-body(@hpbxStyleguide);

body {
    background-color: @sgColorWhite;
    counter-reset: hpbx-hunting-group-user;
    -ms-overflow-style: scrollbar;
}

.hpbx-body-background-color {
    background: url("../../dist/img/background-pattern-@{hpbxStyleguide}.png") repeat;
    height: 100%;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: -2;
}

.hpbx-body-background-image {
    display: none;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
only screen and (-o-min-device-pixel-ratio: 3/2),
only screen and (min--moz-device-pixel-ratio: 1.5),
only screen and (min-device-pixel-ratio: 1.5) {
    .hpbx-body-background-color {
        background: url("../../dist/img/background-pattern-@{hpbxStyleguide}@2x.png") repeat;
        background-size: 1px 1px;
    }

    body.hpbx-page-404 {
        .hpbx-body-background-color {
            background: none;
        }
    }
}

.ie8 {
    body {
        min-width: 960px;

        .container {
            padding-right: 250px;
            width: 980px;
        }
    }
}