.sg-animated-icon {
	.transition(margin-top @sgMiscDurationTransitionDefault);
	.remMarginTop(-0.1);
	.remMarginLeft(-0.1);
	.remWidth(@sgMiscHeightElements);
	.remHeight(@sgMiscHeightElements * 2);
	
	&:before,
	&:after {
		display: block;
		
		.remFontSize(1.6);
		.remWidth(@sgMiscHeightElements);
		.remHeight(@sgMiscHeightElements);
        line-height: 1;
        .remPaddingAll(1.6);
	}	
	
	&:after {
		float: left;
	}
	
	&.sg-rotate-right:before,
	&.sg-rotate-right:after {
		.transform(rotate(90deg));
	}

	&.sg-rotate-left:before,
	&.sg-rotate-left:after {
		.transform(rotate(-90deg));
	}
	
	&.sg-rotate-half:before,
	&.sg-rotate-half:after {
		.transform(rotate(180deg));
	}

	&.sg-flip:before,
	&.sg-flip:after {
		.transform(scale(-1));
	}	
}
