.hpbx-initialize-variables-buttons(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxFontSizeButtonsWizard: 1.8;
}

.hpbx-initialize-variables-buttons(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-buttons('upc-business');
}

.hpbx-initialize-variables-buttons(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxFontSizeButtonsWizard: 1.6;
}

.hpbx-initialize-variables-buttons(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-buttons('ziggo');
}

.hpbx-initialize-variables-buttons(@styleguide) when (@styleguide = 'vtr') {
    @hpbxFontSizeButtonsWizard: 1.6;
}

.hpbx-initialize-variables-buttons(@hpbxStyleguide);

.sg-btn +.sg-btn-icon {
    .sg-btn-sg-btn-icon(@hpbxStyleguide);
}

.sg-btn-sg-btn-icon(@styleguide) when (@styleguide = 'ziggo') {
    display: none !important;
}

.sg-btn-sg-btn-icon(@styleguide) when (@styleguide = 'virgin') {
    display: none !important;
}

.sg-btn-sg-btn-icon(@styleguide) when (@styleguide = 'unitymedia') {
    display: none !important;
}

.sg-btn-sg-btn-icon(@styleguide) when (@styleguide = 'vtr') {
    display: none !important; 
}

.sg-btn-primary {
    .sg-btn-primary(@hpbxStyleguide);
}

.sg-btn-secondary {
    .sg-btn-secondary(@hpbxStyleguide);
}

.sg-btn-disabled {
    .sg-btn-disabled(@hpbxStyleguide);
}

.sg-btn-primary(@styleguide) when (@styleguide = 'upc-business') {
    .sg-btn-upc-business-blue();
}

.sg-btn-secondary(@styleguide) when (@styleguide = 'upc-business') {
    .sg-btn-upc-business-white();
}

.sg-btn-disabled(@styleguide) when (@styleguide = 'upc-business') {
    .sg-btn-upc-business-disabled();
}

.sg-btn-primary(@styleguide) when (@styleguide = 'unitymedia') {
    .sg-btn-unitymedia-green();
}

.sg-btn-secondary(@styleguide) when (@styleguide = 'unitymedia') {
    .sg-btn-unitymedia-white();
}

.sg-btn-disabled(@styleguide) when (@styleguide = 'unitymedia') {
    .sg-btn-unitymedia-disabled();
}

.sg-btn-primary(@styleguide) when (@styleguide = 'ziggo') {
    .sg-btn-ziggo-orange();
}

.sg-btn-secondary(@styleguide) when (@styleguide = 'ziggo') {
    .sg-btn-ziggo-white();
    .sg-btn-small-ziggo();

    &:not(.hpbx-delete) {
        .remMarginTop(0.4);
    }
}

.sg-btn-disabled(@styleguide) when (@styleguide = 'ziggo') {
    .sg-btn-ziggo-disabled();
}


.sg-btn-primary(@styleguide) when (@styleguide = 'virgin') {
    .sg-btn-virgin-purple();
}

.sg-btn-secondary(@styleguide) when (@styleguide = 'virgin') {
    .sg-btn-virgin-grey();
    .sg-btn-small-virgin();

    border: 0; //should be below the includes for button styling

    &:not(.hpbx-delete) {
        .remMarginTop(0.4);
    }
}

.sg-btn-disabled(@styleguide) when (@styleguide = 'virgin') {
    .sg-btn-virgin-disabled();
}


.sg-btn-primary(@styleguide) when (@styleguide = 'vtr') {
    .sg-btn-vtr-pink();
}

.sg-btn-secondary(@styleguide) when (@styleguide = 'vtr') {
    // .sg-btn-vtr-purple();
    .sg-btn-vtr-white();
    .sg-btn-small-vtr();

    &:not(.hpbx-delete) {
        .remMarginTop(0);
    }
    .remMarginTop(0);
    padding: 1.7rem 1.7rem 0rem 1.7rem;
    min-height:3.5rem;
}

.sg-btn-disabled(@styleguide) when (@styleguide = 'vtr') {
    .sg-btn-vtr-disabled();
}


.sg-btn-wizard {
    border: 1px solid @hpbxColorPrimary !important;
    color: @hpbxColorPrimary;

    .remFontSize(@hpbxFontSizeButtonsWizard);
    .remMinHeight(4.5);
    .remPaddingLeft(1.5);
    .remPaddingRight(1.5);
    .remPaddingTop(1);

    &:hover {
        background-color: @sgColorWhite;
        border-color: @sgColorGrey4 !important;
        color: @hpbxColorPrimary;
    }
}

.sg-btn.hpbx-click-with-alert {
    input[type="file"] {
        height: 0 !important;
        width: 0 !important;
        z-index: -1;
    }
}