.hpbx-scrollable-container {
    position: relative;
}

.ps-active-y {
    .ps-scrollbar-y-rail {
        background-color: transparent !important;

        &:before,
        &:after {
            .sg-lgi-icon-font();

            color: @sgColorGrey3;
            position: absolute;
            right: 17px;

            .remFontSize(0.6);
        }

        &:before {
            content: '\e62d';
            top: 20px;
        }

        &:after {
            content: '\e62b';
            top: 28px;
        }

        .ps-scrollbar-y {
            cursor: pointer;
            height: 0 !important;
            position: relative;

            &:after {
                background-color: @sgColorGrey3;
                content: '';
                height: 200px;
                position: absolute;
                right: 17px;
                top: 43px;
                width: 6px;

                .remBorderRadiusAll(10);
            }
        }
    }
}