input[type="text"],
input[type="email"],
input[type="password"],
textarea {
	display: inline-block;
	font-family: @sgFontFamilyText;
	height: inherit;
	line-height: inherit;
	outline: none;
	vertical-align: bottom;

	.box-sizing(border-box);
	.remBorderRadiusAll(@sgMiscBorderRadiusDefault);
	.remFontSize(@sgFontSizeInput);
	.placeholder(@sgColorTextfieldPlaceholder);
	.remMinHeight(@sgMiscHeightElements);
	.remPadding(@sgPaddingInputTop, @sgPaddingInputRight, @sgPaddingInputBottom, @sgPaddingInputLeft);
	.sg-give-color-to-element(@sgColorTextfieldBackground, @sgColorTextfieldBackgroundHover, @sgColorTextfieldText, @sgColorTextfieldTextHover, @sgColorTextfieldBorder, @sgColorTextfieldBorderHover);
	.transition(@sgMiscDurationTransitionDefault);

	&:focus {
		background-color: @sgColorTextfieldBackgroundFocus;
		border-color: @sgColorTextfieldBorderFocus !important;
		color: @sgColorTextfieldTextFocus;

		.transition(0s);
		
		/* style placeholder http://css-tricks.com/snippets/css/style-placeholder-text/ */
		&::-webkit-input-placeholder { 
		   color: @sgColorTextfieldTextFocus;  
		}

		&:-moz-placeholder  /* Firefox 18- */ { 
		   color: @sgColorTextfieldTextFocus;  
		}

		&::-moz-placeholder  /* Firefox 19+ */ { 
		   color: @sgColorTextfieldTextFocus;  
		}

		&:-ms-input-placeholder { 
		   color: @sgColorTextfieldTextFocus;  
		}
	}

	&.sg-completed {
		background-color: @sgColorTextfieldBackgroundCompleted;
        border-color: @sgColorTextfieldBorderCompleted;
		color: @sgColorTextfieldTextCompleted;
	}

	&.sg-error {
		border-color: @sgColorTextfieldBorderError;
		color: @sgColorTextfieldTextError;

		.sg-animate('sg-animation-shake', 1s, initial);

		&:hover {
			border-color: @sgColorTextfieldBorderErrorHover;
		}
	}

	&.sg-disabled {
		.sg-give-color-to-element(@sgColorTextfieldBackgroundDisabled, @sgColorTextfieldBackgroundDisabled, @sgColorTextfieldTextDisabled, @sgColorTextfieldTextDisabled, @sgColorTextfieldBorderDisabled, @sgColorTextfieldBorderDisabled);
	}
	
	/* Clear blue cross on IE http://stackoverflow.com/questions/14007655/remove-ie10s-clear-field-x-button-on-certain-inputs */
	&::-ms-clear {
		display: none;
	}
}


textarea {
	.remPaddingBottom(@sgPaddingTextareaBottomTop);
	.remPaddingTop(@sgPaddingTextareaBottomTop);
}
