.hpbx-initialize-variables-full-screen(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxFullScreenHeaderTitleIconColor:         @hpbxColorPrimary;
}

.hpbx-initialize-variables-full-screen(@styleguide) when (@styleguide = 'unitymedia') {
    @hpbxFullScreenHeaderTitleIconColor:         @sgColorGrey3;
}

.hpbx-initialize-variables-full-screen(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxFullScreenHeaderTitleIconColor:         @hpbxColorPrimary;
}

.hpbx-initialize-variables-full-screen(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-full-screen('ziggo');
}

.hpbx-initialize-variables-full-screen(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-full-screen('ziggo');
}

.hpbx-initialize-variables-full-screen(@hpbxStyleguide);


body.hpbx-page-full-screen {
    .remPaddingTop(9);
    .remPaddingBottom(8);
}

.hpbx-full-screen-header {
    background-color: @sgColorGrey2;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;

    .remHeight(9);
}

.hpbx-full-screen-header-title {
    position: absolute;

    .remLeft(1.8);
    .remTop(1.8);
}

.hpbx-full-screen-header-title-icon {
    color: @hpbxFullScreenHeaderTitleIconColor;
    left: 0;
    position: absolute;

    .remFontSize(3.6);
    .remTop(0.5);
}

.hpbx-full-screen-header-title-text {
    color: @sgColorWhite;
    font-family: @sgFontFamilyHeadings;

    .remFontSize(@sgFontSizeH2);
    .remPaddingLeft(4.4);
}

.hpbx-full-screen-header-links {
    position: absolute;

    .remFontSize(1.6);
    .remRight(0.2);
    .remTop(3.4);
}

.hpbx-full-screen-header-link {
    color: @sgColorGrey3;
    float: left;
    font-family: @sgFontFamilyHeadings;
    position: relative;
    text-decoration: none;

    .remMarginRight(2.9);
    .transition(@sgMiscDurationTransitionDefault);

    &:hover {
        color: @sgColorWhite;
    }
}

.hpbx-full-screen-header-link-icon {
    left: 0;
    position: absolute;

    .remTop(0.3);
}

.hpbx-full-screen-header-link-text {
    .remPaddingLeft(2.3);
}

.hpbx-full-screen-content {
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;

    .remPaddingBottom(8);
    .remPaddingTop(9);

    .hpbx-scrollable-container {
        height: 100%;
    }
}

.hpbx-full-screen-footer {
    background-color: @sgColorGrey2;
    bottom: 0;
    left: 0;
    position: fixed;
    width: 100%;
    z-index: 1;

    .remHeight(8);
}

.hpbx-full-screen-footer-buttons {
    position: absolute;

    .remRight(1.2);
    .remTop(2.2);
}

.hpbx-full-screen-footer-button {
    float: left;

    .remMarginRight(2.9);
}

.hpbx-full-screen-footer-link-button {
    color: @sgColorGrey3;
    display: inline-block;

    .remPaddingTop(1);
}

