.hpbx-initialize-variables-slots-row(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxRightSlotsRowDeleteIcon: 1;
    @hpbxTopSlotsRowDeleteIcon: 0;

    @hpbxLeftTimeSlotsRowDeleteIcon: 0;
    @hpbxTopTimeSlotsRowDeleteIcon: 0;
}

.hpbx-initialize-variables-slots-row(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-slots-row('upc-business');
}

.hpbx-initialize-variables-slots-row(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxRightSlotsRowDeleteIcon: 0;
    @hpbxTopSlotsRowDeleteIcon: 0.8;

    @hpbxLeftTimeSlotsRowDeleteIcon: 1.9;
    @hpbxTopTimeSlotsRowDeleteIcon: 0.8;
}

.hpbx-initialize-variables-slots-row(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-slots-row('ziggo');
}

.hpbx-initialize-variables-slots-row(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-slots-row('ziggo');
}

.hpbx-initialize-variables-slots-row(@hpbxStyleguide);


.hpbx-slots-row {
    &.hpbx-invisible {
        display: none;
    }

    .hpbx-delete {
        .remRight(@hpbxRightSlotsRowDeleteIcon);
        .remTop(@hpbxTopSlotsRowDeleteIcon);

        &.hpbx-delete-timeslots {
            .remLeft(@hpbxLeftTimeSlotsRowDeleteIcon);
            .remTop(@hpbxTopTimeSlotsRowDeleteIcon);
        }
    }

    .hpbx-tab-form-field {
        padding: 0;
    }
}
