//global filelist styling
.hpbx-help-filelist-item {
    .remMinHeight(7);

    &:hover {
        .hpbx-help-filelist-hover-icon {
            display: block;
        }
    }

    & > .row {
        height: 100%;

        .remMinHeight(7);
    }
}

.hpbx-help-filelist-hover-icon {
    color: @sgColorGrey3;
    display: none;

    .remFontSize(1.6);
    .remMarginLeft(1);
    .remMarginTop(2.6);

    & when not(@hpbxStyleguide = 'upc-business') {
        &:before {
            content: '\e623';
        }
    }

    &:hover {
        cursor: pointer;
    }
}

.hpbx-help-filelist-item-info {
    .remPaddingTop(1.7);

    &.hpbx-multiple-lines {
        .remPaddingTop(0.4);
    }
}

.hpbx-help-filelist-item-title {
    color: @sgColorGrey2;
    font-family: @sgFontFamilyHeadings;
    line-height: 1.25;

    .remFontSize(@sgFontSizeH5);
}

.hpbx-help-filelist-item-subtitle {
    color: @sgColorGrey3;
    line-height: 1.25;

    .remFontSize(@sgFontSizeLegend);
}


// photo-specific styling, instead of files-specific
.hpbx-help-filelist-photos {

    .hpbx-help-filelist-items {
        .remMarginTop(-0.3);
    }

    .hpbx-help-filelist-item {
        .remMarginBottom(1);
        .remMarginTop(1);
    }

    .hpbx-help-filelist-item-info {
        .remPaddingLeft(8.8);
    }

    .hpbx-help-filelist-thumbnail-container {
        float: left;
        position: relative;

        .remMarginAll(0.5);
        .remHeight(7);
        .remWidth(7);


        //for thumbnail grid
        &.hpbx-selectable {
            cursor: pointer;

            &:hover {
                .hpbx-help-filelist-thumbnail-overlay,
                .hpbx-help-filelist-thumbnail-overlay-icon {
                    display: block;
                }
            }

            input:checked ~ .hpbx-help-filelist-thumbnail-outer {
                background-color: @sgColorWhite;
                border-color: @hpbxColorPrimary;

                .hpbx-help-filelist-thumbnail-overlay-icon:before {
                    content: '\e620';

                    & when not(@hpbxStyleguide = 'upc-business') {
                        content: '\e621';
                    }
                }
            }
        }
    }


    //for thumbnail grid
    .hpbx-help-filelist-thumbnail-overlay {
        background-color: @hpbxColorHelpOpenIcon;
        display: none;
        height: 100%;
        width: 100%;

        .opacity(0.5);
    }

    //for thumbnail grid
    .hpbx-help-filelist-thumbnail-overlay-icon {
        color: @sgColorWhite;
        display: none;
        position: absolute;

        .remFontSize(1.5);
        .remLeft(2.3);
        .remTop(1.9);
        .sg-lgi-icon-font();

        &:before {
            content: '\e61e';

            & when not(@hpbxStyleguide = 'upc-business') {
                content: '\e61f';
            }
        }
    }

    .hpbx-help-filelist-thumbnail-outer {
        background-color: @sgColorGrey6;
        border: 1px solid @sgColorGrey4;
        left: 0;
        position: absolute;
        top: 0;

        .box-sizing(border-box);
        .remHeight(7);
        .remWidth(7);
    }

    .hpbx-help-filelist-thumbnail-inner {
        background-position: center center;
        background-size: cover;
        position: absolute;

        .remHeight(6);
        .remLeft(0.4);
        .remTop(0.4);
        .remWidth(6);
    }
}

// files-specific styling, instead of photo-specific
.hpbx-help-filelist-files {
    .hpbx-help-filelist-item {
        &:hover {
            cursor: pointer;
        }
    }

    .hpbx-help-filelist-items {
        .remMarginBottom(1.3);
        .remMarginTop(0.3);
    }

    .hpbx-help-filelist-item-info {
        .remPaddingLeft(6.3);
    }

    .hpbx-help-filelist-thumbnail-container {
        position: absolute;

        .remLeft(0.9);
        .remTop(0.4);
    }

    .hpbx-help-filelist-thubnail-image {
        .remHeight(6.3);
        .remWidth(4.5);
    }
}


.hpbx-help-filelist-editable {
    .hpbx-help-filelist-item {
        &:hover {
            cursor: auto;

            & > .row {
                background-color: fade(@sgColorGrey6, 70%);
            }
        }
    }
}