.hpbx-help-steps-container {
    .remMarginTop(4.9);
}

.hpbx-help-step-container {
    border-top: 1px dashed @sgColorGrey4;
    
    .remPaddingBottom(2);
    .remPaddingTop(2);
}

.hpbx-help-step-image {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    border: 1px solid @sgColorGrey4;
    cursor: pointer;

    .box-sizing(border-box);
    .remHeight(16.5);
}

.hpbx-help-step-title {
    color: @hpbxColorPrimary;
    font-family: @sgFontFamilyHeadings;

    .remFontSize(@sgFontSizeH6);
    .remMarginBottom(0.2);
    .remMarginTop(-0.7);
}

.hpbx-help-step-substeps {
    .remMarginBottom(2);
}

.hpbx-help-step-substep {
    position: relative;

    .remFontSize(@sgFontSizeLegend);
    .remPaddingLeft(0.9);

    &:before {
        .sg-lgi-icon-font();

        color: @hpbxColorPrimary;
        content: "\e64f";
        left: 0;
        position: absolute;

        .remFontSize(0.6);
        .remTop(0.4);
    }
}

@media @XS {
    .hpbx-steps-xs();
}

.hpbx-steps-xs() {
    .hpbx-help-step-substep {
        padding-left: 0;

        &:before {
            .remLeft(-0.9);
        }
    }
}

.ie8 {
    .hpbx-steps-xs();
}

