@sgSrsColorPrimary              : @sgColorLibraryGray;
@sgSrsColorSecondary            : @sgColorLibraryBlack;

@sgSrsHandleBackgroundColor     : @sgColorLibraryBackground;
@sgSrsHandleBorderColor         : @sgSrsColorPrimary;
@sgSrsHandleBorderColorHover    : @sgColorLibraryWhite;
@sgSrsHandleBorderRadius        : @sgSrsHandleHeight;
@sgSrsHandleBorderWidth         : 2px;
@sgSrsHandleHeight              : 18px;
@sgSrsHandleWidth               : 18px;

@sgSrsLineColor                 : @sgSrsColorSecondary;
@sgSrsLineColorActive           : @sgSrsColorPrimary;
@sgSrsLineThickness             : 1px;
@sgSrsLineThicknessActive       : 2px;

@sgSrsTickHeight                : 2px;
@sgSrsTickThickness             : 2px;
@sgSrsTickThicknessOuter        : 1px;

@sgSrsLabelColor                : @sgSrsColorSecondary;
@sgSrsLabelColorActive          : @sgSrsColorPrimary;
@sgSrsLabelFontSize             : @sgFontSizeText;

.sg-snapped-range-slider {
    position: relative;
    width: 100%;

    .text-selection(@sgSrsLabelColor, transparent);

    &.sg-srs-dragging {
        .sg-srs-line,
        .sg-srs-active-line {
            .transition(0s);
        }
    }
}

.sg-srs-line-container {
    cursor: pointer;
    height: @sgSrsHandleHeight;
    position: relative;
}

.sg-srs-line {
    background-color: @sgSrsLineColor;
    bottom: @sgSrsHandleHeight / 2;
    height: @sgSrsLineThickness;
    position: absolute;
    right: 0;
    width:100%;

    .transition(@sgMiscDurationTransitionDefault);
}

.sg-srs-active-line {
    background-color: @sgSrsLineColorActive;
    bottom: @sgSrsHandleHeight / 2 - 1;
    height: @sgSrsLineThicknessActive;
    left: 0;
    position: absolute;

    .transition(@sgMiscDurationTransitionDefault);
}

.sg-srs-ticks {
    bottom: @sgSrsHandleHeight / 2;
    position: absolute;
    width: 100%;
}

.sg-srs-tick {
    background-color: @sgSrsLineColor;
    bottom: @sgSrsLineThickness;
    height: @sgSrsTickHeight;
    position: absolute;
    width: @sgSrsTickThickness;

    &.sg-srs-outer {
        width: @sgSrsTickThicknessOuter;
    }
}

.sg-srs-handle {
    background-color: @sgSrsHandleBackgroundColor;
    border: @sgSrsHandleBorderWidth solid @sgSrsHandleBorderColor;
    border-radius: @sgSrsHandleBorderRadius;
    cursor: pointer;
    height: @sgSrsHandleHeight;
    left: 0%;
    position: absolute;
    top: 0;
    width: @sgSrsHandleWidth;

    .transition(@sgMiscDurationTransitionDefault);

    &:hover {
        border-color: @sgSrsHandleBorderColorHover;
    }

    &.ui-draggable-dragging {
        .transition(0s);
    }
}

.sg-srs-labels {
    position: relative;
}

.sg-srs-label {
    color: @sgSrsLabelColor;
    position: absolute;
    text-align: center;

    .opacity(0);
    .remFontSize(@sgSrsLabelFontSize);
    .transition(@sgMiscDurationTransitionDefault);

    &.sg-srs-left {
        position: static;

        .opacity(1);
    }

    &.sg-srs-right {
        right: 0;

        .opacity(1);
    }

    &.sg-srs-to-snap-to {
        .opacity(1);
    }

    &.sg-srs-active {
        color: @sgSrsLabelColorActive;

        .opacity(1);
        .text-selection(@sgSrsLabelColorActive, transparent);
    }
}