.sg-text-intro {
	color: @sgColorText;
	font-family: @sgFontFamilyTextIntro;
	font-weight: normal;
	line-height: @sgLineHeightTextIntro;
	text-transform: @sgTextTransformTextIntro;

	.sg-calculate-margins(@sgFontSizeTextIntro, @sgLineHeightTextIntro, @sgMarginTopParagraph);
	.remFontSize(@sgFontSizeTextIntro);
}
