/******************************* INFORMATION ***************************

All custom mixins for implementing the styleguide. 
     
************************************************************************/

/********************************** START ******************************/ 

/* This mixin sets colors on an HTML element. */
.sg-give-color-to-element(@backgroundColor, @backgroundColorHover, @color, @colorHover, @colorBorder, @colorBorderHover) {
    background-color: @backgroundColor;
    border: 1px solid @colorBorder;
    color: @color;

    &:hover {
        background-color: @backgroundColorHover;
        border-color: @colorBorderHover;
        color: @colorHover;
    }

    &.sg-background-animation-slide-horizontally {
        .horizontal(@backgroundColor, @backgroundColorHover, 50%, 50%);
    }
}

/* This mixin sets (gradient) colors on an HTML element. */
.sg-give-gradient-to-element(@backgroundColorStart, @backgroundColorEnd, @backgroundColorHoverStart, @backgroundColorHoverEnd, @color, @colorHover, @colorBorder, @colorBorderHover) {
    border: 1px solid @colorBorder;
    color: @color;

    .vertical(@backgroundColorStart, @backgroundColorEnd);

    &:hover {
        border-color: @colorBorderHover;
        color: @colorHover;

        .vertical(@backgroundColorHoverStart, @backgroundColorHoverEnd);
    }
}

/* This mixin sets the color of an HTML element in active state. */
.sg-give-active-color-to-element(@backgroundColor, @color, @colorBorder) {
    &:active {
        background-color: @backgroundColor;
        background-image: none;
        border: 1px solid @colorBorder;
        color: @color;
    }
}

/* This mixin applies the default border to an element without affecting its size. */
.sg-apply-default-border() {
	border: 1px solid @sgColorBorderDefault;
    
    .box-sizing(border-box);
}

/* This mixin calculates top and bottom margins based on font size, line height and the margin top from the style guide. The goal
   is to let the margins match exactly with how they are specified in the style guide. */
.sg-calculate-margins(@fontSize, @lineHeight, @marginTop) {
	@marginsAdjustmentFactor: 0.2;

	.remMarginBottom(-1 * @marginsAdjustmentFactor * @lineHeight * @fontSize);
	.remMarginTop(@marginTop - (@marginsAdjustmentFactor * @lineHeight * @fontSize));
}
