/******************************* INFORMATION ***************************

Custom select
     
************************************************************************/

/********************************** START ******************************/ 

.chzn-select {
	visibility: hidden;
}

.chzn-container {
	display: inline-block;
	position: relative;
	vertical-align: bottom;
	z-index: 1010;

    .chzn-single {
        background-color: @sgColorSelectBackground;
        border: 1px solid @sgColorSelectBorder;
        color: @sgColorSelectText;
        z-index: 1013;

        .remHeight(@sgMiscHeightElements);
		.remLineHeight(@sgMiscHeightElements);
		.remPadding(0, 6.5, 0, @sgPaddingSelectLeft);

		&:hover {
			background-color: @sgColorSelectBackgroundHover;

			.sg-handle {
				background-color: @sgColorSelectHandleBackgroundHover;
				color: @sgColorSelectHandleHover;
			}
		}

		.box-sizing(border-box);
		.remBorderRadiusAll(@sgMiscBorderRadiusDefault);
		.transition(@sgMiscDurationTransitionDefault);
		.transform-origin(50% 0%);
		.transform(translateZ(0));

		&.sg-animate-down {
			.sg-animate('sg-animation-custom-select-expand', @sgMiscDurationTransitionDefault);
		}

		&.sg-animate-up {
			.sg-animate('sg-animation-custom-select-collapse', @sgMiscDurationTransitionDefault);
		}

		.sg-handle {
			background-color: @sgColorSelectHandleBackground;
			color: @sgColorSelectHandle;
			height: 100%;

			.box-sizing(border-box);
			.remBorderRadiusAll(@sgMiscBorderRadiusDefault);
			.remWidth(@sgMiscHeightElements);
			.transition(@sgMiscDurationTransitionDefault);

			.sg-divider {
				background-color: @sgColorSelectBorder;

				.remHeight(@sgMiscHeightCustomSelectDivider);
				.remMarginTop((@sgMiscHeightElements - @sgMiscHeightCustomSelectDivider - 0.2) / 2);
				.remWidth(0.1);
				
				&:after {
					position: absolute;
					font-family: @sgFontFamilyIcons;
					
					content: "\e63f";

					.remFontSize(1.4 / 5 * @sgMiscHeightElements);
					.remTop(0);
					.remRight(1.9 / 5 * @sgMiscHeightElements);
					
					.transition-transform(@sgMiscDurationTransitionDefault);
				}
			}
		}
	}

	.chzn-drop {
        border: 1px solid @sgColorSelectBorder;
        z-index: 1012;

        .box-sizing(content-box);
		.remBorderRadiusAll(@sgMiscBorderRadiusDefault);
		.remMarginTop(-1 * @sgMiscHeightElements + 0.2);
		.transition(@sgMiscDurationTransitionDefault margin-top);

		&.sg-animate-down {
			.remMarginTop(@sgMarginSelectResultsTop);
		}

		&.sg-animate-up {
			overflow: hidden;

			.chzn-search {
				.remMarginTop(-0.9);
			}

			.chzn-results {
				.active-result {
					border-width: 0;
					height: 0;
					min-height: 0;
                    padding-top: 0;
                    padding-bottom: 0;
				}
			}
		}

		.chzn-search {
			position: relative;

			.chzn-search-icon {
				position: absolute;
				top: 50%;
				.remMarginTop(-1);
				.remWidth(0);
				.remLeft(@sgPaddingSelectLeft);
				
				&:after {
					color: @sgColorSelectSearchIcon;
					content: "\e627";
					font-family: @sgFontFamilyIcons;
				}
			}

			input[type="text"] {
				background-color: @sgColorSelectSearchInputBackground;
				border: none;
				color: @sgColorSelectSearchInputText;
				width: 100% !important;

				.placeholder(@sgColorSelectSearchInputPlaceholder);
				.remPaddingLeft(3.5 + @sgPaddingSelectLeft);
			}
		}

		.chzn-results {
			margin: 0;
			overflow: hidden;
			padding: 0;

			.remMaxHeight(25.4);
			.transition(@sgMiscDurationTransitionDefault);

			.active-result {
				background-color: @sgColorSelectResultBackground;
                border-top: 1px solid @sgColorSelectBorderItem;
                color: @sgColorSelectResultText;

				.remMinHeight(@sgMiscHeightElements);
                .remPaddingLeft(@sgPaddingSelectLeft);
                .remPaddingRight(@sgPaddingSelectRight);
                .remPaddingTop(0.4);
                .remPaddingBottom(0.4);
				.transition(@sgMiscDurationTransitionDefault);

				@innerRadius: @sgMiscBorderRadiusDefault - 0.1;

				&:first-child {
					border-top: none;

					.remBorderTopRadius(@innerRadius);
				}

				&:last-child {
					.remBorderBottomRadius(@innerRadius);
				}

				&.highlighted {
					background-color: @sgColorSelectResultBackgroundHighlighted;
				}

				&.result-selected {
                    background-color: @sgColorSelectResultBackgroundHighlighted;
					color: @sgColorSelectResultTextSelected;
				}

				&:before {
					content: none;
				}

				&:hover {
					background-color: @sgColorSelectResultBackgroundHover;
					color: @sgColorSelectResultTextHover;
				}
			}

			.no-results {
				background-color: @sgColorSelectResultBackground;
				color: @sgColorSelectSearchNoResults;

				.remHeight(@sgMiscHeightElements);
				.remLineHeight(@sgMiscHeightElements + 0.4);
				.remPadding(0, 2, 0, 2);

				&:before {
					content: none;
				}
			}
		}
	}

    &.chzn-disabled {
        .chzn-single {
            background-color: @sgColorSelectBackgroundDisabled;
            color: @sgColorSelectTextDisabled;
            cursor: default;

            .sg-handle {
                background-color: @sgColorSelectHandleBackgroundDisabled;
                color: @sgColorSelectHandleDisabled;
            }
        }
    }

	&.sg-error {
		border: 1px solid @sgColorTextfieldBorderError;

		color: @sgColorTextfieldTextError;

		.sg-animate('sg-animation-shake');
		.remBorderRadiusAll(@sgMiscBorderRadiusDefault);

		&:hover {
			border-color: @sgColorTextfieldBorderErrorHover;
		}
	}

	&.sg-completed {
		.chzn-single {
			background-color: @sgColorSelectBackgroundCompleted;
			border-color: @sgColorSelectBorderCompleted;
			color: @sgColorSelectTextCompleted;
	
			.sg-handle {
				background-color: @sgColorSelectHandleBackgroundCompleted;
				color: @sgColorSelectHandleCompleted;
			}
		}
	}

	&.chzn-container-active {
		z-index: 1014;

		.remPerspective(80);

		.chzn-single {
			background-color: @sgColorSelectBackgroundActive;
			border-color: @sgColorSelectBorderActive;
			color: @sgColorSelectTextActive;

			.sg-handle {
				background-color: @sgColorSelectHandleBackgroundActive;
				border-left-color: @sgColorSelectBorderActive;
				color: @sgColorSelectHandleActive;
	
				.sg-divider {
					background-color: @sgColorSelectBorderActive;
				}
			}

			&.chzn-single-with-drop {
				.sg-handle {
					.sg-divider {
						background-color: @sgColorSelectBorderActive;

						&:after {
							.transform(rotate(180deg));

							.transition-transform(@sgMiscDurationTransitionDefault);
						}
					}
				}
			}
		}

		.chzn-drop {
			.sg-animate-down {
				z-index: 1013
			}
		}
	}
}

.chzn-container-single {
	.chzn-single {
		.sg-handle {
			position: absolute;
			right: 0;
			top: 0;
			display: block;
		}
	}
}

.ie8 {
    select {
        display: inline !important;
        visibility: visible !important;
    }
}