.hpbx-initialize-variables-table(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorTableCaptionBackground:   @sgColorPurple1;
    @hpbxColorTableHeadText:            @sgColorPurple1;
    @hpbxColorTableExtension:           @sgColorPurple1;

    @hpbxTopDeleteIcon:                 0.7;
}

.hpbx-initialize-variables-table(@styleguide) when (@styleguide = 'unitymedia') {
    @hpbxColorTableCaptionBackground:   @sgColorBlue;
    @hpbxColorTableHeadText:            @sgColorBlue;
    @hpbxColorTableExtension:           @sgColorBlue;

    @hpbxTopDeleteIcon:                 0.7;
}

.hpbx-initialize-variables-table(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorTableCaptionBackground:   @hpbxColorPrimary;
    @hpbxColorTableHeadText:            @hpbxColorPrimary;
    @hpbxColorTableExtension:           @hpbxColorPrimary;

    @hpbxTopDeleteIcon:                 1.7;
}

.hpbx-initialize-variables-table(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-table('ziggo');
}

.hpbx-initialize-variables-table(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-table('ziggo');
}

.hpbx-initialize-variables-table(@hpbxStyleguide);

.hpbx-table-caption {
    background-color: @hpbxColorTableCaptionBackground;
    color: @sgColorWhite;
    text-align: left;

    .remBorderTopRadius(0.4);
    .remHeight(5);
    .remMarginTop(3);

    &~ .hpbx-table {
      margin-top: 0;
    }
}

.hpbx-table-caption-link {
    color: @sgColorWhite;
    text-decoration: none;

    &:hover {
      .sg-icon {
        color: @sgColorWhite;
      }
    }

    .sg-icon {
        float: left;

        .remPadding(2,1.3,1.6,2);
    }

    .sg-header-5 {
        color: @sgColorWhite;
        margin-top: 0;

        .remPaddingTop(1.5);
    }
}

.hpbx-table {
    border-bottom: 1px solid @sgColorGrey4;
    border-left: 1px solid @sgColorGrey4;
    border-right: 1px solid @sgColorGrey4;
    border-spacing: 0;
    overflow: hidden;

    .box-shadow(0 5px 10px rgba(204,208,218,.5));
    .remBorderBottomRadius(0.4);
    .remMarginTop(3);

    &.hpbx-table-fullwidth {
        width: 100%;
    }

    &.hpbx-table-inside-tabs {
        border: none;
        margin: 0;

        .box-shadow(none);
    }

    .hpbx-table-head() {
        background-color: @sgColorGrey7;

        th, td {
            color: @hpbxColorTableHeadText;
            font-weight: normal;
            text-align: left;

            .remPadding(0.5,0,0.4,2);

            &.hpbx-sortable {
                cursor: pointer;
                position: relative;

                &:after {
                    position: absolute;

                    .remFontSize(0.8);
                    .remPaddingLeft(1);
                    .remTop(0.8);
                    .sg-lgi-icon-font();
                }

                &.hpbx-sorted-asc:after {
                    content: "\e62d";
                }
                &.hpbx-sorted-desc:after {
                    content: "\e62b";
                }
            }
        }

        th a {
            color: @hpbxColorTableHeadText;
            text-decoration: none;
        }
    }

    thead {
        .hpbx-table-head();
    }

    .hpbx-table-head-row {
        .hpbx-table-head();
    }

    tbody {
        background-color: @sgColorWhite;

        tr {
            &:hover {
                background-color: @sgColorGrey7;

                .hpbx-delete {
                    overflow: visible;
                    visibility: visible;
                }
            }

            &[data-url] {
                cursor: pointer;
            }
        }

        td {
            border-top: 1px solid @sgColorGrey5;
            color: @sgColorGrey2;
            word-break: break-all;

            .remFontSize(1.2);
            .remPadding(1.6,0,1.2,2);

            .hpbx-pbx-extension {
                color: @hpbxColorTableExtension;
                font-weight: bold;
            }

            &.hpbx-nowrap {
                white-space: nowrap;
            }
        }
    }

    /* Hiding and displaying of columns */
    tr {
        th,td {
            display: none;
            visibility: hidden;

            &.hpbx-show {
                display: table-cell;
                visibility: visible;
            }
        }
    }
}

.hpbx-table-extra-row-when-has-pagination {
    td {
        padding: 2px !important;
    }
}

.hpbx-table-button-row {
    background-color: @sgColorWhite;
    border-top: 1px solid @sgColorGrey5;

    .remBorderRadius(0, 0, 0.3, 0.3);
    .remPadding(2, 2, 1, 2);

    .sg-btn {
        width: 100%;

        .remMarginBottom(1);
    }
}

// Specific for each table
.hpbx-table-call-log {
    tr {
        td {
            .remPaddingBottom(1.4);
            .remPaddingTop(1.7);
        }
    }
}

.hpbx-table-subscribers {
    tr {
        td {
            position: relative;
        }

        td:first-child {
            color: @sgColorGrey1;

            .remFontSize(1.4);
        }
    }

    .hpbx-delete {
        position: absolute;

        .remRight(1.7);
        .remTop(@hpbxTopDeleteIcon);
    }
}

.hpbx-table-voicemail {
    tr {
        cursor: pointer;

        &:hover {
            .hpbx-audio-control-icon:after {
                color: @hpbxColorPrimary;
            }
        }
    }
}

.hpbx-table-selector-caption {
    border-bottom: 1px solid @sgColorGrey5;

    .remPaddingBottom(1.5);
    .remPaddingLeft(2);
    .remPaddingRight(2);
    .remPaddingTop(1.5);
}

.hpbx-table-selector-title {
    .remMarginBottom(0.5);
    .remMarginTop(1);
}

.hpbx-table-xs() {
    .hpbx-table-caption {
        .remMarginTop(2.6);
    }

    .hpbx-table {
        .remMarginTop(2.6);

        /* Hiding and displaying of columns */
        tr {
            th,td {
                &.hpbx-show-xs {
                    display: table-cell;
                    visibility: visible;
                }

                &.hpbx-hide-xs {
                    display: none;
                    visibility: hidden;
                }
            }
        }

        &.hpbx-table-subscribers {
            margin-top: 0;
        }
    }
}

@media @XS {
    .hpbx-table-xs();
}

.hpbx-table-sm() {
    .hpbx-table {
        /* Hiding and displaying of columns */
        tr {
            th,td {
                &.hpbx-show-sm {
                    display: table-cell;
                    visibility: visible;
                }

                &.hpbx-hide-sm {
                    display: none;
                    visibility: hidden;
                }
            }
        }
    }
}

.hpbx-table-call-log-sm()  {
    .hpbx-table-button-row {
        .sg-btn {
            width: auto;

            .remMarginLeft(0.6);

            &:first-child {
                margin-left: 0;
            }
        }
    }

    .hpbx-table-button {
        display: inline-block;
    }
}

@media @SM {
    .hpbx-table-call-log-sm();
    .hpbx-table-sm();
}

