/******************************* INFORMATION ***************************

Styling that purely takes care of the functionality of a checkbox or radio button.

     
************************************************************************/

/********************************** START ******************************/ 

.sg-custom-check-and-radio,
.sg-custom-toggle {
 	position: relative;

	input {
		cursor: pointer;
		float: left;
		height: 0;
		margin: 0;
		min-height: 0;
		padding: 0;
		width: 0;

		.opacity(0);
	}

	.sg-alternative-display {
		cursor: pointer;
		left: 0;
		position: absolute;
		top: 0;
	}

    label {
		cursor: pointer;
		display: block;
	}

    label > * { pointer-events: none; }

    &.sg-disabled {
        label {
            cursor: default;
        }
    }
}

.ie8 {
    .sg-alternative-display {
        display: none !important;
    }

    .sg-custom-check-and-radio,
    .sg-custom-toggle {
        input {
            height: auto;
            width: auto;

            .opacity(1);
            .remMarginRight(2);
            .remMarginTop(0.6);
        }
    }
}