.sg-styleguide-file-type-block {
	@animationDuration: 0.2s;

	cursor: pointer;

	.sg-filetype-icon {
		display: inline-block;
		position: relative;
		vertical-align: bottom;

		.sg-icon {
			bottom: 2.3rem;
			display: block;
			margin-left: 0.4rem;
			position: absolute;

			.sg-animate('sg-animation-bounce-down', @animationDuration * 2);
			.transition(@animationDuration ease-in);
		}
	}

	.sg-caption {
		line-height: 8rem;
		padding-left: 0.5rem;
		.transition(@sgMiscDurationTransitionDefault);
	}

	&:hover {
		.sg-filetype-icon {
			.sg-icon {
				bottom: 2.7rem;

				.sg-animate('sg-animation-clear', @animationDuration * 2);
				.transition(@animationDuration);
			}
		}

		.sg-caption {
			color: @sgColorAnchorHover;
		}
	}
}