.hpbx-help-loading-overlay {
    background-color: @sgColorWhite;
    display: none;
    height: 100%;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1105;

    .transition-multiple(opacity 0s ease 0s, z-index 0s ease 0s, left 0.5s ease-out;);

    &.hpbx-animate-away {
        z-index: 1103;

        .opacity(0);
        .transition-multiple(opacity 0.3s ease 0.8s, z-index 0s ease 1.1s, left 0.5s ease-out;);

        .hpbx-help-loading-wait-container {
            .remHeight(8.1);
            .transition(height 0.5s ease 0.3s);
        }

        .hpbx-loader-animation-container {
            .remBottom(100);
            .transition(bottom 0.3s ease-in);
        }

        .hpbx-help-loading-text {
            .opacity(0);
            .transition(opacity 0.3s);
        }
    }
}

body.hpbx-help-opened {
    &.hpbx-help-no-loader {
        .hpbx-help-loading-overlay {
            display: none;
        }
    }

    .hpbx-help-loading-overlay {
        display: block;
    }
}


.hpbx-help-loading-wait-container {
    background-color: @sgColorGrey7;
    border-bottom: 1px solid @sgColorGrey4;
    height: 50%;
    position: relative;
}

.hpbx-loader-animation-container {
    bottom: 0;
    left: 0;
    position: absolute;
    text-align: center;
    width: 100%;
}

.hpbx-help-loading-text {
    text-align: center;

    .remPaddingTop(3);
}

.hpbx-help-loading-overlay-md() {
    body.hpbx-help-opened {
        .hpbx-help-loading-overlay {
            left: 0;
        }
    }
}

@media @MD {
    .hpbx-help-loading-overlay-md();
}

.hpbx-help-loading-overlay-lg() {
    .hpbx-help-loading-overlay {
        display: block;

        .remLeft(-@hpbxHelpWidth + 0.1);
        .remWidth(@hpbxHelpWidth - 0.1);
    }
}

@media @LG {
    .hpbx-help-loading-overlay-lg();
}

.ie8 {
    .hpbx-help-loading-overlay-md();
    .hpbx-help-loading-overlay-lg();

    .hpbx-help-loading-overlay {
        .remLeft(-@hpbxHelpWidthIe8);
        .remWidth(@hpbxHelpWidthIe8);
    }
}

