.hpbx-autocomplete-row {
    position: relative;
}

.hpbx-autocomplete-popover {
    background-color: @sgColorWhite;
    border: 1px solid @sgColorGrey3;
    visibility: hidden;
    max-height: 250px;
    overflow-y: auto;
    overflow-x: hidden;
    position: absolute;
    z-index: 1011;

    .box-shadow(0 2px 2px rgba(0,0,0,.15));

    .remBorderRadius(0.4, 0.4, 0.4, 0.4);
    .remMargin(0.3, 0, 0, 0);
}

.hpbx-autocomplete-list {
    list-style: none;

    .remBorderRadius(0.4, 0.4, 0.4, 0.4);
    .remMargin(0, 0, 0, 0);
    .remPadding(0, 0, 0 ,0);
}

.hpbx-autocomplete-list-item,
.hpbx-autocomplete-list-title {
    color: @sgColorGrey2;

    .remMargin(0, 0, 0, 0);
    .remPadding(0.8, 1, 0.8, 1);
    .remLineHeight(2.2);

    &:before {
        content: none;
    }

    &:first-child {
        .remBorderTopRadius(0.4);
    }

    &:last-child {
        .remBorderBottomRadius(0.4);
    }
}

.hpbx-autocomplete-list-item {
    cursor: pointer;

    &:hover,
    &.hpbx-autocomplete-list-item-active {
        background-color: @sgColorGrey6;
        color: @sgColorGrey1;
    }
}

.hpbx-autocomplete-list-title {
    font-style: italic;
}

.hpbx-autocomplete-list-item-hidden {
    display: none;
}

.hpbx-autocomplete-popover-active {
    visibility: visible;
 }



