.sg-subtitle {
	color: @sgColorSubtitle;
	font-family: @sgFontFamilySubtitle;
	font-weight: normal;
	line-height: @sgLineHeightSubtitle;
	text-transform: @sgTextTransformSubtitle;

	.sg-calculate-margins(@sgFontSizeSubtitle, @sgLineHeightSubtitle, @sgMarginTopSubtitle);
	.remFontSize(@sgFontSizeSubtitle);
}
