.sg-handle {
    .vertical(@sgColorGrey7, #bcc1c5);
}

.chzn-results {
    .box-shadow(0 2px 2px rgba(0,0,0,.15));
}

.chzn-container {
    .chzn-single {
        .remPaddingRight(5);

        .sg-divider {
            background-color: @sgColorSelectBackground !important;
            height: 100% !important;
            margin: 0 !important;
        }

        .sg-handle .sg-divider:after {
            content: '\e62b';
            font-family: 'LGI-Iconfont';

            .remFontSize(1.3);
            .remRight(1.2);
        }
    }

    .chzn-drop {
        .chzn-results {
            .active-result {
                .remPaddingBottom(0.9);
                .remPaddingTop(0.9);
            }
        }

        .chzn-search input {
            .remPaddingBottom(0.9);
            .remPaddingTop(0.9);
        }
    }
}