.hpbx-initialize-variables-graph(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorGraphInfoMain: @sgColorPurple1;
    @hpbxColorGraphInfoSub: @sgColorGrey3;
    @hpbxColorGraphInfoIcon: @sgColorPurple1;
    @hpbxColorGraphLineHover: @sgColorPurple1;
}

.hpbx-initialize-variables-graph(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-graph('upc-business');
}

.hpbx-initialize-variables-graph(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorGraphInfoMain: @sgColorGrey1;
    @hpbxColorGraphInfoSub: @sgColorGrey2;
    @hpbxColorGraphInfoIcon: @hpbxColorPrimary;
    @hpbxColorGraphLineHover: @sgColorOrangeExtra;
}

.hpbx-initialize-variables-graph(@styleguide) when (@styleguide = 'virgin') {
    @hpbxColorGraphInfoMain: @sgColorGrey1;
    @hpbxColorGraphInfoSub: @sgColorGrey2;
    @hpbxColorGraphInfoIcon: @hpbxColorPrimary;
    @hpbxColorGraphLineHover: @sgColorRedHover;
}

.hpbx-initialize-variables-graph(@styleguide) when (@styleguide = 'vtr') {
    @hpbxColorGraphInfoMain: @sgColorGrey1;
    @hpbxColorGraphInfoSub: @sgColorGrey2;
    @hpbxColorGraphInfoIcon: @hpbxColorPrimary;
    @hpbxColorGraphLineHover: @sgColorPinkExtra;
}

.hpbx-initialize-variables-graph(@hpbxStyleguide);

.hpbx-graph-container {
    background-color: @sgColorGrey6;
    position: relative;
}

.hpbx-graph-info {
    position: relative;
    visibility: hidden;

    .remHeight(7.5);
    .remMarginLeft(2);

    &.hpbx-visible {
        visibility: visible;
    }
}

.hpbx-graph-info-icon {
    color: @hpbxColorGraphInfoIcon;
    left: 0;
    position: absolute;
    top: 0;

    .remFontSize(2.4);
}

.hpbx-graph-info-content {
    font-family: @sgFontFamilyHeadings;

    .remPaddingLeft(3.7);
}

.hpbx-graph-info-content-main {
    color: @hpbxColorGraphInfoMain;
    line-height: @sgLineHeightH4;

    .remFontSize(@sgFontSizeH4);
}

.hpbx-graph-info-content-sub {
    color: @hpbxColorGraphInfoSub;
    line-height: @sgLineHeightH6;

    .remFontSize(@sgFontSizeH6);
}

.hpbx-graph {
    display: block;
    overflow: visible;
    width: 100%;

    .hpbx-graph-line {
        fill: transparent;
        stroke: @hpbxColorPrimary;
        stroke-width: 2;
    }

    .hpbx-graph-dots {
        cursor: pointer;
        fill: @sgColorGrey6;
        stroke: @hpbxColorPrimary;
        stroke-width: 2;

        &:hover {
            fill: @sgColorWhite;
            stroke: @hpbxColorGraphLineHover;
        }
    }

    .hpbx-graph-grid {
        stroke: @sgColorGrey6;
        stroke-width: 1;
    }
}

.hpbx-graph-legend {
    background-color: @sgColorGrey1;

    .remHeight(8);
}

.hpbx-graph-legend-svg {
    width: 100%;
}

.hpbx-graph-legend-line {
    display: none;
    stroke: @sgColorGrey2;
    stroke-width: 1;
}

.hpbx-graph-legend-item {
    color: @sgColorGrey3;
    font-family: @sgFontFamilyHeadings;
    position: absolute;
    text-align: center;

    .remFontSize(@sgFontSizeH6);
    .remBottom(2.7);
    .remWidth(10);
}

@media @XS {
    .hpbx-graph-xs();
}

.hpbx-graph-xs() {
    .hpbx-graph,
    .hpbx-graph-legend-svg {
        width: ~"calc(100% - 50px)";
    }
}

@media @MD {
    .hpbx-graph-md();
}

.hpbx-graph-md() {
    .hpbx-graph,
    .hpbx-graph-legend-svg {
        width: ~"calc(100% - 250px)";
    }
}

.ie8 {
    .hpbx-graph-xs();
    .hpbx-graph-md();
}

