/******************************* INFORMATION ***************************

Project-specific styling of the checkbox.
     
************************************************************************/

/********************************** START ******************************/ 

.sg-custom-check {
	.sg-alternative-display {
		text-align: center;

		.remBorderRadiusAll(@sgMiscBorderRadiusCheckbox);
		.transition(@sgMiscDurationTransitionDefault);

		.sg-icon-check:after {
			color : @sgColorCheckHover;
			display: block;
			line-height: 0;
			
			.remMarginTop(3.5);
			.remHeight(@sgMiscSizeCheckbox);
			.remWidth(@sgMiscSizeCheckbox);
			
			.transition(@sgMiscDurationTransitionDefault);
		}		
	}

    input:checked ~ .sg-alternative-display {
        background-color: @sgColorCheckRadioBackgroundChecked;

        .sg-icon-check:after {
            color: @sgColorCheckChecked;

            .remMarginTop(1.1);
            .transition(@sgMiscDurationTransitionDefault);
        }
    }

    &:hover {
        .sg-alternative-display {
            background-color: @sgColorCheckRadioBackgroundHover;
            border-color: @sgColorCheckRadioBorderHover;

            .sg-icon-check:after {
                .remMarginTop(1.1);
                .transition(@sgMiscDurationTransitionDefault);
            }   
        }

        input:checked ~ .sg-alternative-display {
            background-color: @sgColorCheckRadioBackgroundChecked;
        }

        &.sg-disabled {
            .sg-alternative-display {
                background-color: @sgColorCheckRadioBackgroundDisabled;
                border-color: @sgColorCheckRadioBorderDisabled;

                .sg-icon-check:after {
                    display: none;
                }   
            }

            input:checked ~ .sg-alternative-display {
                .sg-icon-check:after {
                    display: block;
                }   
            }
        }
    }

    &.sg-disabled {
        .sg-alternative-display {
            background-color: @sgColorCheckRadioBackgroundDisabled;
        }
    }
}

