html {
	font-size: @sgFontSizeBasePercentage * @sgFontSizeRatioXS;
	
	@media (min-width: @screen-xs-min) {
		font-size: @sgFontSizeBasePercentage * @sgFontSizeRatioS;
	}

	@media (min-width: @screen-sm-min) {
		font-size: @sgFontSizeBasePercentage * @sgFontSizeRatioS;
	}

	@media (min-width: @screen-md-min) {
		font-size: @sgFontSizeBasePercentage * @sgFontSizeRatioM;
	}

	@media (min-width: @screen-lg-min) {
		font-size: @sgFontSizeBasePercentage * @sgFontSizeRatioL;
	}
}
