.hpbx-initialize-variables-wizard(@hpbxStyleguide);

.hpbx-page-wizard {
    &[data-background-id="1"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-1.@{hpbxWizardExt}"); }
    &[data-background-id="2"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-2.@{hpbxWizardExt}"); }
    &[data-background-id="3"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-3.@{hpbxWizardExt}"); }
    &[data-background-id="4"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-4.@{hpbxWizardExt}"); }
    &[data-background-id="5"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-5.@{hpbxWizardExt}"); }
    &[data-background-id="6"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-6.@{hpbxWizardExt}"); }
    &[data-background-id="7"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-7.@{hpbxWizardExt}"); }
    &[data-background-id="8"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-8.@{hpbxWizardExt}"); }
    &[data-background-id="9"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-9.@{hpbxWizardExt}"); }
    &[data-background-id="10"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-10.@{hpbxWizardExt}"); }
    &[data-background-id="ready"] .hpbx-wizard-cartoon { background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-ready.@{hpbxWizardExt}"); }

    .hpbx-header {
        background-color: transparent;
        border: none;
        position: relative;
    }

    .hpbx-header-logo {
        background: url("../../dist/img/company-logo-md-@{hpbxStyleguide}.png")   no-repeat;
        // background: url("../../dist/img/company-logo-small-@{hpbxStyleguide}@{hpbxImageNameWizardHeaderLogo}.png") no-repeat;
        background-position: 5px 5px;
        // background-size: 240px 30px; 
        background-size: 65%;
    }

    .hpbx-main-content {
        margin-top: 30vw;
    }

    .hpbx-content-container {
        position: absolute;
        width: ~"calc(100% - 20px)";

        .box-shadow(0 0 30px rgba(0,0,0,.25));
        .remMarginBottom(1);
        .remMarginTop(-2);
        
        &.hpbx-hidden {
            border: none;
            max-height: 0;

            .box-shadow(none);
        }
    }
    .hpbx-help-edit-url-popup-trigger{
        font-size: 2.4rem;
    }
}

.hpbx-wizard-background {
    background-color: @hpbxColorWizardBackgroundStart;
    height: 100%;
    position: fixed;
    width: 100%;

    .directional(@hpbxColorWizardBackgroundStart, @hpbxColorWizardBackgroundEnd, 45deg);
}

.hpbx-wizard-cartoon {
    background-image: url("../../dist/img/wizard/@{hpbxStyleguide}-1.@{hpbxWizardExt}");
    background-position: 100% 0;
    background-repeat: no-repeat;
    background-size: 450px 180px;
    height: 100%;
    position: fixed;
    width: 100%;
}

.hpbx-btn-wizard-exit {
    border: 1px solid @sgColorWhite;
    color: @sgColorWhite;
    cursor: pointer;
    font-family: @sgFontFamilyHeadings;
    position: absolute;
    text-decoration: none;
    text-transform: @hpbxTextTransformWizardExitButton;
    z-index: 1100;

    .remBorderRadiusAll(@hpbxBorderRadiusWizardExitButton);
    .remFontSize(@sgFontSizeH5);
    .remPadding(@hpbxPaddingTopWizardExitButton, @hpbxPaddingRightWizardExitButton, @hpbxPaddingBottomWizardExitButton, @hpbxPaddingLeftWizardExitButton);
    .remRight(1.1);
    .remTop(1);

    &:hover {
        background-color: @hpbxColorPrimary;
        text-decoration: none;
    }
}

.hpbx-btn-wizard-exit-icon {
    position: absolute;

    .remFontSize(1.6);
    .remLeft(@hpbxLeftWizardExitButton);
    .remTop(@hpbxTopWizardExitButton);

    &:before {
        content: @hpbxIconCodeWizardExitButton;

        .sg-lgi-icon-font();
    }
}

.hpbx-wizard-title {
    border-bottom: 1px solid @sgColorGrey4;
    color: @sgColorGrey1;
    margin: 0;

    .remPadding(3, 4, 3.3, 4);
}

.hpbx-wizard-progress {
    overflow: hidden;

    .remHeight(2.4);
    .remMarginTop(-1.3);
    .remMarginBottom(-1.1);
}

.hpbx-wizard-progress-inner {
    position: relative;

    .remMargin(0, 4, 0, 4);
}

.hpbx-wizard-progress-step {
    background-color: @sgColorWhite;
    border: 1px solid @sgColorGrey4;
    position: absolute;

    .box-sizing(border-box);
    .remBorderRadiusAll(10);
    .remHeight(2.4);
    .remWidth(2.4);

    &.hpbx-current,
    &.hpbx-completed {
        border-color: @hpbxColorWizardProgress;

        &:before {
            background-color: @hpbxColorWizardProgress;
            content: '';
            display: block;
            height: 1px;
            position: absolute;

            .remRight(2.3);
            .remTop(1.1);
            .remWidth(100);
        }
    }

    &.hpbx-completed {
        &:after {
            color: @hpbxColorWizardProgress;
            content: '\e624';
            position: absolute;
            top: 0;
            
            .remLeft(0.6);
            .sg-lgi-icon-font();
        }
    }

    &.hpbx-unavailable {
        display: none;
    }
}

.hpbx-wizard-content {
    background-color: @sgColorGrey7;
    border-bottom: 1px solid @sgColorGrey4;
    color: @sgColorGrey2;
 
    .remPadding(3.5, 4, 2.1, 4);

    .hpbx-wizard-content-title:first-child {
        margin-top: 0;
    }
}

.hpbx-wizard-content-title {
   margin: 0;

   .remMarginTop(3);
}

.hpbx-wizard-buttons {
    .remHeight(7.5);
}

.hpbx-wizard-buttons-left {
    float: left;

    .remPadding(2.7, 0, 0, 4);
}

.hpbx-wizard-buttons-no {
    float: right;

    .remPadding(2.7, 1.2, 0, 4);
}

.hpbx-wizard-buttons-right {
    float: right;

    .remPadding(2, 4, 0, 0);
}

.hpbx-wizard-lots-of-space {
    display: none;

    &+ .sg-btn-icon {
        display: none;
    }
}

.hpbx-wizard-xs-only() {
    .hpbx-page-wizard {
        .hpbx-btn-wizard-exit {
            .remRight(8);
            .remTop(1.3);
            &:hover {
                background-color: @sgColorWhite;
                border-color: @sgColorGrey7 !important;
                color: @hpbxColorPrimary;

            }
        }
        .hpbx-content-container {
            .remMarginTop(9);
        }
    }
    
    .hpbx-wizard-little-space {
        display: none;
    
        &+ .sg-btn-icon {
            display: none;
        }
    }

    .hpbx-wizard-lots-of-space {
        display: inline-block;

        &+ .sg-btn-icon {
            display: inline;
        }
    }
}

@media @XS_ONLY {
    .hpbx-wizard-xs-only();
}

.hpbx-wizard-sm() {
    .hpbx-page-wizard {
        .hpbx-btn-wizard-exit {
            .remRight(10);
            .remTop(2.3);
            &:hover {
                background-color: @sgColorWhite;
