            //background-size: contain;
            background-position: 5px 5px;
            margin-top: 0;
            background-size: auto;
        }

        .hpbx-main-content {
            margin-top: 0;

            .remPaddingTop(5);
        }

        .hpbx-content-container {
            .remMarginBottom(2);
            .remMarginTop(-6);
        }
    }

    .hpbx-wizard-cartoon {
        background-position: 50% 0;
        background-size: 136.7521% auto;
    }
}

