        display: inline-block;

        &+ .sg-btn-icon {
            display: block;
        }
    }

    .hpbx-wizard-lots-of-space {
        display: none;

        &+ .sg-btn-icon {
            display: none;
        }
    }

    .hpbx-wizard-progress {
        max-height: 0;
    }

    .hpbx-wizard-progress-step {
        display: inline-block;
