.hpbx-music-on-hold {
    .hpbx-upload-audio-container {
        margin-top: 0;
    }

    .hpbx-audio-container-singular {
        border-top: none;
    }

    .hpbx-audio-selector-no-audio-label {
        .remPaddingLeft(1);
    }
}

.hpbx-music-on-hold-dialog {
    background-color: transparent;

    .remWidth(32);

    .hpbx-dialog-header {
        .remPaddingTop(0);
    }

    .hpbx-dialog-contents {
        background-color: transparent;
        padding: 0;
        height: auto;

        //.remHeight(23);
    }

    .hpbx-table-head-row {
        &:first-child {
            td {
                border-top: 0;
            }
        }
    }

    .hpbx-audio-container {
        font-family: @sgFontFamilyHeadings;

        &:hover {
            .hpbx-table-voicemail-time {
                .remPaddingBottom(1.2);
                .remPaddingTop(1.6);
            }
        }

        .hpbx-table-music-title-container {
            .remFontSize(1.8);
        }

        .hpbx-table-music-time {
            .remFontSize(1.4);
        }
    }
}

/** XS **/
.hpbx-music-on-hold-dialog-xs {

    .hpbx-music-on-hold-dialog {
        .remWidth(46);

        .hpbx-dialog-header {
            .remPaddingTop(3.5);
        }

        .hpbx-dialog-contents {
            //.remHeight(31);
            height: auto;
        }
    }
}

@media @XS {
    .hpbx-music-on-hold-dialog-xs();
}