@media @XS {
    .hpbx-404-xs();
}


.hpbx-404-xs() {
    body.hpbx-page-404 {
        .hpbx-menu-handle-open {
            &:before {
                color: @hpbxColor404HeaderMenuOpenIconSM;
            }
        }
        .hpbx-header-logo {
        // background: url("../../dist/img/company-logo-small-@{hpbxStyleguide}@{hpbxImageNameWizardHeaderLogo}.png") no-repeat;
        background-image: url("../../dist/img/company-logo-md-@{hpbxStyleguide}.png") center center;
        //background-size: contain;
        // background-position: 5px 5px;
        background-size: 70%;
    }
    }
    .hpbx-404 {
      width: 60%;
      margin-left: auto;
      margin-right: auto;
    }
}

@media @SM {
    .hpbx-404-sm();
}

.hpbx-404-sm() {
    body.hpbx-page-404 {
        .hpbx-header-logo {
            // url("../../dist/img/company-logo-md-@{hpbxStyleguide}.png")
            // background-image: url("../../dist/img/company-logo-md-@{hpbxStyleguide}.png");
            background: url("../../dist/img/company-logo-md-@{hpbxStyleguide}.png") 10% 5% no-repeat, url("../../dist/img/company-logo-@{hpbxStyleguide}.jpg") left bottom no-repeat;
            background-color:@sgColorGrey1;
            // background-position: 5px 5px;
             background-size: auto, 100% 48%;
        }
    }
    .hpbx-404 {
        .remMarginTop(-12.4);
      width: 40% !important;
      margin-left: auto !important;
      margin-right: auto !important;
    }
    


}

.ie8 {
    .hpbx-404-sm();
}
