.sg-animate(@type) {
	.sg-animate(@type, 1s);
}

.sg-animate(@type, @duration, @fill: both) {
	-webkit-animation-duration: @duration;
	animation-duration: @duration;

	-webkit-animation-fill-mode: @fill;
	animation-fill-mode: @fill;

	-webkit-animation-name: @type;
	animation-name: @type;

}
