.sg-btn {
    position: relative;

    //.remBorderRadiusAll(0.4);
    .remBorderRadiusAll(10.0);
    .remMinHeight(@sgMiscHeightElements);
    .transition(0s);

     &.sg-btn-small {
        .sg-btn-small-vtr();
    }

    &.sg-disabled {
        .sg-btn-vtr-disabled()
    }

    &.sg-pink {
        .sg-btn-vtr-pink()
    }

    &.sg-purple {
        .sg-btn-vtr-purple()
    }

    &.sg-grey {
        .sg-btn-vtr-grey()
    }

    &.sg-without-icon {
        .remPaddingRight(@sgPaddingButtonLeft);
    }
}

.sg-btn-small-vtr() {
    line-height: 0;
    .remMarginBottom(0.4);
    .remFontSize(1.4);
    .remMaxHeight(2.7);
    .remMinHeight(2.9);
    .remMinWidth(9);
    .remPadding(@sgPaddingButtonSmallTop, @sgPaddingButtonSmallRight, @sgPaddingButtonSmallBottom, @sgPaddingButtonSmallLeft);

    &.sg-without-icon {
        .remPaddingRight(@sgPaddingButtonSmallLeft);
    }
    
    &+ .sg-btn-icon {
        &:before {
            .remFontSize(1.1);
            .remLeft(-1.6);
            .remTop(0.3);
        }
    }
}

button.sg-btn.sg-btn-small {
    .remPaddingTop(0.2);
}

.sg-btn-vtr-pink() {
    .sg-give-color-to-element(@sgColorButtonPink, @sgColorButtonPinkHover, @sgColorButtonPinkText, @sgColorButtonPinkText, @sgColorButtonPink, @sgColorButtonPinkHover);
    .sg-give-active-color-to-element(@sgColorButtonPinkActive, rgba(255,255,255,0.5), @sgColorButtonPinkActiveBorder);

    &:active {
        .box-shadow(inset 0 3px 5px rgba(0,0,0,.4););
    }
}

.sg-btn-vtr-purple() {
    .sg-give-color-to-element(@sgColorButtonPurple, @sgColorButtonPurpleHover, @sgColorButtonPurpleText, @sgColorButtonPurpleText, @sgColorButtonPurple, @sgColorButtonPurpleHover);
    .sg-give-active-color-to-element(@sgColorButtonPurpleActive, rgba(255,255,255,0.5), @sgColorButtonPurpleActiveBorder);

    &:active {
        .box-shadow(inset 0 3px 5px rgba(0,0,0,.4););
    }
}

.sg-btn-vtr-white() {
    .sg-give-color-to-element(@sgColorWhite, @sgColorWhite, @sgColorPink, @hpbxColorPrimary, @hpbxColorPrimary, @sgColorGrey4);
    .sg-give-active-color-to-element(@sgColorWhite, @sgColorPink, @hpbxColorPrimary);

    &:active {
       // .box-shadow(inset 0 3px 5px rgba(0,0,0,.4););
    }
}

.sg-btn-vtr-grey() {
    .sg-give-color-to-element(@sgColorButtonGrey, @sgColorButtonGreyHover, @sgColorButtonGreyText, @sgColorButtonGreyText, @sgColorButtonGrey, @sgColorButtonGreyHover);
    .sg-give-active-color-to-element(@sgColorButtonGreyActive, rgba(255,255,255,0.5), @sgColorButtonGreyActiveBorder);

    &:active {
        .box-shadow(inset 0 3px 5px rgba(0,0,0,.4););
    }
}

.sg-btn-vtr-disabled() {
    .sg-give-color-to-element(@sgColorButtonDisabled, @sgColorButtonDisabled, @sgColorButtonDisabledText, @sgColorButtonDisabledText, @sgColorButtonDisabledBorder, @sgColorButtonDisabledBorder);
}
