.hpbx-dialog {
    padding: 0;
}

.hpbx-dialog-header {
    background-color: @sgColorWhite;
    border-bottom: 1px solid @sgColorGrey4;
    color: @hpbxColorHelpOpenIcon;
    text-align: center;

    .remPaddingBottom(1.5);
    .remPaddingTop(3.5);
}

.hpbx-dialog-header-icon {
    line-height: 1;

    .remFontSize(3.6);
}

.hpbx-dialog-header-title {
    font-family: @sgFontFamilyHeadings;

    .remFontSize(@sgFontSizeH4);
    .remMarginTop(0.5);
}

.hpbx-dialog-contents {
    background-color: @sgColorGrey5;
    border-bottom: 1px solid @sgColorGrey4;

    .remPadding(1.9, 3.5, 1.9, 3.5);
}

.hpbx-dialog-footer {
    background-color: @sgColorWhite;
    position: relative;

    .remHeight(6.3);

    .hpbx-tab-form-link-button {
        .remFontSize(1.6);
    }
}

.hpbx-dialog-footer-left {
    position: absolute;

    .remLeft(3.5);
    .remTop(1.8);
}

.hpbx-dialog-footer-right {
    position: absolute;

    .remRight(1.9);
    .remTop(2.1);
}
