li {
	line-height: @sgLineHeightLists;
	position: relative;

	.remMarginBottom(0.5);

 	&:before {
	    color: @sgColorBullets;
	    left: 0;
		position: absolute;
 	}   
}

ul {
	li {
		.remPaddingLeft(0.9);
	
		&:before {
		    content: "\002022";
		}
	}
}

ol {
	li {
		counter-increment: list;

		.remPaddingLeft(1.7);

		&:before {
			content: counter(list) ".";
		}
	}
}
