.hpbx-initialize-variables-devices(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxFontDevice:                            'BloomSpeakWeb-Bold';
    @hpbxColorDeviceProfile:                    @sgColorPurple1;
    @hpbxColorIdentifierTextfield:              @hpbxColorPrimary;
    @hpbxWidthIdentifierTextfield:              13;
    @hpbxDeviceTitleEditOffset:                 -1;
}

.hpbx-initialize-variables-devices(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-devices('upc-business');
}

.hpbx-initialize-variables-devices(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxFontDevice:                            'MuseoSans700';
    @hpbxColorDeviceProfile:                    @hpbxColorPrimary;
    @hpbxColorIdentifierTextfield:              @sgColorGrey1;
    @hpbxWidthIdentifierTextfield:              15;
    @hpbxDeviceTitleEditOffset:                 -1.4;
}

.hpbx-initialize-variables-devices(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-devices('ziggo');

    @hpbxFontDevice:                            'CircularChatBook';
    @hpbxColorIdentifierTextfield:              @sgColorGrey1;
    @hpbxWidthIdentifierTextfield:              13;
    @hpbxDeviceTitleEditOffset:                 -1;
}

.hpbx-initialize-variables-devices(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-devices('ziggo');
}

.hpbx-initialize-variables-devices(@hpbxStyleguide);

body.page-hpbx-pbxdevices {
    .hpbx-menu-over-menu {
        background-color: transparent;
        display: block;
        position: static;
        width: auto;
        margin-top: 90px;
        .box-shadow(none);
        .transition(opacity 0.4s) !important;
    }
}

.hpbx-add-device {
    .remPaddingTop(1.4);
}

.hpbx-device-model-image-background {
    background: url(../../dist/img/background.png) no-repeat center top;
    background-size: cover;
    border-left: 1px solid @sgColorGrey4;
    border-right: 1px solid @sgColorGrey4;
    border-top: 1px solid @sgColorGrey4;

    .border-top-radius(4px);
}

.hpbx-device-model-image-wrapper {
    background-image: none !important;
    background-repeat: no-repeat;
    background-size: 100%;
    position: relative;

    .clearfix;

    .remMargin(2,0,2,0);

    .hpbx-device-model-image {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -3;
    }

    .hpbx-device-model-image-hidden {
        visibility: hidden;
    }
}

.hpbx-device-subscribers {
    font-family: @hpbxFontDevice;
    width: 100%;
}

.hpbx-device-header {
    background-color: @sgColorWhite;
    color: @sgColorGrey1;
    display: none;

    .remHeight(7.2);

    .hpbx-menu-over-menu-menu-handle-open {
        .remRight(1.6) !important;
    }
}

.hpbx-device-footer {
    background-color: @sgColorGrey1;
    bottom: 0;
    color: @sgColorGrey1;
    left: 0;
    position: fixed;
    right: 0;
    z-index: 1101;

    .remMinHeight(8);
    .remPaddingLeft(1);
    .remPaddingRight(1);

    .hpbx-tab-form-link-button {
        color: @sgColorGrey4;
        float: left;
        text-align: center;

        .remFontSize(1.6);
        .remPadding(1,0,1,0);
        .remMarginTop(2);
        .remMarginLeft(1);
    }

    .sg-btn {
        float: left;

        .remMarginTop(2);
    }
}

.page-hpbx-pbxdevices {
    &.hpbx-help-opened {
        &.hpbx-too-little-space {
            .hpbx-device-header,
            .hpbx-device-footer,
            .hpbx-menu-over-menu-menu-handle-open {
                position: absolute;
            }
        }

        .hpbx-header,
        .hpbx-menu {
            .hpbx-menu-handle-open {
                display: none !important;
            }

            .hpbx-device-header {
                .hpbx-menu-handle-open {
                    display: inline-block !important;
                }
            }
        }

        .hpbx-device-info {
            .remMarginLeft(49.8);
        }

        .hpbx-device-identifier-label {
            .remMarginLeft(2);
        }
    }
}

.hpbx-device-icon {
    float: left;

    .remPadding(1.6, 0, 0, 1.3);

    &:before {
        content: "\e609";
        color: @sgColorGrey3;
        display: block;

        .remFontSize(2.6);
        .sg-lgi-icon-font();
    }
}

.hpbx-device-title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    .remFontSize(2.1);
    .remPadding(2, 2.5, 0, 1);
    .remMaxWidth(20);
}

input.hpbx-device-title-edit {
    font-family: @sgFontFamilyHeadings;
    height: auto;
    line-height: 1;
    min-height: 0;

    .remFontSize(2.1);
    .remMaxWidth(17);
    .remMarginTop(1.7);
    .remPaddingBottom(0.7);
    .remPaddingTop(0.7);
}

.hpbx-page-title {

    .hpbx-device-title {
        max-width: none;
        overflow: visible;
        white-space: normal;
    }

    input.hpbx-device-title-edit {
        display: block;
        max-width: ~"calc(100% - 5rem)";

        .remFontSize(@sgFontSizeH2);
        .remMarginLeft(4.8);
        .remMarginTop(1.5);
    }
}


.hpbx-device-keys {
    display: none;
}

.hpbx-device-key-line {
    border: 0;
    cursor: default;
    height: 1px;
    position: absolute;

    .remMargin(0,0,0,0);
    .remPadding(0,0,0,0);
}

.hpbx-device-key {
    cursor: pointer;
    position: absolute;
    text-align: center;
    z-index: 1;

    .box-shadow(0 0 10px rgba(51,51,51,.5));

    .remBorderRadius(0.3, 0.3, 0.3, 0.3);
    .remFontSize(1.2);
    .remHeight(2);
    .remLineHeight(2);
    .remWidth(2.4);

    &.inactive {
        background-color: @sgColorGrey3;
        color: @sgColorWhite;

        + .hpbx-device-key-line {
            background-color: @sgColorGrey3;
            color: @sgColorGrey3;
        }
    }

    &.active {
        background-color: @hpbxColorPrimary;
        color: @sgColorWhite;

        + .hpbx-device-key-line {
            background-color: @hpbxColorPrimary;
            color: @hpbxColorPrimary;
        }
    }


    &.assigned {
        background-color: @sgColorWhite;
        color: @sgColorGrey3;

        + .hpbx-device-key-line {
            background-color: @sgColorWhite;
            color: @sgColorWhite;
        }
    }
}

.hpbx-device-info {
    background-color: @sgColorGrey2;
    color: @sgColorGrey6;
    z-index: 3;

    .remFontSize(1.2);
    .remMinHeight(5);
    .remPadding(0,2,0,2);
}

.hpbx-device-profile-label,
.hpbx-device-identifier-label {
    color: @hpbxColorDeviceProfile;
    font-weight: bold;

    .remMarginLeft(4.7);

}

.hpbx-device-identifier-label {
    .remMarginTop(1);
}

.hpbx-device-profile,
.hpbx-device-identifier {
    color: @hpbxColorDeviceProfile;

    .remMarginLeft(4.7);
}

input[type="text"].hpbx-device-footer-input-incognito {
    background-color: transparent;
    border: none;
    border-color: transparent;
    color: @hpbxColorDeviceProfile;
    min-height: 0;
    padding: 0;

    .remHeight(2.2);
    .remMarginLeft(4.7);
    .remMarginRight(1);
    .remWidth(@hpbxWidthIdentifierTextfield);

    &:focus {
        background-color: transparent;
        color: @hpbxColorIdentifierTextfield;
    }    
}

.hpbx-device-info {
    input[type="text"].hpbx-device-footer-input-incognito {
        color: @sgColorGrey6;
        
        .remFontSize(1.2);
        .remHeight(1.9);
        .remMarginLeft(0.5);
        .remMarginRight(1);
        .remPaddingLeft(1);
        .remPaddingRight(1);

        &:focus {
            background-color: @sgColorGrey6;
            color: @sgColorGrey2;
        }    
    }
}

.hpbx-device-identifier-edit-icon {
    color: @sgColorGrey3;
    cursor: pointer;
}

.hpbx-device-info {
    .hpbx-device-identifier-edit-icon {
        color: @sgColorGrey6;
    }
}

.hpbx-device-subscriber-table {
    background-color: @sgColorWhite;
    border-collapse: collapse;
    border-left: 1px solid @sgColorGrey5;
    border-right: 1px solid @sgColorGrey5;
    width: 100%;

    .remMarginBottom(8);

    tbody {
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
    }
}

.hpbx-device-key-subscriber-num {
    border-top: 1px solid @sgColorGrey5;
    color: @sgColorGrey3;
    text-align: right;
    vertical-align: top;

    .remFontSize(1.2);
    .remLineHeight(1.2);
    .remMinWidth(3.4);
    .remPaddingTop(1.6);
    .remWidth(3.4);
}

.hpbx-device-key-subscriber-data {
    border-top: 1px solid @sgColorGrey5;
    width: 100%;

    .remPaddingLeft(1.6);
}

.hpbx-device-key-subscriber-name {
    color: @hpbxColorPrimary;

    .remLineHeight(1.6);
    .remMarginTop(1.2);
    .remFontSize(1.6);
}

.hpbx-device-key-subscriber-number {
    color: @sgColorGrey3;

    .remFontSize(1.4);
    .remLineHeight(1.4);
    .remMarginTop(0.7);
    .remMarginBottom(0.6);
    .remMinHeight(2);
}

.hpbx-device-key-subscriber-actions {
    border-top: 1px solid @sgColorGrey5;
    vertical-align: top;

    .remWidth(8.6);
    .remPaddingTop(1.2);
    .remPaddingRight(1.8);
    .remMinWidth(8.6);
}

.hpbx-device-key-subscriber {
    &:last-child {
        .hpbx-device-key-subscriber-actions,
        .hpbx-device-key-subscriber-data,
        .hpbx-device-key-subscriber-num {
            border-bottom: 1px solid @sgColorGrey5;
        }
    }
}

.hpbx-device-capability {
    color: @hpbxColorPrimary;
    float: right;
    display: block;

    .remFontSize(1.6);
    .remWidth(1.6);
    .remHeight(1.6);
    .remMarginRight(0.3);
    .sg-lgi-icon-font();

    &.hpbx-device-capability-blf:before {
        content: "\e62e";
        display: block;

        .remMarginTop(-0.3);
    }

    &.hpbx-device-capability-private:before {
        content: "\e62f";
        display: block;

        .remFontSize(1.3);
    }

    &.hpbx-device-capability-shared:before {
        content: "\e630";
        display: block;

        .remMarginTop(-0.2);
    }

    &.hpbx-device-capability-inactive {
        color: @sgColorGrey3;
    }
}

.hpbx-device-delete {
    color: @sgColorGrey2;
    display: none;
    float: right;
    text-decoration: none;

    .remFontSize(1.6);
    .remMarginTop(0.5);
    .remMarginRight(-0.1);
    .sg-lgi-icon-font();

    .hpbx-device-delete-img:before {
        content: "\e622";
    }
}

.hpbx-device-key-subscriber {
    cursor: pointer;

    &.active {
        background-color: @sgColorGrey6;
    }

    &:hover {
        background-color: @sgColorGrey6;

        .hpbx-device-capability {
            display: none;
        }

        .hpbx-device-delete {
            display: block;
        }

        &.hpbx-device-key-unassigned {
            .hpbx-device-capability {
                display: block;
            }
        }
    }
}


.hpbx-device-key-unassigned {
    background-color: @sgColorGrey7;

    .hpbx-device-key-subscriber-name {
        color: @sgColorGrey3;
        font-style: italic;
        text-transform: lowercase;
    }

    .hpbx-device-capability {
        color: @sgColorGrey3;
    }

    &:hover {
        background-color: @sgColorGrey6;

        .hpbx-device-key-subscriber-name,
        .hpbx-device-key-subscriber-num,
        .hpbx-device-capability {
            color: @sgColorGrey1;
        }

        .hpbx-device-delete {
            display: none;
        }
    }
}

.hpbx-device-xs() {
    .hpbx-device-footer {
        .remPaddingLeft(2);
        .remPaddingRight(7);
    }
}

@media @XS {
    .hpbx-device-xs();
}

.hpbx-device-sm() {
    .hpbx-device-model-image-wrapper {
        background-size: auto;
    }

    .hpbx-device-info {
        bottom: 0;
        left: 0;
        position: fixed;
        right: 0;

        .remPadding(2,2,2,2);
    }
}

@media @SM {
    .hpbx-device-sm();
}

.hpbx-device-md() {
    body.page-hpbx-pbxdevices {
        .hpbx-menu-over-menu {
            background-color: @sgColorWhite;
            position: fixed;
            width: 250px;

            .box-shadow(-2px 0px 10px 0px rgba(0, 0, 0, 0.1));
        }

        &.hpbx-menu-in-page-flow {
            .hpbx-menu-over-menu {
                position: absolute;
            }
        }
    }

    .page-hpbx-pbxdevices {
        .hpbx-menu-handle {
            display: none;
            position: fixed;

            .remTop(2.8);
        }

        .container {
            .transform(none);
        }

        &.hpbx-menu-opened {
            .hpbx-menu-handle {
                position: inherit;

                .remTop(0);
            }
        }
    }

    .hpbx-device-keys {
        display: block;
    }

    .hpbx-device-subscriber-table {
        .remMarginTop(7.2);
        .remMarginBottom(12.2);
    }

    .hpbx-device-profile-label,
    .hpbx-device-profile,
    .hpbx-device-identifier-label,
    .hpbx-device-identifier {
        color: @sgColorGrey6;
    }

    .hpbx-device-header {
        border-left: 1px solid @sgColorGrey5;
        display: block;
        position: fixed;
        right: 0;
        top: 90px;
        width: 250px;
        z-index: 2;
    }

    .hpbx-device-footer {
        left: auto;
        right: 0;
        width: 250px;

        .remPaddingRight(2);

        .hpbx-tab-form-link-button {
            width: 100%;

            .remMarginLeft(0);
            .remMarginTop(1);
            .remMarginBottom(1);
        }

        .sg-btn {
            width: 100%;
        }
    }

    .hpbx-device-body {
        background-color:  @sgColorWhite;
        position: fixed;
        width: 250px;

        .remBottom(8);
        .remTop(7.2);
    }

    .hpbx-device-subscriber-table {
        border-right: 0;
    }
    
    .hpbx-device-profile-label,
    .hpbx-device-identifier-label {
        .remMarginLeft(6);

    }

    .hpbx-device-profile,
    .hpbx-device-identifier {
        .remMarginLeft(1.3);
    }
}

@media @MD {
    .hpbx-device-md();
}

.ie8 {
    .hpbx-device-sm();
    .hpbx-device-md();
}