.hpbx-initialize-variables-global(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxBottomPageTitleIcon: 0.9;
    @hpbxColorPrimary:        @sgColorBlue;
    @hpbxColorGlobalTitle:    @sgColorPurple1;
}

.hpbx-initialize-variables-global(@styleguide) when (@styleguide = 'unitymedia') {
    @hpbxBottomPageTitleIcon: 0.9;
    @hpbxColorPrimary:        @sgColorBlue;
    @hpbxColorGlobalTitle:    @sgColorBlue;
}

.hpbx-initialize-variables-global(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxBottomPageTitleIcon: 0.6;
    @hpbxColorPrimary:      @sgColorOrange;
    @hpbxColorGlobalTitle:   @hpbxColorPrimary;
}

.hpbx-initialize-variables-global(@styleguide) when (@styleguide = 'virgin') {
    @hpbxBottomPageTitleIcon: 1;
    @hpbxColorPrimary:      @sgColorRed;
    @hpbxColorGlobalTitle:   @hpbxColorPrimary;
}

.hpbx-initialize-variables-global(@styleguide) when (@styleguide = 'vtr') {
    @hpbxBottomPageTitleIcon: 0.6;
    @hpbxColorPrimary:      @sgColorPink;
    @hpbxColorGlobalTitle:   @hpbxColorPrimary;
}

.hpbx-initialize-variables-global(@hpbxStyleguide);



.hpbx-page-title {
    color: @hpbxColorGlobalTitle;
    position: relative;

    .sg-icon {
        position: absolute;
        margin-top: 0.4rem;
        .remFontSize(3.2);
        .remPadding(0, 0, 0, 0.2);
    }

    .sg-header-2 {
        .remFontSize(3);
        .remPadding(0.3, 0 ,0, 4.2);
    }

    .hpbx-page-title-link {
        color: @sgColorGrey2;
        float: right;
        font-family: @sgFontFamilyDefault;

        .remFontSize(1.4);
        .remLineHeight(5);
    }
}

.hpbx-page-subtitle {
    color: @hpbxColorGlobalTitle;
    font-family: @sgFontFamilyHeadings;

    .remFontSize(@sgFontSizeH4);
    .remMarginTop(0.2);
    .remPadding(0.3, 0 ,0, 4.4);
}

.hpbx-main-content-inner {
    background-color: @sgColorWhite;
    border: 1px solid @sgColorGrey4;
    border-spacing: 0;

    .box-shadow(0 5px 10px rgba(204,208,218,.5));
    .remBorderRadiusAll(0.4);
    .remMarginTop(3);

    &> form {
        .remPadding(0);
    }

    .hpbx-tab-form-label,
    .hpbx-tab-form-field,
    .hpbx-tab-form-explanation {
        .remPaddingLeft(1);
        .remPaddingRight(1);
    }
}

.hpbx-main-content-buttons-row {
    .clearfix();
    .remMarginBottom(2.9);
    .remMarginTop(1.8);

    input {
        .remMarginLeft(1);
    }
}

.hpbx-always-hidden {
    display: none !important;
}

.hpbx-always-visible {
    display: block !important;
}

.hpbx-hidden-xxs {
    @media @XXS_MAX {
        .responsive-invisibility();
    }
}

.hpbx-visible-xxs-inline {
    display: none !important;

    @media @XXS_MAX {
        display: inline !important;
    }
}

.hpbx-contact-item {
    color: @hpbxColorPrimary;
    position: relative;
    
    .remMarginBottom(1.4);
    .remPaddingLeft(2.5);

    a {
        color: @hpbxColorPrimary;
    }
}

.hpbx-contact-item-icon {
    //color: @hpbxColorPrimary;
    color: @sgColorGrey2;
    position: absolute;

    .remFontSize(1.6);
    .remLeft(0.2);
    .remTop(0.3);
}

.hpbx-search-input-container {
    position: relative;

    &:hover {
        .hpbx-search-input-icon {
            color: @hpbxColorPrimary;
            cursor: pointer;
        }
    }
}

input[type="text"].hpbx-search-input {
    background-color: @sgColorGrey7;
    color: @sgColorGrey3;

    .placeholder(@sgColorGrey3);
    .remPaddingRight(3);

    &:focus,
    &.sg-completed {
        background-color: @hpbxColorPrimary;
        border-color: @hpbxColorPrimary !important;
        color: @sgColorWhite;

        .placeholder(@sgColorGrey5);

        & + .hpbx-search-input-icon {
            color: @sgColorWhite;
        }
    }

    &:hover {
        border-color: @hpbxColorPrimary;

        .placeholder(@hpbxColorPrimary);
    }
}

.hpbx-search-input-icon {
    color: @sgColorGrey3;
    position: absolute;
    text-align: center;

    .remFontSize(1.6);
    .remHeight(4);
    .remLineHeight(4);
    .remRight(0);
    .remTop(0);
    .remWidth(4);

    .transition(@sgMiscDurationTransitionDefault);
}

.hpbx-help-article-media {
    border: 1px solid @sgColorGrey4;

    .remHeight(24.3);
}

.hpbx-help-article-image {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
}

.hpbx-help-horizontal-line {
    border-top: 1px solid @sgColorGrey4;
    height: 1px;

    .box-sizing(border-box);

    &.hpbx-dashed {
        border-top-style: dashed;
    }
}

.hpbx-help-footer {
    .remMarginBottom(3);
}

.hpbx-help-footer-content {
    color: @sgColorGrey3;
    font-family: @sgFontFamilyHeadings;
    position: relative;
    text-align: right;

    .remFontSize(@sgFontSizeH6);
    .remPaddingRight(2);
    .remPaddingTop(2.4);
}

.hpbx-help-footer-link {
    color: @sgColorGrey3;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}

.hpbx-help-footer-icon-right {
    position: absolute;
    right: 0;

    .remFontSize(1.6);
    .remTop(2.9);
}

.hpbx-ie8 {
    display: none;
}

.hpbx-visible-xxs {
    display: none;
}

.hpbx-visible-xs {
    display: none;
}

.hpbx-visible-sm {
    display: none;
}

.hpbx-visible-md {
    display: none;
}

.hpbx-visible-lg {
    display: none;
}

.hpbx-global-xxs() {
    .hpbx-help-header-title {
        margin-top:1rem !important;
    }
    .hpbx-visible-xxs {
        display: block;
    }

    .hpbx-hidden-xxs {
        display: none;
    }

    .hpbx-help-article-media {
        .remHeight(20);
    }
}

@media @XXS_MAX {
    .hpbx-global-xxs();
}

.hpbx-global-xs() {
    // added for help & faq section
    .hpbx-help-header-title {
        margin-top:1.8rem !important;
    }

    .hpbx-visible-xs {
        display: block;
    }

    .hpbx-hidden-xs {
        display: none;
    }

    .hpbx-page-title {
        .remMarginTop(6);

        .sg-icon {
            .remFontSize(3.6);
            .remPadding(0, 0, 0, 0);
        }
        .sg-header-2 {
            .remFontSize(4);
            .remPadding(0.0, 0 ,0, 4.5);
        }
    }
}

@media @XS {
    .hpbx-global-xs;
}

.hpbx-global-sm() {
    // added for help & faq section
     .hpbx-help-header-title {
        margin-top:1.5rem !important;
    }
    
    .hpbx-visible-sm {
        display: block;
    }

    .hpbx-hidden-sm {
        display: none;
    }

    .hpbx-page-title {
        .remMarginTop(0);
    }
}

@media @SM {
    .hpbx-global-sm();
}

.hpbx-global-md() {
    .hpbx-help-header-title {
        margin-top:1.5rem !important;
    }
    .hpbx-visible-md {
        display: block;
    }

    .hpbx-hidden-md {
        display: none;
    }
}

@media @MD {
    .hpbx-global-md();
}

.hpbx-global-lg() {
    .hpbx-help-header-title {
        margin-top:1.5rem !important;
    }
    .hpbx-visible-lg {
        display: block;
    }

    .hpbx-hidden-lg {
        display: none;
    }
}

@media @LG {
    .hpbx-global-lg();
}


.ie8 {
    .hpbx-global-xs();
    .hpbx-global-sm();

    .hpbx-ie8 {
        display: block;
    }
}