.hpbx-initialize-variables-timeslots(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorSlot:                  @sgColorGrey3;
    @hpbxColorSlotHover:             @sgColorGrey3b;
    @hpbxColorSlotLabelFont:         'BloomSpeakWeb-Bold';

    @hpbxTopSlotLabel:               1;
}

.hpbx-initialize-variables-timeslots(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-timeslots('upc-business');

    @hpbxColorSlot:                  @sgColorGrey2;
    @hpbxColorSlotHover:             @sgColorGrey3;
}

.hpbx-initialize-variables-timeslots(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorSlot:                  @sgColorGrey3;
    @hpbxColorSlotHover:             @sgColorGrey2;
    @hpbxColorSlotLabelFont:         'MuseoSans700';

    @hpbxTopSlotLabel:               0.9;
}

.hpbx-initialize-variables-timeslots(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-timeslots('ziggo');

    @hpbxColorSlotLabelFont:         'CircularChatBook';
}

.hpbx-initialize-variables-timeslots(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-timeslots('ziggo');
    @hpbxColorSlotLabelFont:         @sgFontFamilyDefault;
}

.hpbx-initialize-variables-timeslots(@hpbxStyleguide);

.hpbx-timeslots {
    background-color: @sgColorGrey7;
    border: 1px solid @sgColorGrey5;
    position: relative;
    width: 100%;

    .remBorderRadius(0.4,0.4,0.4,0.4);
    .remHeight(3.6);

    .hpbx-timeslot {
        background-color: @hpbxColorSlot;
        cursor: pointer;
        overflow: hidden;
        position: absolute;
        text-align: center;
        top: -1px;

        .remBorderRadius(0.4,0.4,0.4,0.4);
        .remHeight(3.6);

        &:before {
            color: @sgColorWhite;
            content: attr(data-label);
            font-family: @hpbxColorSlotLabelFont;
            left: 0;
            overflow: hidden;
            position: absolute;
            width: 100%;

            .remFontSize(1.2);
            .remTop(@hpbxTopSlotLabel);
        }

        &:hover {
            background-color: @hpbxColorSlotHover;
            &:before {
                content: attr(data-label-edit);
                .remFontSize(1.4);
            }
        }
    }

}

.hpbx-timeslot-error {
    padding-left: 0;
    padding-top: 0;
    text-align: left;

    .remPaddingBottom(2);
}
