.hpbx-initialize-variables-tabs(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorTabText:                  @sgColorPurple1;
    @hpbxColorTabActiveBackground:      @sgColorPurple1;
    @hpbxColorTabActiveBackgroundHover: @sgColorPurple2;
    @hpbxColorTabCreateBarTextHover:    @sgColorBlue;
    @hpbxFontSizeTab:                   'BloomSpeakWeb-Bold';
    @hpbxPaddingTopTabCreateBarText:    1.3;
}

.hpbx-initialize-variables-tabs(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-tabs('upc-business');
    @hpbxColorTabText:                  @sgColorBlue;
    @hpbxColorTabActiveBackground:      @sgColorBlue;
    @hpbxColorTabActiveBackgroundHover: @sgColorBlue2;
    @hpbxColorTabCreateBarTextHover:    @sgColorBlue2;
}

.hpbx-initialize-variables-tabs(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorTabText:                  @hpbxColorPrimary;
    @hpbxColorTabActiveBackground:      @hpbxColorPrimary;
    @hpbxColorTabActiveBackgroundHover: @sgColorOrangeExtra;
    @hpbxColorTabCreateBarTextHover:    @sgColorOrange;
    @hpbxFontSizeTab:                   'MuseoSans700';
    @hpbxPaddingTopTabCreateBarText:    1.5;
}

.hpbx-initialize-variables-tabs(@styleguide) when (@styleguide = 'virgin') {
    @hpbxColorTabText:                  @hpbxColorPrimary;
    @hpbxColorTabActiveBackground:      @hpbxColorPrimary;
    @hpbxColorTabActiveBackgroundHover: @sgColorRedHover;
    @hpbxColorTabCreateBarTextHover:    @sgColorPurple;
    @hpbxFontSizeTab:                   'CircularChatBook';
    @hpbxPaddingTopTabCreateBarText:    1.5;
}

.hpbx-initialize-variables-tabs(@styleguide) when (@styleguide = 'vtr') {
    @hpbxColorTabText:                  @hpbxColorPrimary;
    @hpbxColorTabActiveBackground:      @hpbxColorPrimary;
    @hpbxColorTabActiveBackgroundHover: @sgColorPinkExtra;
    @hpbxColorTabCreateBarTextHover:    @sgColorPink;
    @hpbxFontSizeTab:                   'NeoSans700';
    @hpbxFontSizeTab1:                  'NeoSansLight';
    @hpbxPaddingTopTabCreateBarText:    1.5;
}

.hpbx-initialize-variables-tabs(@hpbxStyleguide);


.hpbx-tabs {
    .remMarginTop(3);

    > div:nth-child(2) {
        .remBorderTopRadius(0.4);

        &.r-tabs-state-active {
            border-top: 1px solid @hpbxColorTabActiveBackground;
        }
    }

    > div:nth-last-child(2) {
        border-bottom: 1px solid @sgColorGrey4;

        .remBorderBottomRadius(0.4);

        &.r-tabs-state-active {
            border-bottom: 0;
        }
    }


    > div:last-child {
        .remBorderBottomRadius(0.4);

        &.r-tabs-state-active {
            border-bottom: 1px solid @sgColorGrey4;
        }
    }

    .r-tabs-nav {
        display: none;

        .remPadding(0, 0, 0, 0);
        .remMargin(0, 0, 0, 0);
    }

    .r-tabs-accordion-title {
        background-color: @sgColorGrey7;
        border-left: 1px solid @sgColorGrey4;
        border-right: 1px solid @sgColorGrey4;
        border-top: 1px solid @sgColorGrey4;
        display: block;

        &:hover {
            background-color: @sgColorGrey6;
        }

        .r-tabs-anchor {

            color: @hpbxColorTabText;
            display: block;
            font-family: @hpbxFontSizeTab;
            text-decoration: none;

            .remFontSize(1.8);
            .remLineHeight(5);
            .remPadding(0, 2, 0, 2);

            &:after {
                content: "\e62a";
                float: right;

                .remFontSize(1.6);
                .sg-lgi-icon-font();
            }
        }

        &.r-tabs-state-active {
            background-color: @hpbxColorTabActiveBackground;
            border-left: 1px solid @hpbxColorTabActiveBackground;
            border-right: 1px solid @hpbxColorTabActiveBackground;
            border-top: 1px solid @hpbxColorTabActiveBackground;

            .remBorderBottomRadius(0);

            .r-tabs-anchor {
                color: @sgColorWhite;

                &:after {
                    content: "\e62c";
                }
            }
        }
    }

    .hpbx-tab-title {
        .remMarginLeft(2);
    }

    .r-tabs-tab {
        display: inline-block;
        list-style: none;

        .remMargin(0, 0, 0, 0);
    }

    .r-tabs-panel {
        background-color: @sgColorWhite;
        border-left: 1px solid @sgColorGrey4;
        border-right: 1px solid @sgColorGrey4;
        display: none;

        .remPadding(0, 0, 0, 0);

        &.r-tabs-state-active {
            border-top: none;
            display: block;
        }

        .hpbx-foldable-list-container {
            border: none;
            margin-top: 0;

            .remBorderRadius(0, 0, 0.4, 0.4);
        }

        .hpbx-table {
            border: none;
            margin-top: 0;
            overflow: visible;
        }
    }
}

.hpbx-tabs-create-bar {
    background-color: @sgColorWhite;
    border-bottom: 1px solid @sgColorGrey5;
    display: block;
    text-decoration: none;

    .remHeight(5);

    &:hover {
        .sg-icon {
            color: @hpbxColorTabCreateBarTextHover;
        }

        .sg-header-5 {
            color: @hpbxColorTabCreateBarTextHover;            
        }
    }

    .sg-icon {
        color: @sgColorGrey3;
        float: left;

        .remPadding(1.8,1.3,1.5,2);
    }

    .sg-header-5 {
        color: @sgColorGrey3;
        margin-top: 0;

        .remPaddingTop(@hpbxPaddingTopTabCreateBarText);
    }
}

.hpbx-tabs-xs() {
    .hpbx-tabs {
        .r-tabs-nav {
            display: table;
            table-layout: fixed;
            width: 100%;
        }

        .r-tabs-nav li, // Fallback
        .hpbx-tab-item {
            background-color: @sgColorGrey7;
            border-bottom: 1px solid @sgColorGrey4;
            border-left: 1px solid @sgColorGrey4;
            border-top: 1px solid @sgColorGrey4;
            display: table-cell;
            vertical-align: middle;

            .remHeight(5);
            .remPadding(0, 0, 0, 0);

            .r-tabs-anchor {
                color: @hpbxColorTabText;
                display: block;
                font-family: @hpbxFontSizeTab;
                text-align: center;
                overflow: hidden;
                text-decoration: none;
                text-overflow: ellipsis;
                white-space: nowrap;

                .remFontSize(1.8);
                .remLineHeight(1.8);
                .remPadding(1.5, 2, 1.5, 2);

                .transition(0.3s ease);
                &:hover {
                    color: @sgColorGrey1;
                }
            }


            &:hover {
                background-color: @sgColorGrey6;
            }

            &:first-child {
                .remBorderRadius(0.4, 0, 0, 0);

                .hpbx-tab-item-background {
                    .remBorderRadius(0.4, 0, 0, 0);
                }                
            }

            &:last-child {
                border-right: 1px solid @sgColorGrey4;

                .remBorderRadius(0, 0.4, 0, 0);

                .hpbx-tab-item-background {
                    .remBorderRadius(0, 0.4, 0, 0);
                }                
            }

            &:before {
                content: "";
            }

            &.r-tabs-state-active {
                .r-tabs-anchor {
                    color: @sgColorWhite;

                    .transition(0.3s ease 0.3s);
                    &:hover {
                        color: @sgColorWhite;
                    }
                }
                
                .hpbx-tab-item-background {
                    background-color: @hpbxColorTabActiveBackground;

                    .remHeight(5);
                    .transition-multiple(background-color 0.3s ease 0.3s, height 0.3s cubic-bezier(0.7,0,0.3,1) 0.3s;);
                }
            }
        }

        .hpbx-tab-item-title {
            position: relative;
            z-index: 2;
        }

        .hpbx-tab-item-background {
            background-color: @sgColorGrey7;
            bottom: -1px;
            height: 0;
            left: -1px;
            position: absolute;
            width: 100%;
            width: ~"calc(100% + 1px)";
            z-index: 1;

            .transition-multiple(background-color 0.3s ease, height 0.3s cubic-bezier(0.7,0,0.3,1););
        }

        .hpbx-tab-icon {
            .remFontSize(2.4);
        }

        .hpbx-tab-title {
            display: none;
        }

        .r-tabs-panel {
            border-bottom: 1px solid @sgColorGrey4;

            .remBorderBottomRadius(0.4);

            .hpbx-table {
                overflow: hidden;
            }
        }

        .r-tabs-accordion-title {
            display: none;
        }
    }
}


@media @XS {
    .hpbx-tabs-xs;
}

.ie8 {
    .hpbx-tabs-xs;

    .hpbx-tabs {
        .r-tabs-nav {
            border-right: 1px solid @sgColorGrey4;
        }
    }
}