.hpbx-help-faq-foldable-list {
    .remFontSize(@sgFontSizeText);
    
    .hpbx-foldable-heading {
        background-color: transparent;
        border: none !important;

        &.hpbx-foldable-state-open {
            .hpbx-foldable-heading-title {
                color: @sgColorH4;
            }

            .hpbx-foldable-content {
                .remPaddingBottom(1.7) !important;
            }
        }
    }

    .hpbx-foldable-heading-title {
        color: @sgColorGrey3;
        font-family: @sgFontFamilyHeadings;
        line-height: 1.25;
        padding: 0;

        .remFontSize(@sgFontSizeH4);
    }

    .hpbx-foldable-content {
        padding-top: 0 !important;

        .sg-text-intro {
            .remMarginTop(1.1);
        }
    }

    .hpbx-foldable-footer {
        border: none !important;
        cursor: pointer;
        margin: 0;
        position: relative;

        .remPaddingBottom(1.2);
        .remPaddingRight(2);
        .remPaddingTop(1.1);

        &.hpbx-foldable-state-open {
            .hpbx-foldable-footer-icon {
                &:before {
                    color: @sgColorH4;
                    content: '\e620';

                    & when not(@hpbxStyleguide = 'upc-business') and not(@hpbxStyleguide = 'unitymedia') {
                        content: '\e621';
                    }
                }
            }
        }
    }

    .hpbx-foldable-footer-line {
        background-color: @sgColorGrey4;
        height: 1px;
    }

    .hpbx-foldable-footer-icon {
        font-family: 'LGI-Iconfont';
        position: absolute;
        right: 0;
        top: 0;

        .remFontSize(1.5);
        .remHeight(1.5);
        .remWidth(1.5);

        &:before {
            .sg-lgi-icon-font();

            color: @sgColorGrey4;
            content: '\e61e';

            & when not(@hpbxStyleguide = 'upc-business') and not(@hpbxStyleguide = 'unitymedia') {
                content: '\e61f';
            }
        }
    }
}

.hpbx-faq-xxs() {
    .hpbx-help-faq-foldable-list {
        .remMarginTop(2.5);
    }
}

@media @XXS_MAX {
    .hpbx-faq-xxs();
}
