.hpbx-initialize-variables-popup(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorPopupError: @sgColorRed;
}

.hpbx-initialize-variables-popup(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-popup('upc-business');

}

.hpbx-initialize-variables-popup(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorPopupError: @sgColorSignal1;
}

.hpbx-initialize-variables-popup(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-popup('ziggo');
}

.hpbx-initialize-variables-popup(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-popup('ziggo');
}

.hpbx-initialize-variables-popup(@hpbxStyleguide);

.mfp-bg {
    background-color: @sgColorWhite;
    .opacity(1);
}

.mfp-wrap {
    cursor: default;
    position: fixed !important;
}

.mfp-container {
    padding: 0;
}

.mfp-content {
    position: static;
}

img.mfp-img {
    padding: 0;
}

.hpbx-popup {
  background: @sgColorWhite;
  margin: 20px auto;
  max-width: 500px;
  padding: 15px;
  position: static !important;
  width: auto;
}

.hpbx-popup-title {
    margin-top: 0;
}

.hpbx-popup-form-row {
    .clearfix();
    .remMarginTop(2.7);
}

.hpbx-popup-form-item-left {
    float: left;
}

.hpbx-popup-form-item-right {
    float: right;
}

.hpbx-popup-form-label {
    color: @sgColorGrey2;
    font-weight: bold;

    .remFontSize(@sgFontSizeTextIntro);
    .remMarginBottom(0.4);
}
.hpbx-popup-form-field {
    .remWidth(14);
}

.hpbx-popup-form-days-container {
    background-color: @sgColorGrey7;
    border: 1px solid @sgColorGrey5;
    display: table;
    width: 100%;

    .remBorderRadiusAll(@sgMiscBorderRadiusDefault);
    .remHeight(@sgMiscHeightElements);

    &.sg-error {
        border-color: @hpbxColorPopupError;
    }
}

.hpbx-day-checkbox {
    border-right: 1px solid @sgColorGrey7;
    display: table-cell;
    position: relative;

    label {
        color: sgColorGrey2;
        font-family: @sgFontFamilyHeadings;
        left: 0;
        position: absolute;
        text-align: center;
        text-transform: uppercase;
        top: 0;
        width: 100%;

        .remFontSize(@sgFontSizeCta);
        .remHeight(3.6);
        .remLineHeight(@sgMiscHeightElements);

        &.hpbx-is-checked {
            background-color: @sgColorGrey3;
            color: @sgColorWhite;

            .remBorderRadiusAll(@sgMiscBorderRadiusDefault);
        }
    }

    &:last-child {
        border-right: none;
    }

    &:hover {
        label {
            background-color: @sgColorGrey5;
        
            .remBorderRadiusAll(@sgMiscBorderRadiusDefault);
        }
    }

    input {
        outline: none;

        .opacity(0);

        &:checked + label {
            background-color: @sgColorGrey3;
            color: @sgColorWhite;
        
            .remBorderRadiusAll(@sgMiscBorderRadiusDefault);
        }
    }
}

.hpbx-popup-form-add-rule {
    .remMarginTop(1.8);
}

.hpbx-popup-form-buttons-row {
    text-align: right;

    .remMarginTop(2.2);
}

.hpbx-popup-form-link-button {
    font-family: @sgFontFamilyHeadings;
    color: @sgColorGrey2;

    .remFontSize(1.8);
    .remMarginRight(1.9);
}

@media @SM {
    .hpbx-popup-sm();
}

.hpbx-popup-sm() {
    .mfp-bg {
        background-color: #000;

        .opacity(0.5);
    }

    .mfp-container {
        padding: 0;
    }

    .mfp-content {
        position: relative;
    }

    .hpbx-popup {
        border: 1px solid @sgColorGrey4;
        padding: 36px 40px 41px 40px;
        position: relative !important;

        .remBorderRadiusAll(0.5);
    }

    .hpbx-popup,
    .hpbx-popup-root{
        .mfp-close {
            color: @sgColorWhite;
            cursor: pointer !important;
            padding-right: 0;
            right: -22px;
            text-align: center;
            top: -22px;
            width: 44px;

            .opacity(1);

            &:before {
                background-color: #000;
                color: @sgColorWhite;
                content: "\e623";
                display: block;
                font-size: 7px;
                height: 20px;
                line-height: 20px;
                position: absolute;
                right: 11px;
                top: 11px;
                width: 20px;

                .remBorderRadius(10, 10, 10, 0);
                .sg-lgi-icon-font();
            }
        }
    }
}

.ie8 {
    .hpbx-popup-sm();

    .hpbx-popup,
    .hpbx-popup-root{
        .mfp-close {
            color: #000;

            &:before {
                display: none;
            }
        }
    }
}