.ep-header {
    background-color: @sgColorWhite;

	.remHeight(8);

	.ep-header-logo-container {
		.remMarginLeft(0.6);
	}
}

.ep-styleguide-color-block {
	color: @sgColorWhite;

    &.ep-pink { background-color: @sgColorPink;                                                 .ep-code:after { content: "@{sgColorPink}"; } }
    &.ep-pink-extra { background-color: @sgColorPinkExtra;                                      .ep-code:after { content: "@{sgColorPinkExtra}"; } }

    &.ep-purple { background-color: @sgColorPurple;                                             .ep-code:after { content: "@{sgColorPurple}"; } }
    &.ep-purple-extra { background-color: @sgColorPurpleExtra;                                  .ep-code:after { content: "@{sgColorPurpleExtra}"; } }

    &.ep-signal-1 { background-color: @sgColorSignal1;                                          .ep-code:after { content: "@{sgColorSignal1}"; } }
    &.ep-signal-2 { background-color: @sgColorSignal2;                                          .ep-code:after { content: "@{sgColorSignal2}"; } }

    &.ep-grey-1 { background-color: @sgColorGrey1;                                              .ep-code:after { content: "@{sgColorGrey1}"; } }
    &.ep-grey-2 { background-color: @sgColorGrey2;                                              .ep-code:after { content: "@{sgColorGrey2}"; } }

    &.ep-grey-3 { background-color: @sgColorGrey3;                                              .ep-code:after { content: "@{sgColorGrey3}"; } }
    &.ep-grey-4 { background-color: @sgColorGrey4;                                              .ep-code:after { content: "@{sgColorGrey4}"; } }
    
    &.ep-grey-5 { background-color: @sgColorGrey5; color: @sgColorGrey1;                        .ep-code:after { content: "@{sgColorGrey5}"; } }
    &.ep-grey-6 { background-color: @sgColorGrey6; color: @sgColorGrey1;                        .ep-code:after { content: "@{sgColorGrey6}"; } }
    
    &.ep-grey-7 { background-color: @sgColorGrey7; color: @sgColorGrey1;                        .ep-code:after { content: "@{sgColorGrey7}"; } }
    &.ep-white { background-color: @sgColorWhite; color: @sgColorGrey1;                         .ep-code:after { content: "@{sgColorWhite}"; } }
}

.ep-typography-legend {
	.remMarginTop(5.2);
}

.ep-with-margin-annotation:before,
.ep-with-margin-annotation:after,
.ep-show-margin-annotation-left:before,
.ep-show-margin-annotation-left:after {
	color: @sgColorSignal2 !important;
	border-color: @sgColorSignal2 !important;
}

.ep-styleguide-button-row {
	.sg-btn {
		.remMarginBottom(1.6);
	}
}

h5 {
    &.ep-with-margin-annotation {
        .ep-show-margin-annotation-left(@sgMarginTopH5, @sgPaddingLeftRightGridItemTextContent);
    }
}

h6 {
    &.ep-with-margin-annotation {
        .ep-show-margin-annotation-left(@sgMarginTopH6, @sgPaddingLeftRightGridItemTextContent);
    }
}

.ep-typography-font-family-header-5:after { content: "@{sgFontFamilyHeadings}"; }
.ep-typography-font-size-header-5:after {   content: "@{sgFontSizeH5} rem"; }
.ep-typography-color-code-header-5:after {  content: "@{sgColorH5}"; }
.ep-typography-line-height-header-5:after { @actualHeight: round(@sgFontSizeH5 * @sgLineHeightH5, 1); content: "@{sgLineHeightH5} (@{actualHeight} rem)"; }
.ep-typography-margin-top-header-5:after {  content: "@{sgMarginTopH5} rem"; }

.ep-typography-font-family-header-6:after { content: "@{sgFontFamilyHeadings}"; }
.ep-typography-font-size-header-6:after {   content: "@{sgFontSizeH6} rem"; }
.ep-typography-color-code-header-6:after {  content: "@{sgColorH6}"; }
.ep-typography-line-height-header-6:after { @actualHeight: round(@sgFontSizeH6 * @sgLineHeightH6, 1); content: "@{sgLineHeightH6} (@{actualHeight} rem)"; }
.ep-typography-margin-top-header-6:after {  content: "@{sgMarginTopH6} rem"; }

.ep-typography-font-family-text-small:after { content: "@{sgFontFamilyTextSmall}"; }
.ep-typography-font-size-text-small:after {   content: "@{sgFontSizeTextSmall} rem"; }
.ep-typography-color-code-text-small:after {  content: "@{sgColorTextSmall}"; }
.ep-typography-line-height-text-small:after { @actualHeight: round(@sgFontSizeTextSmall * @sgLineHeightTextSmall, 1); content: "@{sgLineHeightTextSmall} (@{actualHeight} rem)"; }
.ep-typography-margin-top-text-small:after {  content: "@{sgMarginTopParagraph} rem"; }

