body.hpbx-menu-opened {
    .hpbx-menu-over-menu {
        display: none;
    }
}

.hpbx-menu-over-menu {
    display: block;
    float: none;
    right: -260px;
    z-index: 3002;

    .transition(0.5s ease-out);
}

.hpbx-menu-over-menu-title-container {
    background-color: @sgColorGrey6;
    border-bottom: 1px solid @sgColorGrey5;

    .box-sizing(border-box);
    .remHeight(7.3);
}

.hpbx-menu-over-menu-title {
    font-family: @sgFontFamilyText;
    font-style: italic;

    .remFontSize(@sgFontSizeTextSmall);
    .remPaddingLeft(2);
    .remPaddingRight(4.7);
    .remPaddingTop(1.8);
}

.hpbx-menu-over-menu-menu-handle-open {
    //display: block !important;
    right: 20px !important;
    top: 25px !important;
}

.hpbx-menu-over-menu-xs() {
    .hpbx-menu-over-menu {
        display: none;
    }
}

.hpbx-menu-over-menu-md() {
    body {
        &.hpbx-contains-menu-over-menu {
            &.hpbx-show-menu-over-menu {
                &.hpbx-menu-opened {
                    &:not(.page-hpbx-pbxdevices) {
                        .hpbx-menu-over-menu {
                            right: -260px;
                        }
                    }

                    &.page-hpbx-pbxdevices {
                        .hpbx-menu-over-menu {
                            .opacity(0);
                        }
                    }
                }

                .hpbx-menu-over-menu {
                    right: 0;
                }
            }
        }

        &.hpbx-menu-opened {
            .hpbx-menu {
                display: block;
            }

            .hpbx-menu-over-menu {
                z-index: 0;

                .transition(0.4s ease-in);
            }
        }
    }

    .hpbx-menu-over-menu {
        &.col-xs-12 {
            float: none;
            padding: 0;
        }

        &.hpbx-main-content {
            margin-top: 0;
        }
    }
}

@media @MD {
    .hpbx-menu-over-menu-md();
}

.ie8 {
    .hpbx-menu-over-menu-md();
}