.hpbx-initialize-variables-menu(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxFontMenu: 'BloomSpeakWeb-Bold';
    @hpbxFontSizeMenu: 1.8;
    @hpbxFontSizeMenuItemVoicemailCount: 1;

    @hpbxPaddingBottomMenuItem: 0.9;
    @hpbxPaddingTopMenuItem: 0.9;

    @hpbxLineHeightMenuItem: 1.49;

    @hpbxColorMenuHandleClose: @sgColorGrey3;
    @hpbxColorMenuBackground: @sgColorGrey3;
    @hpbxColorMenuActiveBackground: @hpbxColorPrimary;

    @hpbxColorMenuItemEveryoneContent: @sgColorBlue;
    @hpbxColorMenuItemEveryoneContentHover: @sgColorBlue;

    @hpbxColorMenuItemSubBackground: @sgColorGrey3;
    @hpbxColorMenuItemSubBackgroundHover: @sgColorGrey3b;
    @hpbxColorMenuItemSubLines: @sgColorGrey3b;

    @hpbxColorMenuItemVoicemailCountBackground: @sgColorOrange;
}

.hpbx-initialize-variables-menu(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-menu('upc-business');

    @hpbxColorMenuItemEveryoneContent: @sgColorBlue;
    @hpbxColorMenuItemEveryoneContentHover: @sgColorBlue;

    @hpbxColorMenuBackground: @sgColorGrey2;
    @hpbxColorMenuActiveBackground: @sgColorBlue;

    @hpbxColorMenuItemSubBackground: @sgColorGrey2;
    @hpbxColorMenuItemSubBackgroundHover: @sgColorGrey3;
    @hpbxColorMenuItemSubLines: @sgColorGrey3;
}

.hpbx-initialize-variables-menu(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxFontMenu: 'MuseoSans700';
    @hpbxFontSizeMenu: 1.6;
    @hpbxFontSizeMenuItemVoicemailCount: 0.9;

    @hpbxPaddingBottomMenuItem: 1;
    @hpbxPaddingTopMenuItem: 1;

    @hpbxLineHeightMenuItem: 1.49;

    @hpbxColorMenuHandleClose: @sgColorGrey2;
    @hpbxColorMenuBackground: @sgColorGrey2;
    @hpbxColorMenuActiveBackground: @hpbxColorPrimary;

    @hpbxColorMenuItemEveryoneContent: @sgColorGrey1;
    @hpbxColorMenuItemEveryoneContentHover: @sgColorGrey1;

    @hpbxColorMenuItemSubBackground: @sgColorGrey2;
    @hpbxColorMenuItemSubBackgroundHover: @sgColorGrey3;
    @hpbxColorMenuItemSubLines: @sgColorGrey3;

    @hpbxColorMenuItemVoicemailCountBackground: @sgColorGrey2;
}

.hpbx-initialize-variables-menu(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-menu('ziggo');

    @hpbxFontMenu:              'CircularChatBook';
}

.hpbx-initialize-variables-menu(@styleguide) when (@styleguide = 'vtr') {
    @hpbxFontMenu: 'OpenSans400';

    @hpbxFontSizeMenu: 1.6;
    @hpbxFontSizeMenuItemVoicemailCount: 0.9;

    @hpbxPaddingBottomMenuItem: 1;
    @hpbxPaddingTopMenuItem: 1;

    @hpbxLineHeightMenuItem: 1.49;

    @hpbxColorMenuHandleClose: @sgColorGrey2;
    @hpbxColorMenuBackground: @sgColorGrey2;
    @hpbxColorMenuActiveBackground: @hpbxColorPrimary;

    @hpbxColorMenuItemEveryoneContent: @sgColorPink;
    @hpbxColorMenuItemEveryoneContentHover: @sgColorPink;

    @hpbxColorMenuItemSubBackground: @sgColorGrey2;
    @hpbxColorMenuItemSubBackgroundHover: @sgColorGrey3;
    @hpbxColorMenuItemSubLines: @sgColorGrey3;

    @hpbxColorMenuItemVoicemailCountBackground: @sgColorGrey2;
}

.hpbx-initialize-variables-menu(@hpbxStyleguide);

.hpbx-body-menu-opened-overlay {
    background-color: @sgColorGrey1;
    height: 0;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1104;

    .opacity(0);
    .transition-multiple(opacity 0.7s ease-out, z-index 0.7s ease-out, height 0s ease 0.7s;);
}

body.vrm-android-fix {
    .hpbx-menu,
    .hpbx-menu-handle {
        position: fixed !important;
    }
}

.hpbx-menu {
    background-color: @hpbxColorMenuBackground;
    bottom: 0;
    font-family: @hpbxFontMenu;
    position: fixed;
    right: -250px;
    top: 0;
    width: 0px;
    z-index: 1104;

    .box-shadow(-2px 0px 10px 0px rgba(0, 0, 0, 0.1));
    .transition(0.4s ease-in);
}

body.hpbx-menu-opened {

    .hpbx-body-menu-opened-overlay {
        height: 100%;

        .opacity(0.8);
        .transition-multiple(opacity 0.7s ease-out, z-index 0.7s ease-out, height 0s;);
    }

    .hpbx-menu {
        right: 0;
        width: 250px;

        .transition(0.5s ease-out);
    }

    .hpbx-menu-handle-open {
        .opacity(0);
        .transition(opacity 0.1s);
        &:before {
            color: @sgColorGrey1;
            content: '\e61a';
        }
    }

    .hpbx-menu-item-icon-container {
        left: 0;

        .transition(0.5s ease-out 0.2s);
    }

    .hpbx-submenu-item {
        .hpbx-menu-item-icon-container {
            left: 11px;
        }
    }


    .hpbx-menu-item-title-container,
    .hpbx-menu-item-subtitle-container {
        .remPaddingLeft(4.1);
        .transition(0.5s ease-out 0.25s);
    }

    .hpbx-menu-item-user {
        .hpbx-menu-item-icon-container {
            .remLeft(20);
        }

        .hpbx-menu-item-title-container,
        .hpbx-menu-item-subtitle-container {
            .remPaddingLeft(0.6);
            .remPaddingRight(3.5);
            .transition(0.5s ease-out 0.15s);
        }
    }

    .hpbx-menu-item-handle-container {
        right: 0;

        .transition(0.5s ease-out 0.2s);
    }
}

body.hpbx-menu-in-page-flow {
    overflow-x: hidden;

    .hpbx-menu {
        bottom: auto;
    }

    &.hpbx-menu-opened {
        .hpbx-menu {
            min-height: 100%;
            position: absolute;
        }
    }
}

.hpbx-menu-in-page-flow-full-height {
    height: 100%;
}

.hpbx-menu-handle {
    cursor: pointer;
    position: absolute;
    z-index: 1103;

    &:before {
        .remFontSize(1.6);
        .sg-lgi-icon-font();
    }
}

.hpbx-menu-dummy-handle {
    background-color: @sgColorWhite;
    border-bottom: 1px solid @sgColorGrey5;
    display: none;
    height: 45px;
    position: fixed;
    right: 0;
    top: 0;
    width: 50px;
    z-index: 1103;
    margin-top: 90px;
}

.hpbx-menu-handle-open {
    .remRight(1.3);
    .remTop(1.5);
    .transition(opacity 0.2s ease 0.5s);

    &:before {
        color: @sgColorWhite;
        content: '\e61a';
    }
}

.hpbx-menu-handle-close {
    &:before {
        color: @sgColorWhite;
        content: '\e622';
    }
}

.hpbx-menu-items {
    border-bottom: 1px solid @hpbxColorMenuItemSubLines;
}

.hpbx-menu-item {
    border-top: 1px solid @sgColorGrey5;
    margin-bottom: 0;
    position: relative;

    .box-sizing(content-box);
    .remFontSize(@hpbxFontSizeMenu);
    .remMinHeight(4.4);

    &:before {
        content: '';
    }

    &.active-trail {
        background-color: @hpbxColorMenuActiveBackground;

        &:hover {
            background-color: @hpbxColorMenuActiveBackground;

            .hpbx-menu-item-link {
                color: @sgColorWhite;
            }
        }
        .hpbx-menu-item-link {
            color: @sgColorWhite;
        }
    }
}

.hpbx-submenu-item {
    .remPaddingLeft(2);
}

.hpbx-menu-item-link {
    text-decoration: none;
}

.hpbx-menu-item-user {
    background-color: @sgColorWhite;
    border-top: none;

    .remFontSize(1.6);

    .hpbx-menu-item-icon-container {
        .remLeft(45);
    }

    .hpbx-menu-item-title-container {
        line-height: 1;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        .remPaddingLeft(25.6);
        .remPaddingTop(1.6);

        &.hpbx-menu-item-user-title-container-multiple-lines {
            .remPaddingTop(1.6);
        }
    }

    .hpbx-menu-item-title-container,
    .hpbx-menu-item-subtitle-container {
        .transition(0s ease-out 0.4s);
    }
}

.hpbx-menu-item-everyone {
    background-color: @sgColorWhite;
    
    &:hover {
        background-color: @sgColorGrey6;

        .hpbx-menu-item-link {
            color: @hpbxColorMenuItemEveryoneContentHover;
        }
    }

    .hpbx-menu-item-link {
        color: @hpbxColorMenuItemEveryoneContent;
    }
}

.hpbx-menu-item-admin {
    background-color: @sgColorGrey7;

    &:hover {
        background-color: @sgColorGrey6;

        .hpbx-menu-item-link {
            color: @sgColorGrey1;
        }
    }

    .hpbx-menu-item-link {
        color: @sgColorGrey3;
    }
}

.hpbx-menu-item-sub {
    background-color: @hpbxColorMenuItemSubBackground;
    border-bottom: 1px solid @hpbxColorMenuItemSubLines;
    border-top: none;

    &:hover {
        background-color: @hpbxColorMenuItemSubBackgroundHover;

        .hpbx-menu-item-link {
            color: @sgColorWhite;
        }
    }

    &[data-parent-id] {
        display: none;

        .hpbx-menu-item-icon-container {
            display: none;
        }
    }

    &[data-parent-id="language"] {
        .hpbx-menu-item-icon {
            .remMarginLeft(1.5);
        }
    }

    .hpbx-menu-item-link {
        color: @sgColorGrey7;
    }
}

.hpbx-menu-item-sub-active {
    display: none !important;
}

.hpbx-menu-item-sub-opened[data-parent-id] {
    display: block;
}

.hpbx-menu-item-call-log .hpbx-menu-item-icon:before { content: '\e600'; }
.hpbx-menu-item-fax .hpbx-menu-item-icon:before { content: '\e665'; }
.hpbx-menu-item-voicemail .hpbx-menu-item-icon:before { content: '\e602'; }
.hpbx-menu-item-personal-settings .hpbx-menu-item-icon:before { content: '\e604'; }
.hpbx-menu-item-dashboard .hpbx-menu-item-icon:before { content: '\e63f'; }
.hpbx-menu-item-customers .hpbx-menu-item-icon:before { content: '\e63c'; }
.hpbx-menu-item-subscribers .hpbx-menu-item-icon:before { content: '\e606'; }
.hpbx-menu-item-pbx-devices .hpbx-menu-item-icon:before { content: '\e608'; }
.hpbx-menu-item-hunting-groups .hpbx-menu-item-icon:before { content: '\e60a'; }
.hpbx-menu-item-auto-attendant .hpbx-menu-item-icon:before { content: '\e60c'; }
.hpbx-menu-item-conference-room .hpbx-menu-item-icon:before { content: '\e60e'; }
.hpbx-menu-item-music-on-hold .hpbx-menu-item-icon:before { content: '\e610'; }
.hpbx-menu-item-default-forward .hpbx-menu-item-icon:before { content: '\e612'; }
.hpbx-menu-item-password .hpbx-menu-item-icon:before { content: '\e633'; }
.hpbx-menu-item-help .hpbx-menu-item-icon:before { content: '\e614'; }
.hpbx-menu-item-download .hpbx-menu-item-icon:before { content: '\e647'; }
.hpbx-menu-item-logout .hpbx-menu-item-icon:before { content: '\e618'; }
.hpbx-menu-item-language .hpbx-menu-item-icon:before { content: '\e616'; }
.hpbx-menu-item-start-wizard .hpbx-menu-item-icon:before { content: '\e64d'; }
.hpbx-menu-item-bulk-upload .hpbx-menu-item-icon:before { content: '\e645'; }

.hpbx-menu-item-icon-container {
    position: absolute;
    top: 0;

    .remHeight(4.4);
    .remLeft(25);
    .remWidth(4.4);
    .transition(0s ease-out 0.4s);
}

.hpbx-menu-item-icon {
    line-height: 1;
    //color: @sgColorGrey2;
    .remFontSize(1.6);
    .remMarginLeft(1.7);
    .remMarginTop(1.4);

    &:before {
        .sg-lgi-icon-font();
    }
}

.hpbx-menu-item-count {
    background-color: @hpbxColorMenuItemVoicemailCountBackground;
    color: @sgColorWhite;
    display: inline-block;
    position: absolute;

    .remBorderRadiusAll(10);
    .remFontSize(@hpbxFontSizeMenuItemVoicemailCount);
    .remPaddingLeft(0.5);
    .remPaddingRight(0.5);
    .remTop(1);
    .remRight(0.3);
}

.hpbx-menu-item-title-container {
    line-height: @hpbxLineHeightMenuItem;

    .remPaddingLeft(29.1);
    .remPaddingBottom(@hpbxPaddingBottomMenuItem);
    .remPaddingTop(@hpbxPaddingTopMenuItem);
    .remPaddingRight(2);
    .transition(0s ease-out 0.4s);
}

.hpbx-menu-item-subtitle-container {
    display: none;

    .remFontSize(1.2);
    .remPaddingLeft(29.1);
    .remPaddingRight(2);
}

.hpbx-menu-item-subtitle-addition {
    color: @sgColorGrey2;

    .remFontSize(1.2);

    &:before {
        content: '[';
    }

    &:after {
        content: ']';
    }
}

.hpbx-menu-item-subtitle {
    .remFontSize(1.2);
}

.hpbx-menu-item-handle-container {
    position: absolute;
    top: 0;

    .remHeight(4.4);
    .remRight(-25);
    .remWidth(4.4);
    .transition(0.5s ease-in 0s); 
}

.hpbx-menu-item-handle {
    line-height: 1;

    .remFontSize(1.6);
    .remMarginLeft(0.9);
    .remMarginTop(1.4);
    .sg-lgi-icon-font();
}

.hpbx-menu-item-language .hpbx-menu-item-handle:before {
    content: '\e62a';
}

.hpbx-menu-opened .hpbx-menu-item-sub {
    &[data-item-id] {
        &.hpbx-menu-item-sub-opened {
            .hpbx-menu-item-handle:before {
                content: '\e62c';
            }
        }
    }
}

@media @XS {
    .hpbx-menu-xs();
}

.hpbx-menu-xs() {
    .hpbx-menu {
        right: 0px;
        width: 50px;
        margin-top: 55px;
        position: fixed;
    }

    .hpbx-menu-handle-open {
        .remRight(1.6);
        .remTop(1);
        &:before {
            color: @sgColorGrey1;
            content: '\e61a';
        }
    }

    .hpbx-menu-handle-close {
        display: none;
    }

    .hpbx-menu-item-title-container,
    .hpbx-menu-item-subtitle-container {
        .remWidth(49.1);
    }

    .hpbx-menu-item-user {
        .hpbx-menu-item-title-container,
        .hpbx-menu-item-subtitle-container {
            .remWidth(56.2);
        }
    }

    .hpbx-menu-opened {
        .hpbx-menu-handle-close {
            display: block;
        }

        .hpbx-menu-item-sub[data-parent-id] {
            .hpbx-menu-item-icon-container {
                display: none;
            }
        }
    }

    .hpbx-menu-item-sub[data-parent-id] {
        .hpbx-menu-item-icon-container {
            display: block;
        }
    }

    .hpbx-menu-item-icon-container {
        left: 0;
    }

    .hpbx-submenu-item {
        .hpbx-menu-item-icon-container {
            left: 0px;
        }
    }

    body {
        &.hpbx-menu-in-page-flow {
            .hpbx-menu {
                overflow-x: hidden;
                width: 50px;
                //margin-top: 90px;
            }
            
            .hpbx-menu-dummy-handle {
                display: block;
            }
        }

        &.hpbx-menu-opened {
            .hpbx-menu {
                width: 250px;
            }

            .hpbx-menu-dummy-handle {
                display: none;
            }
    
            .hpbx-menu-item-title-container,
            .hpbx-menu-item-subtitle-container {
                .transition-delay(0.15s);
                .remWidth(24.1);
            }

            .hpbx-menu-item-user {
                .hpbx-menu-item-title-container,
                .hpbx-menu-item-subtitle-container {
                    .transition-delay(0.1s);
                    .remWidth(24.1);
                }
            }
        }
    }
}

@media @SM {
    .hpbx-menu-sm();
}

.hpbx-menu-sm() {
    .hpbx-menu {
        right: 0px;
        width: 50px;
        margin-top: 90px;
        position: absolute;
    }
    .hpbx-menu-item-user {
        height: auto;

        .remMinHeight(8);
    
        .hpbx-menu-item-title-container {
            line-height: 1;

            .remPaddingTop(2.8);

            overflow: visible;
            text-overflow: clip;
            white-space: normal;

            &.hpbx-menu-item-user-title-container-multiple-lines {
                .remPaddingTop(1.8);
            }
        }

        .hpbx-menu-item-title-container,
        .hpbx-menu-item-subtitle-container {
            .remPaddingLeft(29.1);
        }

        .hpbx-menu-item-subtitle-container {
            .remPaddingBottom(1);
        }
    }

    .hpbx-menu-handle-open {
        .remTop(3);
        &:before {
            color: @sgColorGrey1;
            content: '\e61a';
        }
    }

    .hpbx-menu-opened {
        .hpbx-menu-handle-close {
            .remMarginTop(3.2);
        }
    }

    .hpbx-menu-item-subtitle-container {
        display: block;
    }

    .hpbx-menu-dummy-handle {
        display: none !important;
    }
}

@media @MD {
    .hpbx-menu-md();
}

.hpbx-menu-md() {
    .hpbx-menu {
        right: 0;
        width: 250px;
        margin-top: 90px;
        position: absolute;
    }

    body {
        &.hpbx-menu-in-page-flow {
            .hpbx-menu {
                width: 250px;
                overflow-x: visible;
                margin-top: 90px;
                position: absolute;
            }
        }

        &.hpbx-contains-menu-over-menu {
            .hpbx-menu-item-user {
                .hpbx-menu-item-title-container,
                .hpbx-menu-item-subtitle-container {
                    .remPaddingLeft(0.6);
                }
            }
        }
    }

    .hpbx-menu-handle {
        display: none;
    }

    .hpbx-menu-item-user {
        .hpbx-menu-item-icon-container {
            display: none;
        }
    }

    .hpbx-menu-item-title-container,
    .hpbx-menu-item-subtitle-container {
        width: auto;

        .remPaddingLeft(4.1);
    }

    .hpbx-submenu-item {
        .hpbx-menu-item-icon-container {
            left: 11px;
        }
    }

    .hpbx-menu-item-user {
        .hpbx-menu-item-title-container,
        .hpbx-menu-item-subtitle-container {
            width: auto;

            .remPaddingLeft(4.1);
        }
    }

    .hpbx-menu-item-sub[data-parent-id] {
        .hpbx-menu-item-icon-container {
            display: none;
        }
    }
    
    .hpbx-menu-item-language .hpbx-menu-item-handle:before {
        content: '\e62a';
    }

    .hpbx-menu-item-handle-container {
        right: 0;
    }

    .hpbx-menu-item-sub {
        &[data-item-id] {
            &.hpbx-menu-item-sub-opened {
                .hpbx-menu-item-handle:before {
                    content: '\e62c';
                }
            }
        }
    }
}

.ie8 {
    .hpbx-menu-xs();
    .hpbx-menu-sm();
    .hpbx-menu-md();

    .hpbx-menu {
        right: 0;
    }

    .hpbx-menu-handle {
        display: none;
    }
}