
body button.hpbx-delete,
body div.hpbx-delete {
    .hpbx-delete(@hpbxStyleguide);
}

.hpbx-delete(@styleguide) when (@styleguide = 'upc-business') {
    &.sg-btn-small {
        border-bottom: 1px solid @sgColorButtonWhiteBorder !important;

        .box-shadow(none);

        .remHeight(3.6);
        .remLineHeight(3.6);
        .remMinHeight(3.6);
        .remPaddingAll(0);
        .remWidth(3.6);

        &:hover {
            background-color: transparent;
        }
        &:active {
            color: @sgColorGrey1;
        }
    }

    .sg-btn-icon {
        .remFontSize(1.1);
    }
}

.hpbx-delete(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-delete('upc-business');
    .remBorderRadiusAll(0.3);
}

.hpbx-delete(@styleguide) when (@styleguide = 'ziggo') {
    background: transparent;
    border: none !important;
    filter: none !important;
    height: 16px !important;
    min-height: 16px !important;
    min-width: 16px !important;
    max-height: 16px !important;
    overflow: visible;
    padding: 0 !important;
    width: 16px !important;

    .box-shadow(none);

    &:after {
        content: '\e622';
        position: absolute;

        .remFontSize(1.6);
        .remRight(0);
        .remTop(0.8);
        .sg-lgi-icon-font();
    }

    &:hover {
        background: transparent;
    }

    span {
        display: none;
    }
}

.hpbx-delete(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-delete('ziggo');
}

.hpbx-delete(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-delete('ziggo');
}

.hpbx-deletable-row {
    .remMarginBottom(1) !important;

    .hpbx-tab-form-field {
        position: relative;
    }

    .hpbx-delete-timeslots {
        .remRight(2);
    }
}

.hpbx-deletable-row-xs-max() {
    .hpbx-deletable-row {

        .hpbx-tab-form-label-field-row {
            position: relative;
            left: 0;

            .clearfix();
            .transition(all 0.5s);
        }

        &.hpbx-show-delete {
            overflow: hidden;

            .hpbx-tab-form-label-field-row {
                .remLeft(-5);

                > div:last-child {
                    display: block !important;

                    .remMarginRight(-3.8);
                    .remPadding(0,0,0,0);

                    .hpbx-delete {
                        visibility: visible;
                    }
                }
            }
        }

        .hpbx-delete {
            .remMarginTop(0.1);
        }
    }

    .hpbx-timeslot-row {
        .hpbx-delete {
            position: absolute;
            
            .remRight(1);
        }
    }
}

@media @XS_MAX {
    .hpbx-deletable-row-xs-max();
}

.hpbx-deletable-row-sm() {
    .hpbx-deletable-row {
        .hpbx-delete-timeslots {
            .remRight(1.7);
        }
    }
}

@media @SM {
    .hpbx-deletable-row-sm();
}

.hpbx-delete-sm(@styleguide) when (@styleguide = 'upc-business') {
    body button.hpbx-delete {
        &.sg-btn-small {
            .remHeight(1.5);
            .remLineHeight(1.3);
            .remMinHeight(1.5);
            .remPaddingAll(0);
            .remWidth(1.5);
        }

        .sg-btn-icon {
            .remFontSize(.7);
        }
    }
}

.hpbx-delete-sm(@styleguide) when (@styleguide = 'ziggo') {
    .hpbx-timeslot-row {
        .hpbx-delete {
            //.remMarginTop(2.3);
            .box-shadow(none);
            .remTop(1);
        }
    }
}

.hpbx-delete-sm(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-delete-sm('ziggo');
}

.hpbx-delete-sm(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-delete-sm('ziggo');
}

@media @SM {
    .hpbx-delete-sm(@hpbxStyleguide);
}

.hpbx-deletable-row-md() {
    .hpbx-deletable-row {
        .hpbx-delete-timeslots {
            .remRight(1.3);
        }
    }
}

@media @MD {
    .hpbx-deletable-row-md();
}

.hpbx-deletable-row-lg() {
    .hpbx-deletable-row {
        .hpbx-delete-timeslots {
            .remRight(0.4);
        }
    }
}

@media @LG {
    .hpbx-deletable-row-lg();
}

.ie8 {
    .hpbx-deletable-row-xs-max();
    .hpbx-delete-sm(@hpbxStyleguide);
}