.hpbx-initialize-variables-bulk-upload(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorBulkUploadTableHeader: @sgColorPurple1;
    @hpbxColorBulkUploadStatusValid: @sgColorGreen2;
    @hpbxColorBulkUploadStatusInvalid: @sgColorRed;
}

.hpbx-initialize-variables-bulk-upload(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-bulk-upload('upc-business');
}

.hpbx-initialize-variables-bulk-upload(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorBulkUploadTableHeader: @hpbxColorPrimary;
    @hpbxColorBulkUploadStatusValid: @sgColorSignal2;
    @hpbxColorBulkUploadStatusInvalid: @sgColorSignal1;
}

.hpbx-initialize-variables-bulk-upload(@styleguide) when (@styleguide = 'virgin') {
    @hpbxColorBulkUploadTableHeader: @hpbxColorPrimary;
    @hpbxColorBulkUploadStatusValid: @sgColorSignal2;
    @hpbxColorBulkUploadStatusInvalid: @sgColorSignal1;
}

.hpbx-initialize-variables-bulk-upload(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-bulk-upload('ziggo');
}

.hpbx-initialize-variables-bulk-upload(@hpbxStyleguide);

.hpbx-bulk-upload-table {
    .remFontSize(@sgFontSizeTextSmall);

    &.hpbx-animating {
        .hpbx-bulk-upload-status-container {
            overflow: hidden;
        }
    }

    th {
        background-color: @sgColorGrey7;
        border-bottom: 1px solid @sgColorGrey5;
        border-top: 1px solid @sgColorGrey5;
        color: @hpbxColorBulkUploadTableHeader;
        font-weight: normal;
        position: relative;
        text-align: left;

        .box-sizing(border-box);
        .remHeight(3);
        .remPaddingLeft(1.5);
        .remPaddingRight(1.5);
        .remMinWidth(17.6);

        &.hpbx-bulk-column-checkmark,
        &.hpbx-bulk-column-delete {
            .remMinWidth(6.5);
        }

        &.hpbx-bulk-column-select {
            .remMinWidth(15);
        }
    }

    td.hpbx-invalid:not(.hpbx-disabled) {
        position: relative;

        &:before {
            background-color: @hpbxColorBulkUploadStatusInvalid;
            color: @sgColorWhite;
            content: attr(data-invalid-message);
            position: absolute;
            text-align: center;
            white-space: pre-wrap;
            width: 100%;
            z-index: 1011;

            .opacity(0);
            .remBorderRadius(0, 0, 0.2, 0.2);
            .remFontSize(@sgFontSizeTextSmall);
            .remLeft(-9999);
            .remPaddingBottom(0.4);
            .remPaddingLeft(0.8);
            .remPaddingRight(0.8);
            .remPaddingTop(0.5);
            .remTop(5);
            .transition-multiple(@sgMiscDurationTransitionDefault opacity, @sgMiscDurationTransitionDefault top, 0s left ease @sgMiscDurationTransitionDefault;);
        }

        &:hover {
            &:before {
                left: 0;

                .opacity(1);
                .transition-multiple(@sgMiscDurationTransitionDefault opacity ease 0.5s, @sgMiscDurationTransitionDefault top ease 0.5s;);
            }
        }

        input[type="text"],
        .chzn-single {
            border-bottom-color: @hpbxColorBulkUploadStatusInvalid;
            color: @hpbxColorBulkUploadStatusInvalid;

            .sg-divider:after {
                color: @hpbxColorBulkUploadStatusInvalid;
            }
        }
    }

    input[type="text"] {
        background-color: @sgColorWhite;
        border-bottom-color: @sgColorGrey5;
        border-left-color: @sgColorGrey5;
        border-right-color: @sgColorWhite;
        border-top-color: @sgColorWhite;
        min-width: 100%;

        .remBorderRadiusAll(0);
        .remFontSize(@sgFontSizeTextSmall);
        .remHeight(5);
        .remPaddingLeft(1.4);
        .remPaddingRight(1.4);
        .transition-property(~"color, border-color, width, background-color");

        &:focus {
            background-color: @sgColorWhite;
            border-color: @hpbxColorPrimary !important;
            border-left-width: 1px !important;
            color: @hpbxColorPrimary;

            .remPaddingLeft(1.4) !important;
        }

        &.hpbx-disabled {
            background-color: @sgColorGrey7;
            border-right-color: @sgColorGrey7;
            border-top-color: @sgColorGrey7;
            color: @sgColorGrey7;
        }

        &.hpbx-same-as-left-neighbour {
            border-left-color: @sgColorWhite;
            border-left-width: 0px;

            .remPaddingLeft(1.5);

            &.hpbx-disabled {
                border-left-color: @sgColorGrey7;
            }
        }
    }

    .chzn-container {
        .remHeight(5);
        .remMinWidth(15);

        &.hpbx-disabled {
            .chzn-single {
                background-color: @sgColorGrey7 !important;
                border-bottom-color: @sgColorGrey5;
                border-left-color: @sgColorGrey5;
                border-right-color: @sgColorGrey7;
                border-top-color: @sgColorGrey7;
                color: @sgColorGrey7;
        
                &.hpbx-same-as-left-neighbour {
                    border-left-color: @sgColorGrey7;
                }
            }

            .sg-handle {
                background-color: @sgColorGrey7 !important;
            }

            .sg-divider {
                background-color: @sgColorGrey7 !important;

                &:after {
                    color: @sgColorGrey7 !important;
                }
            }
        }

        &.chzn-container-active {
            .chzn-single {
                border-bottom-color: @sgColorGrey5;
                border-left-color: @sgColorGrey5;
                border-right-color: @sgColorWhite;
                border-top-color: @sgColorWhite;
            }
        }

        .chzn-single {
            background: none;
            background-color: @sgColorWhite !important;
            border-bottom-color: @sgColorGrey5;
            border-left-color: @sgColorGrey5;
            border-right-color: @sgColorWhite;
            border-top-color: @sgColorWhite;
            height: 100%;

            .remBorderRadius(0);
            .remPaddingLeft(1.5);
            .remPaddingTop(0.7);

            &.sg-animate-down {
                border-bottom: 1px solid @sgColorGrey4 !important;
                border-left: 1px solid @hpbxColorPrimary !important;
                border-right: 1px solid @hpbxColorPrimary !important;
                border-top: 1px solid @hpbxColorPrimary !important;
                color: @hpbxColorPrimary;

                .sg-divider:after {
                    color: @hpbxColorPrimary;
                }
        
                &+ .chzn-drop {
                    .box-shadow(0 2px 2px rgba(0,0,0,.15));
                    .remMarginLeft(-2.5);
                    .remMarginTop(0);
                    .remMinWidth(2);
                }
            }

            &.hpbx-same-as-left-neighbour {
                border-left-color: @sgColorWhite;
            }
        }

        .chzn-drop {
            overflow: hidden;

            &.sg-animate-up {
                border: none;
            }
        }
    }


    .sg-handle {
        background: none;
        background-color: @sgColorWhite !important;
    }

    .sg-divider {
        background-color: @sgColorWhite !important;

        &:after {
            color: @sgColorGrey5;

            .remTop(0.8) !important;
        }
    }

    .chzn-results {
        .remMaxHeight(19.1) !important;
        .remMinWidth(14.8);
    }

    .hpbx-autocomplete-popover {
        border-color: @sgColorGrey4;
        overflow-y: hidden;
        
        .box-shadow(0 2px 2px rgba(0,0,0,.15));
        .remMarginLeft(-2.5);
        .remMarginTop(-0.1);
    }

    .hpbx-autocomplete-popover-active {
        overflow-y: auto;
    }

    .hpbx-bulk-upload-successful {
        .hpbx-bulk-upload-status-container {
            background-color: @hpbxColorBulkUploadStatusValid;
            border-left-color: @hpbxColorBulkUploadStatusValid;
            border-right-color: @hpbxColorBulkUploadStatusValid;
            border-top-color: @hpbxColorBulkUploadStatusValid;
        }

        .hpbx-bulk-upload-status-icon:before {
            color: @sgColorGrey7;
        }

        .hpbx-bulk-upload-second-checkmark {
            display: block;
        }

        input[type="text"],
        input[type="text"].hpbx-disabled,
        .chzn-container .chzn-single {
            background-color: @sgColorGrey7 !important;
            border-left-color: @sgColorGrey7 !important;
            border-right-color: @sgColorGrey7 !important;
            border-top-color: @sgColorGrey7 !important;
            color: @sgColorGrey7;
        }

        input[type="text"]:not(.hpbx-disabled),
        .chzn-container:not(.hpbx-disabled) .chzn-single {
            color: @hpbxColorBulkUploadStatusValid;
        }

        .chzn-container .sg-handle {
            background-color: @sgColorGrey7 !important;

            .sg-divider {
                display: none;
            }
        }
    }

    .hpbx-bulk-upload-unsuccessful .hpbx-bulk-upload-status-container {
        &:hover {
            .hpbx-bulk-upload-status-upload-failure-message {
                .opacity(1);
                .remLeft(7.9);
                .remTop(-0.5);
                .transition-multiple(@sgMiscDurationTransitionDefault opacity, @sgMiscDurationTransitionDefault top;);
            }
        }

        &:after {
            border-color: transparent @hpbxColorBulkUploadStatusInvalid transparent transparent;
            border-style: solid;
            border-width: 0 9px 9px 0;
            content: 'dummy';
            font-size: 0;
            height: 0;
            position: absolute;
            right: 0;
            top: 0;
            width: 0;
            z-index: 1011;

            .transition(@sgMiscDurationTransitionDefault);
        }
    }
}

.hpbx-bulk-upload-status-container {
    background-color: #f3fbf5;
    position: relative;
}

.hpbx-bulk-upload-status-icon:before {
    color: @hpbxColorBulkUploadStatusValid;
    content: '\e624';

    & when not(@hpbxStyleguide = 'upc-business') {
        content: '\e625';
    }
}

.hpbx-bulk-upload-second-checkmark {
    display: none;

    .remMarginTop(-3.1);
}


.hpbx-bulk-upload-status-upload-failure-message {
    background-color: @hpbxColorBulkUploadStatusInvalid;
    color: @sgColorWhite;
    position: absolute;
    top: 0;
    white-space: nowrap;
    z-index: 1014;

    .opacity(0);
    .remBorderRadiusAll(0.3);
    .remFontSize(@sgFontSizeText);
    .remLeft(-9999);
    .remMinHeight(5);
    .remPaddingBottom(1.5);
    .remPaddingLeft(1.5);
    .remPaddingRight(1.5);
    .remPaddingTop(1.5);
    .remTop(-1.5);
    .transition-multiple(@sgMiscDurationTransitionDefault opacity, @sgMiscDurationTransitionDefault top, 0s left ease @sgMiscDurationTransitionDefault;);

    &:before {
        border-color: transparent @hpbxColorBulkUploadStatusInvalid transparent transparent;
        border-style: solid;
        border-width: 8px 8px 8px 0;
        content: 'dummy';
        font-size: 0;
        position: absolute;
        z-index: 1011;

        .remLeft(-0.8);
        .remTop(1.7);
    }
}
.hpbx-bulk-upload-invalid,
.hpbx-bulk-upload-unsuccessful {
    .hpbx-bulk-upload-status-container {
        background-color: #f1eeea;
    }

    .hpbx-bulk-upload-status-icon:before {
        color: @hpbxColorBulkUploadStatusInvalid;
        content: '\e622';

        & when not(@hpbxStyleguide = 'upc-business') {
            content: '\e623';
        }
    }
}

.hpbx-bulk-upload-status-container {
    border-left: 1px solid @sgColorGrey5;
    border-bottom: 1px solid @sgColorGrey5;

    .remMinWidth(6);
}

.hpbx-bulk-upload-status-icon {
    .sg-lgi-icon-font();

    .remFontSize(1.6);
    .remMarginLeft(2.1);
}

.hpbx-bulk-upload-add {
    position: relative;

    .remHeight(4.9);
}

.hpbx-bulk-upload-add-table {
    display: none;
}

.hpbx-bulk-upload-add-icon {
    color: @sgColorGrey3;
    cursor: pointer;
    position: absolute;

    .remFontSize(1.5);
    .remLeft(2.3);
    .remTop(1.8);
    .transition(@sgMiscDurationTransitionDefault);

    &:hover {
        color: @hpbxColorPrimary;

        .rotate(180deg);
    }

    & when not(@hpbxStyleguide = 'upc-business') {
        &:before {
            content: '\e61f';
        }
    }
}

.hpbx-bulk-upload-custom-select-expand-space {
    .remHeight(17);
}

.hpbx-bulk-upload-anmiation-remove-prepare {
    overflow: hidden;

    .remHeight(5);

    .hpbx-bulk-upload-remove-icon,
    .hpbx-bulk-upload-status-icon {
        display: none;
    }
}

.hpbx-bulk-upload-remove-container {
    border-bottom: 1px solid @sgColorGrey5;
    border-left: 1px solid @sgColorGrey5;

    &.hpbx-same-as-left-neighbour {
        border-left-color: @sgColorWhite;
    }
}

.hpbx-bulk-upload-remove-icon {
    color: @sgColorGrey3;
    cursor: pointer;

    .remFontSize(1.6);
    .remPaddingLeft(2.5);

    &:hover {
        color: @sgColorGrey2;
    }
}

.hpbx-bulk-upload-row-can-be-deleted {
    visibility: hidden;
}