/*
 Anchor

 Markup: <a href="#" class="{$modifiers}">Link</a>

 .sg-animate.sg-horizontal-lines  - Shows an animation of a horizontal line going up and down on hover
 :focus                           - remove outline

 Styleguide 1.0.1
*/
a {
	color: @sgColorAnchor;
	text-decoration: underline;

	&:focus {
		outline-style: none;
	}

	&.sg-animate {
		display: inline-block;
		position: relative;

		&.sg-horizontal-lines {
			@animationDuration: @sgMiscDurationTransitionDefault;
			
			text-decoration: none;

			.remPadding(0.2, 0, 0.1, 0);

			&:before,
			&:after {
				background: @sgColorAnchor;
				content: '';
				left: 0;
				position: absolute;
				top: 100%;
				width: 100%;

				.remHeight(0.1);
				.transition-transform(@animationDuration);
				.transform(scale(0.85));
			}

			&:after {
				.opacity(0);
				.transition(all @animationDuration);
			}

			&:hover:before,
			&:hover:after,
			&:focus:before,
			&:focus:after {
				.transform(scale(1));
			}

			&:hover:after,
			&:focus:after {
				top: 0%;
				
				.opacity(1);
			}
		}
	}
}
