.hpbx-foldable-list-container {
    background-color: @sgColorWhite;
    border: 1px solid @sgColorGrey4;
    border-spacing: 0;

    .box-shadow(0 5px 10px rgba(204,208,218,.5));
    .remBorderRadiusAll(0.4);
    .remMarginTop(3);

    .hpbx-foldable-content {
        .row:last-child {
            .remPaddingBottom(1.3);
        }
    }

    .hpbx-foldable-heading-first {
        .remBorderTopRadius(0.4);
    }
}
