.hpbx-initialize-variables-dashboard(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorDashboardInfoIcon: @sgColorPurple1;
    @hpbxColorDashboardExtension: @sgColorPurple1;
}

.hpbx-initialize-variables-dashboard(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-dashboard('upc-business');

}

.hpbx-initialize-variables-dashboard(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorDashboardInfoIcon: @hpbxColorPrimary;
    @hpbxColorDashboardExtension: @hpbxColorPrimary;
}

.hpbx-initialize-variables-dashboard(@styleguide) when (@styleguide = 'virgin') {
    @hpbxColorDashboardInfoIcon: @hpbxColorPrimary;
    @hpbxColorDashboardExtension: @hpbxColorPrimary;
}

.hpbx-initialize-variables-dashboard(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-dashboard('ziggo');
}

.hpbx-initialize-variables-dashboard(@hpbxStyleguide);

.page-hpbx-dashboard,
.hpbx-page-dashboard {
    background-color: @sgColorGrey6;

    .hpbx-body-background-image,
    .hpbx-header-background-image {
        display: none !important;
    }

    .hpbx-body-background-color {
        background: none;
        background-color: @sgColorGrey7;
        position: absolute;

        .remHeight(135);
    }

    .hpbx-percentage-display-svg {
        .remWidth(15);
    }
}

.hpbx-dashboard-circles {
    .remMarginTop(4.2);
}

.hpbx-dashboard-wizard-buttons {
    text-align: center;

    .remMarginTop(3);
}

.hpbx-dashboard-info {
    .remMarginTop(4);

    a {
        text-decoration: none;

        // &:hover {
        //     text-decoration: underline;
        // }
    }
}

.hpbx-dashboard-info-title {
    margin-top: 0;
    position: relative;

    .remLineHeight(4.4);
    .remMarginBottom(0.1);
    .remPaddingLeft(4.3);
}

.hpbx-dashboard-info-subtitle {
    .remMarginBottom(1.4);
}

.hpbx-dashboard-info-icon {
    color: @hpbxColorDashboardInfoIcon;
    left: 0;
    position: absolute;
    top: 0;

    .remFontSize(3.6);
}

.hpbx-dashboard-middle-section {
    background-color: @sgColorGrey6;
    border-top: 1px solid @sgColorGrey4;

    .remMarginTop(-3.8);

    .hpbx-graph-container {
        .remMarginTop(6);
    }
}

.hpbx-dashboard-faq-container {
    .remMarginTop(1);

    .hpbx-dashboard-info-title {
        .remMarginBottom(1.5);
    }
}

.hpbx-dashboard-tabs-container {
    .remMarginTop(4);

    .hpbx-tabs {
        margin-top: 0;
        .r-tabs-nav li, // Fallback
        .hpbx-tab-item {
            &.r-tabs-state-active {
                .r-tabs-anchor {
                    .transition(0.3s ease 0.3s);
                    &:hover {
                        color: @hpbxColorTabText;
                    }
                }
            }
        }
    }

    .hpbx-pbx-extension {
        color: @hpbxColorTableExtension;
        font-weight: bold;
    }
}

.hpbx-dashboard-ordered-list {
    color: @hpbxColorPrimary;
    li {
        .remMarginBottom(1.4);

        &:before {
            color: @hpbxColorPrimary;
        }
    }
}

.hpbx-dashboard-xxs() {
    .hpbx-dashboard-info {
        margin-top: 0;
    }

    .hpbx-dashboard-faq-container {
        .remMarginTop(4);
    }
}

@media @XXS_MAX {
    .hpbx-dashboard-xxs();
}

.hpbx-dashboard-sm() {
    .page-hpbx-dashboard,
    .hpbx-page-dashboard {
        .hpbx-body-background-color {
            .remHeight(80);
        }
    }
}

@media @SM {
    .hpbx-dashboard-sm();
}

.hpbx-dashboard-md() {
    .hpbx-dashboard-tabs-container {
        margin-top: 0;
    }

    .hpbx-dashboard-wizard-buttons {
        text-align: left;
    }

    .hpbx-dashboard-info {
        margin-top: 0;
    }
}

@media @MD {
    .hpbx-dashboard-md();
}

.ie8 {
    .hpbx-dashboard-sm();
    .hpbx-dashboard-md();

    .hpbx-progress-circle-inner,
    .hpbx-pie-chart-inner {
        display: none;
    }
}
