.hpbx-alert {
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    top: -10000px;
    width: 100%;
    z-index: 4000;

    &.hpbx-alert-show {
        top: 0;
    }
}

.hpbx-alert-box {
    background-color: @sgColorGrey7;
    border: 1px solid @sgColorGrey4;
    border-spacing: 0;
    left: 50%;
    position: absolute;
    text-align: center;
    top: 50%;
    z-index: 4001;

    .box-shadow(0 0 20px rgba(0,0,0,.2));
    .box-sizing(border-box);
    .remBorderRadiusAll(0.5);
    .remMarginLeft(-11);
}

.hpbx-alert-small .hpbx-alert-box {
    .remMarginLeft(-11);
    .remPadding(2.9, 2, 2.5, 2);
    .remWidth(22);
}

.hpbx-alert-confirm {
    .hpbx-alert-box {
        .remMarginLeft(-15);
        .remPadding(5, 2, 5, 2);
        .remWidth(27);
    }

    .hpbx-notification-title {
        .remMarginBottom(3);
        .remPaddingLeft(2);
        .remPaddingRight(2);
    }
}

.hpbx-alert-big .hpbx-alert-box {
    .remMarginLeft(-23);
    .remWidth(46);
}

.hpbx-alert-header {
    background-color: @sgColorGrey7;
    border-bottom: 1px solid @sgColorGrey4;

    .remPaddingBottom(1.6);
    .remPaddingTop(3);
}

.hpbx-alert-message-section {
    background-color: @sgColorWhite;
    border-top: 1px solid @sgColorGrey5;

    .remPaddingBottom(2.2);
    .remPaddingTop(2.6);

    &.hpbx-first {
        border-top: none;
    }
}

.hpbx-alert-message-links {
    .remMarginTop(0.4);
}

.hpbx-alert-message-link {
    display: inline;

    .remPaddingLeft(1);
    .remPaddingRight(1);

    &:before {
        display: none;
    }
}

.hpbx-alert-footer {
    border-top: 1px solid @sgColorGrey4;
    font-family: @sgFontFamilyHeadings;

    .remFontSize(@sgFontSizeCta);
    .remPaddingBottom(0.7);
    .remPaddingTop(0.9);
}

.hpbx-notification-button {
    .remMarginTop(1.3);

    .sg-btn {
        .remMarginLeft(0.3);
        .remMarginRight(0.3);
        .remMinWidth(10);
        .sg-give-color-to-element(@sgColorWhite, @sgColorWhite, @hpbxColorPrimary, @hpbxColorPrimary, @hpbxColorPrimary, @sgColorGrey4);
        .sg-give-active-color-to-element(@sgColorWhite, @hpbxColorPrimary, @hpbxColorPrimary);
        font-size: 1.6rem;
        min-height: 4.5rem;
    }
}

.hpbx-alert-close-button {
    cursor: pointer;
}