.hpbx-initialize-variables-foldable(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorStateLabel:               @sgColorPurple1;
}

.hpbx-initialize-variables-foldable(@styleguide) when (@styleguide = 'unitymedia') {
    @hpbxColorStateLabel:               @sgColorBlue;
}

.hpbx-initialize-variables-foldable(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorStateLabel:               @sgColorGrey2;
}

.hpbx-initialize-variables-foldable(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-foldable('ziggo');
}

.hpbx-initialize-variables-foldable(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-foldable('ziggo');
}

.hpbx-initialize-variables-foldable(@hpbxStyleguide);

.hpbx-foldable-list {
    .remMargin(0, 0, 0, 0);

    .hpbx-foldable-heading {
        background-color: @sgColorGrey7;
        border-top: 1px solid @sgColorGrey4;
        border-bottom: 1px solid @sgColorGrey4;
        cursor: pointer;
        overflow: hidden;
        position: relative;

        &.hpbx-disabled {
            .hpbx-foldable-heading-title,
            .hpbx-foldable-heading-state {
                color: @sgColorGrey4;
            }
        }

        .sg-custom-toggle {
            position: absolute;

            .remMarginLeft(2);
            .remRight(5.5);
            .remTop(1.1);

            .sg-caption {
                padding-left: 0;

                .remPaddingRight(1.6);
            }
        }
        
    }
    .hpbx-foldable-heading.hpbx-foldable-state-close {
        border-bottom: none;
    }
    
    .hpbx-foldable-heading-first {
        border-top: 1px solid @sgColorGrey7;
    }

    .hpbx-foldable-heading-last {
        &.hpbx-foldable-state-open {
            border-bottom: 1px solid @sgColorGrey4;
        }
        &.hpbx-foldable-state-close {
            border-bottom: 1px solid @sgColorGrey4;
        }
    }

    .hpbx-foldable-heading-title {
        .remLineHeight(5);
        .remPadding(0, 2, 0, 2);
    }

    .hpbx-foldable-heading-destination {
        display: none;
    }

    .hpbx-foldable-heading-suffix {
        color: @sgColorGrey3;
        display: none;
        font-family: Arial;

        .remFontSize(@sgFontSizeText);

        &:before {
            content: '|';

            .remMarginRight(0.7);
        }
    }

    .hpbx-foldable-heading-state {
        color: @hpbxColorStateLabel;
        float: right;
        vertical-align: top;

        .remFontSize(1.2);
        .remLineHeight(4.7);
        .remPadding(0, 1.5, 0, 1.5);

        &:after {
            .remFontSize(1.6);
            .remMargin(0, 0, 0, 1);
            .sg-lgi-icon-font();
        }
    }

    .hpbx-foldable-heading-state-caption {
        display: none;
    }

    .hpbx-foldable-content {
        overflow: visible;
        //border-bottom: 1px solid @sgColorGrey4;
        .remMargin(2, 0, 2, 0);
    }
    
    .hpbx-foldable-content.hpbx-bundle-settings{
        overflow: scroll; 
    }

    #hpbx-numberranges-table-wrapper{
        overflow: scroll; 
    }
    .hpbx-foldable-state-open {

            
        overflow: visible;
        
        .remPadding(0, 0, 0, 0);

        // & + .hpbx-foldable-state-close {
        //    border-top: 0px solid @sgColorGrey4;
        // }

        // & + .hpbx-foldable-state-open {
        //     border-top: 1px solid @sgColorGrey4;
            
        // }

        &.hpbx-fax-members,
        &.hpbx-hunting-group-members {
            padding-top: 0;
        }
    }
        
    .hpbx-foldable-heading-state:after {
        content: "\e62c";
    }
    

    .hpbx-foldable-state-close {
        & + .hpbx-foldable-content {
            border-bottom: none;
            border-top: none;
            height: 0;
        }

        .hpbx-foldable-heading-state:after {
            content: "\e62a";
        }
    }

    // Child lists
    .hpbx-foldable-list {
        .remMargin(1.5, 0, 1.5, 0);

        .hpbx-foldable-heading {
            background-color: transparent;

            .remMargin(0, 1.5, 0, 1.5);
        }

        .hpbx-foldable-heading-first {
            border-top: 1px solid @sgColorGrey4;
        }

        .hpbx-foldable-heading-state {
            .remPaddingRight(0);
        }

        .hpbx-foldable-heading-title {
            .remPaddingLeft(0);
        }
    }
    
    .hpbx-tab-form-explanation {
        .remPaddingLeft(1);
    }
}

@media @XS {
    .hpbx-foldable-xs();
}

.hpbx-foldable-xs() {
    .hpbx-foldable-list {
        .hpbx-foldable-heading {
            .sg-custom-toggle {
                .remRight(12.5);
            }
        }

        .hpbx-foldable-heading-state-caption {
            display: inline-block;
        }

        .hpbx-foldable-content.hpbx-bundle-settings{
            overflow: scroll; 
        }

        #hpbx-numberranges-table-wrapper{
            overflow: scroll; 
        }
    }
}

@media @SM {
    .hpbx-foldable-sm();
}

.hpbx-foldable-sm() {
    .hpbx-foldable-list {
        .hpbx-foldable-heading-destination,
        .hpbx-foldable-heading-suffix {
            display: inline;
        }

        .hpbx-foldable-content.hpbx-bundle-settings{
            overflow: scroll; 
        }

        #hpbx-numberranges-table-wrapper{
            overflow: scroll; 
        }
    }
}

@media @MD {
    .hpbx-foldable-md();
}

.hpbx-foldable-md() {
    .hpbx-foldable-list {
        .hpbx-foldable-content.hpbx-bundle-settings{
            overflow: visible; 
        }

        #hpbx-numberranges-table-wrapper{
            overflow: visible; 
        }
    }
}

.ie8 {
    .hpbx-foldable-sm();
}