.hpbx-initialize-variables-voicemail(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorVoicemailPersonal: @sgColorOrange;
    @hpbxColorVoicemailHuntgroup: @sgColorBlue;

    @hpbxMarginRightVoicemailTableDeleteIcon: 1.7;
    @hpbxMarginRightVoicemailTableTime: 5;
    @hpbxMarginTopVoicemailTitleIcon: 1.7;

    @hpbxColorVoicemailTableNumber: @sgColorBlue;

    @hpbxRightVoicemailDeleteIcon: 1.8;
    @hpbxTopVoicemailDeleteIcon: 0.6;
}

.hpbx-initialize-variables-voicemail(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-voicemail('upc-business');
}

.hpbx-initialize-variables-voicemail(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorVoicemailPersonal: @hpbxColorPrimary;
    @hpbxColorVoicemailHuntgroup: @sgColorGrey2;

    @hpbxMarginRightVoicemailTableDeleteIcon: 0;
    @hpbxMarginRightVoicemailTableTime: 3;
    @hpbxMarginTopVoicemailTitleIcon: 2.1;

    @hpbxColorVoicemailTableNumber: @sgColorGrey2;

    @hpbxRightVoicemailDeleteIcon: 1.7;
    @hpbxTopVoicemailDeleteIcon: 1.7;
}

.hpbx-initialize-variables-voicemail(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-voicemail('ziggo');
}

.hpbx-initialize-variables-voicemail(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-voicemail('ziggo');
}

.hpbx-initialize-variables-voicemail(@hpbxStyleguide);

.hpbx-content-container-voicemail-page {
    background-color: transparent;

    .hpbx-table-selector-caption {
        background-color: @sgColorWhite;
        border: none;

        .remBorderRadius(0.3, 0.3, 0, 0);
    }

    .hpbx-audio-background-active {
        .remBorderRadiusAll(0.4);
    }

    .chzn-container-single {
        width: 100% !important;
    }

    .chzn-drop
    {
        width: 100% !important;
        .box-sizing(border-box);
    }
}

.hpbx-voicemail-page-title {
    overflow: visible;
}

.hpbx-page-title-voicemail-title {
    .clearfix();

    .sg-icon {
        .remMarginTop(@hpbxMarginTopVoicemailTitleIcon);
    }
}

.hpbx-page-title-voicemail-counters {
    .remMarginTop(0.3);

    .hpbx-progress-circle-container {
        display: inline-block;

        .remMarginTop(3);
        .remMarginRight(2);
    }
}

.hpbx-voicemail-counter-personal {
    .hpbx-progress-circle-svg {
        stroke: @hpbxColorVoicemailPersonal;
    }

    .hpbx-progress-circle-inner {
        color: @hpbxColorVoicemailPersonal;
    }
}

.hpbx-voicemail-counter-huntgroup {
    .hpbx-progress-circle-svg {
        stroke: @hpbxColorVoicemailHuntgroup;
    }

    .hpbx-progress-circle-inner {
        color: @hpbxColorVoicemailHuntgroup;
    }
}

.hpbx-table-voicemail {
    .hpbx-table-head-row {
        cursor: default;

        &.hpbx-read {
            background-color: @sgColorGrey5;

            td {
                color: @sgColorGrey2;
            }
        }

        .sg-custom-check {
            margin: 0;
        }
    }

    .hpbx-table-voicemail-number {
        color: @hpbxColorVoicemailTableNumber;
        font-weight: bold;
    }

    .hpbx-table-voicemail-time {
        color: @hpbxColorVoicemailTableNumber;
        position: relative;
        text-align: right;

        .remPaddingRight(7);
        .remWidth(13);

        .hpbx-delete {
            position: absolute;

            .remRight(@hpbxRightVoicemailDeleteIcon);
            .remTop(@hpbxTopVoicemailDeleteIcon);
        }
    }

    tr:hover {
        .hpbx-table-voicemail-time {
            .hpbx-delete {
                display: block;

                &:after {
                    right: 0;
                }
            }
        }
    }
}

.hpbx-tab-audio-container {
    .hpbx-table-voicemail {
        .hpbx-table-audio-container {
            &.hpbx-read {
                background-color: @sgColorGrey7;

                .hpbx-audio-control-icon:after {
                    color: @sgColorGrey7;
                }

                .hpbx-table-voicemail-number,
                .hpbx-table-voicemail-time {
                    color: @sgColorGrey3;
                }

                &:hover {
                    .hpbx-audio-control-icon:after {
                        color: @hpbxColorPrimary;
                    }
                }
            }
        }
    }
}

@media @XS {
    .hpbx-voicemail-xs();
}

.hpbx-voicemail-xs() {
    .hpbx-voicemail-page-title {
        .remMarginTop(2.8);
    }

    .hpbx-page-title-voicemail-title
    {
        .remTop(-0.8);
        position: relative;
    }
}

@media @SM {
    .hpbx-voicemail-sm();
}

.hpbx-voicemail-sm() {

    .hpbx-page-title-voicemail-title {
        position: absolute;
        top: auto;

        .remBottom(0.2);
    }

    .hpbx-voicemail-page-title {
        margin-top: 0;
        overflow: hidden;
    }

    .hpbx-page-title-voicemail-counters {
        float: right;

        .remMarginTop(0);
        .remMarginLeft(3.8);
        .hpbx-progress-circle-container;

    }

    .hpbx-voicemail-page-title .hpbx-progress-circle {
        display: none;
    }
}

@media @SM_ONLY {
    .hpbx-page-title-voicemail-counters {
        margin-top: 0;

        .hpbx-progress-circle-container {
            margin-top: 0;
        }
    }
}


@media @MD {
    .hpbx-voicemail-md();
}

.hpbx-voicemail-md() {
    .hpbx-content-container-voicemail-page {
        .hpbx-table-selector-caption {
            .remMinHeight(20);
        }
    }

    .hpbx-voicemail-page-title {
        overflow: visible;
        position: relative;

        .remMarginTop(0);
    }

    .hpbx-page-title-voicemail-title {
        float: none;
        margin: 0;
        position: relative;

        .remTop(1);
        .remMarginBottom(4);

        .sg-icon {
            .remMarginTop(@hpbxMarginTopVoicemailTitleIcon);
        }
    }

    .hpbx-page-title-voicemail-counters {
        float: none;
        margin: 0;
        position: absolute;
        right: 0;

        .remMarginTop(0.3);
        .remBottom(-22.3);

        .hpbx-progress-circle-svg {
            fill: @sgColorWhite;
        }
    }

    .hpbx-progress-circle.hpbx-progress-circle
    {
        display: inline-block;
    }
}

.ie8 {
    .hpbx-voicemail-xs();
    .hpbx-voicemail-sm();
    .hpbx-voicemail-md();
}

