/******************************* INFORMATION ***************************

Project-specific styling of the toggle
     
************************************************************************/

/********************************** START ******************************/ 

.sg-custom-toggle {
	&.sg-slide-over-text {
		.sg-alternative-display {
			border: 1px solid @sgColorToggleBorder;
			font-size: @sgFontSizeText * 10px;
			height: 40px;
			width: 120px;

			.border-radius(@sgMiscBorderRadiusDefault * 10px);
			.box-sizing(border-box);
			.transition(@sgMiscDurationTransitionDefault);

			.sg-on {
				background-color: @sgColorToggleBackgroundOn;
				color: @sgColorToggleTextOn;

				.border-left-radius(@sgMiscBorderRadiusDefault * 10px - 1px);
			}

			.sg-off {
				background-color: @sgColorToggleBackgroundOff;
				color: @sgColorToggleTextOff;

				.border-right-radius(@sgMiscBorderRadiusDefault * 10px - 1px);
			}

			.sg-on,
			.sg-off {
				float: left;
				height: 100%;
				line-height: 41px;
				text-align: center;
				width: 50%;
			}

			.sg-knob {
				background-color: @sgColorToggleBackgroundKnob;
				border: 1px solid @sgColorToggleBorder;
				height: 40px;
				left: -1px;
				position: absolute;
				top: -1px;
				width: 62px;

				.border-radius(@sgMiscBorderRadiusDefault * 10px);
				.box-sizing(border-box);
				.transition(@sgMiscDurationTransitionDefault);
				
				.sg-icon-toggle {
					left: 23px;
					position: absolute;
					top: 12px;
				}
			}
		}

		label {
            min-height: 40px;
			line-height: 44px;
			padding: 0 0 0 140px;
		}

		&.sg-under-label {
			label {
				padding: 0;

				.sg-alternative-display {
					position: relative;
				}
			}
		}

		&.sg-right-aligned {
			label {
				padding: 0;
			}

			.sg-alternative-display {
				left: auto;
				right: 0;
			}
		}
		
		input:checked ~ .sg-alternative-display {
			.sg-knob {
				left: 57px;
			}
		}

		&:hover {
			.sg-alternative-display {
				.sg-knob {
					background-color: @sgColorToggleBackgroundKnobHover;
				}
			}
		}
	}

	&.sg-slide-colors {
		.sg-alternative-display {
			background-color: @sgColorToggleBackground;
			border: 1px solid @sgColorToggleBorder;
			display: inline-block;
			font-size: @sgFontSizeText * 10px;
			height: 30px;
			position: relative;
			width: 80px;

			.border-radius(100px);
			.box-sizing(border-box);
			.transition(@sgMiscDurationTransitionDefault);

			.sg-on {
				background-color: @sgColorToggleBackgroundOn;
				left: 10px;
				width: 0;
			}

			.sg-off {
				background-color: @sgColorToggleBackgroundOff;
				right: 10px;
				width: 58px;
			}

			.sg-on,
			.sg-off {
				height: 4px;
				position: absolute;
				top: 12px;

				.border-radius(100px);
				.transition(@sgMiscDurationTransitionDefault);
			}

			.sg-knob {
				background-color: @sgColorToggleBackgroundKnob;
				height: 24px;
				left: 2px;
				position: absolute;
				top: 2px;
				width: 24px;

				.border-radius(100px);
				.box-sizing(border-box);
				.box-shadow(0 2px 6px rgba(150,149,145,.5););
				.transition(@sgMiscDurationTransitionDefault);
				
				.sg-dot {
					background-color: @sgColorToggleBackgroundOff;
					height: 8px;
					left: 8px;
					position: absolute;
					top: 8px;
					width: 8px;

					.border-radius(100px);
					.transition(@sgMiscDurationTransitionDefault);
				}
			}
		}

		.sg-caption-container {
			display: inline-block;
			vertical-align: top;
		}

		.sg-caption {
			color: @sgColorToggleBackgroundOff;
			line-height: 30px;
			padding-left: 17px;
			text-transform: uppercase;

			&:after {
				content: "Off";
			}
		}

		label {
			line-height: 44px;
		}

        input:checked ~ .sg-alternative-display {
            .sg-on {
                width: 58px;
            }

            .sg-off {
                width: 0;
            }

            .sg-knob {
                left: 52px;

                .sg-dot {
                    background-color: @sgColorToggleBackgroundOn;
                }
            }
        }

        input:checked ~ .sg-caption-container {
            .sg-caption {
                color: @sgColorToggleBackgroundOn;

                &:after {
                    content: "On";
                }
            }
        }

        &:hover {
            .sg-alternative-display {
                .sg-knob {
                    background-color: @sgColorToggleBackgroundKnobHover;
                }
            }
        }
    }
}

.ie8 {
    .sg-custom-toggle {
        &.sg-slide-colors {
            .sg-caption {
                &:after {
                    color: @sgColorText;
                    content: "On/Off";
                }
            }
        }
    }
}