/*
    borders
*/
.remBorder(@width:0, @style:solid, @color:@bluePhilips) {
    @remValue: @width;
    @pxValue: (@width * 10);
    border: ~"@{pxValue}px @{style} @{color}";
    border: ~"@{remValue}rem @{style} @{color}";
}
.remBorderTop(@width:0, @style:solid, @color:@bluePhilips) {
    @remValue: @width;
    @pxValue: (@width * 10);
    border-top: ~"@{pxValue}px @{style} @{color}";
    border-top: ~"@{remValue}rem @{style} @{color}";
}
.remBorderRight(@width:0, @style:solid, @color:@bluePhilips) {
    @remValue: @width;
    @pxValue: (@width * 10);
    border-right: ~"@{pxValue}px @{style} @{color}";
    border-right: ~"@{remValue}rem @{style} @{color}";
}
.remBorderBottom(@width:0, @style:solid, @color:@bluePhilips) {
