    border-left: ~"@{pxValue}px @{style} @{color}";
    border-left: ~"@{remValue}rem @{style} @{color}";
}
.remBorderWidth(@value) {
    @remValue: @value;
    @pxValue: (@value * 10);
    border-width: ~"@{pxValue}px";
    border-width: ~"@{remValue}rem";
}

.remBorderWidthAll(@valueTop, @valueRight, @valueBottom, @valueLeft) {
    @remValueTop: @valueTop;
    @pxValueTop: (@valueTop * 10);
    @remValueRight: @valueRight;
    @pxValueRight: (@valueRight * 10);
    @remValueBottom: @valueBottom;
    @pxValueBottom: (@valueBottom * 10);
    @remValueLeft: @valueLeft;
    @pxValueLeft: (@valueLeft * 10);

    border-width: ~"@{pxValueTop}px " ~"@{pxValueRight}px " ~"@{pxValueBottom}px " ~"@{pxValueLeft}px " ;
    border-width: ~"@{remValueTop}rem" ~"@{remValueRight}rem" ~"@{remValueBottom}rem" ~"@{remValueLeft}rem" ;
}

.remBorderWidthWithRatio(@value) {
	.remBorderWidth(@value);

	@media (max-width: @screen-xs-max) {
		.remBorderWidth(@value * @sgFontSizeRatioXS);
	}
	@media (min-width: @screen-sm-min) {
		.remBorderWidth(@value * @sgFontSizeRatioS);
	}

	@media (min-width: @screen-md-min) {
		.remBorderWidth(@value * @sgFontSizeRatioM);
	}

	@media (min-width: @screen-lg-min) {
		.remBorderWidth(@value * @sgFontSizeRatioL);
	}
}

.remBorderRadiusAll(@radius:0) {
	.remBorderRadius(@radius, @radius, @radius, @radius);
}

.remBorderRadius(@top:0, @right:0, @bottom:0, @left:0) {
    @remTop: @top;
    @pxTop: (@top * 10);
    @remRight: @right;
    @pxRight: (@right * 10);
    @remBottom: @bottom;
    @pxBottom: (@bottom * 10);
    @remLeft: @left;
    @pxLeft: (@left * 10);
    .border-radius(~"@{pxTop}px @{pxRight}px @{pxBottom}px @{pxLeft}px");
    .border-radius(~"@{remTop}rem @{remRight}rem @{remBottom}rem @{remLeft}rem");
}

