.hpbx-initialize-variables-call-log(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorCallLogCounterBackground: @sgColorGrey3b;
}

.hpbx-initialize-variables-call-log(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-call-log('upc-business');
}

.hpbx-initialize-variables-call-log(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorCallLogCounterBackground: @sgColorGrey2;
}

.hpbx-initialize-variables-call-log(@styleguide) when (@styleguide = 'virgin') {
    @hpbxColorCallLogCounterBackground: @sgColorGrey2;
}

.hpbx-initialize-variables-call-log(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-call-log('ziggo');
}

.hpbx-initialize-variables-call-log(@hpbxStyleguide);

.hpbx-table-call-log {
    .remMarginTop(2.6);

    tbody tr {
        &:hover {
            background-color: @sgColorWhite;
        }

        &.hpbx-table-head-row:hover {
            background-color: @sgColorGrey7;
        }
    }

    &.hpbx-table {
        margin-top: 0;
    }

    .hpbx-table-selector-caption {
        background-color: @sgColorWhite;
        border-bottom: 1px solid @sgColorGrey5;
        border-left: 1px solid @sgColorGrey4;
        border-right: 1px solid @sgColorGrey4;
        border-top: 1px solid @sgColorGrey4;
        text-align: left;

        .box-shadow(0 5px 10px rgba(204,208,218,.5));
        .remBorderTopRadius(0.4);
        .remPaddingLeft(2);
        .remPaddingRight(2);
    }

    .hpbx-table-selector-title {
        color: @sgColorGrey2;
        margin: 0;

        .remMarginTop(1);
    }

    .hpbx-table-selector-inputs {
        position: relative;

        &.hpbx-table-selector-submit-container {
            text-align: right;
        }

        .sg-btn {
            width: 100%;

            .remMarginTop(1);
        }

        .sg-btn-icon {
            display: none;
        }

        &> .row {
            .remMarginTop(0.4);
        }
    }

    .hpbx-table-icon-container {
        padding: 0;
        position: relative;

        .remWidth(5.6);
    }

    .hpbx-table-icon {
        position: absolute;

        .remLeft(2);
        .remTop(1.3);
    }

    [class^="LGI-icon"], [class*=" LGI-icon"] {
        &:before {
            color: @sgColorGrey3;

            .remFontSize(1.6);
        }
    }

    .hpbx-call-icon,
    .hpbx-download-icon {
        &:before {
            color: @sgColorGrey3;

            .remFontSize(1.6);
            .sg-lgi-icon-font();
        }

    }

    .hpbx-call-icon {
        &.hpbx-inbound:before {
            content: '\e631';
        }

        &.hpbx-outbound:before {
            content: '\e632';
        }
    }

    .hpbx-download-icon {
        cursor: pointer;

        &:hover {
            &:before {
                color: @sgColorGrey2;
            }
        }

        &:before {
            content: '\e647';
        }
    }

    .hpbx-table-row-expandable {
        cursor: pointer;

        &:hover {
            background-color: @sgColorGrey7;
        }

        .hpbx-expand-icon:before {
            color: @sgColorGrey3;
            content: '\e63b';

            .remFontSize(1.6);
            .sg-lgi-icon-font();
        }

        .hpbx-expandable-content-counter {
            .remTop(1.7);
        }

        &+ .hpbx-table-row-expandable-content {
            .hpbx-invisible-when-expanded {
                &:before {
                    background-color: @hpbxColorCallLogCounterBackground;
                    content: '';
                    display: block;
                    height: 1px;
                    position: absolute;
                    width: 100%;

                    .remLeft(0.3);
                    .remTop(0.7);
                }
            }
        }
    }

    .hpbx-table-row-expanded {
        background-color: @sgColorGrey3;
        display: table-row !important;

        &:hover {
            background-color: @sgColorGrey3;
        }

        td {
            color: @sgColorWhite;
            position: relative;
        }

        .hpbx-hide-when-expanded {
            display: none;
        }

        .hpbx-invisible-when-expanded {
            color: @sgColorGrey3;

            [class^="LGI-icon"], [class*=" LGI-icon"] {
                &:before {
                    display: none;
                }
            }
        }

        [class^="LGI-icon"], [class*=" LGI-icon"] {
            &:before {
                color: @sgColorWhite;
            }
        }

        .hpbx-table-icon {
            &:before {
                color: @sgColorWhite;
            }
        }

        .hpbx-expandable-content-counter {
            display: block;
        }
    }

    .hpbx-table-row-expandable-content {
        display: none;

        &.hpbx-table-row-expandable-content-last {
            .hpbx-expandable-content-counter:after {
                display: none;
            }
        }

        td {
            border: none;
            position: relative;

            .remPaddingBottom(1.4);
            .remPaddingTop(0);
        }
    }

    .hpbx-expandable-content-counter {
        background-color: @hpbxColorCallLogCounterBackground;
        display: none;
        position: absolute;
        text-align: center;

        .remBorderRadiusAll(10);
        .remFontSize(1);
        .remHeight(1.5);
        .remLeft(-0.4);
        .remLineHeight(1.5);
        .remPaddingLeft(0.4);
        .remPaddingRight(0.5);
        .remTop(0);

        &:after {
            background-color: @hpbxColorCallLogCounterBackground;
            content: '';
            position: absolute;
            width: 1px;

            .remHeight(3.8);
            .remLeft(0.7);
            .remTop(1.4);
        }
    }
}

.hpbx-table-call-log-xs() {
    .hpbx-table-call-log {
        .hpbx-expandable-content-counter {
            &:after {
                .remHeight(2.6);
            }
        }
    }
}

@media @XS {
    .hpbx-table-call-log-xs();
}

.hpbx-table-call-log-sm() {
    .hpbx-table-call-log {
        .hpbx-table-selector-inputs {
            .sg-btn {
                margin-top: 0;
                width: auto;
            }

            .sg-btn-icon {
                display: inline;
            }
        }

        .hpbx-table-selector-subcaption {
            .remPaddingTop(1);
        }
    }
}

@media @SM {
    .hpbx-table-call-log-sm();
}

.ie8 {
    .hpbx-table-call-log-sm();
}