/******************************* INFORMATION ***************************

Project-specific styling that the checkbox and the radio button have in common.
     
************************************************************************/

/********************************** START ******************************/ 

.sg-custom-check-and-radio {
	.remMinHeight(@sgMiscSizeCheckbox);

	.sg-alternative-display {
		background-color: @sgColorCheckRadioBackground;
        border: 1px solid @sgColorCheckRadioBorder;
		min-height: 0;
		overflow: hidden;

		.box-sizing(border-box);
		.remHeight(@sgMiscSizeCheckbox);
		.remWidth(@sgMiscSizeCheckbox);
	}

    label {
		.remPadding(0, 0, 0, 4);

		.sg-caption {
            color: @sgColorCheckRadioCaption;

			.remLineHeight(@sgMiscSizeCheckbox * 1.1);
		}
	}

    &.sg-disabled {
        .sg-alternative-display {
            background-color: @sgColorCheckRadioBackgroundDisabled;
            border-color: @sgColorCheckRadioBorderDisabled;
        }

        label {
            .sg-caption {
                color: @sgColorCheckRadioCaptionDisabled;
            }
        }
    }
}
