.hpbx-initialize-variables-progress-circle(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorProgressCircleTitle: @sgColorPurple1;
    @hpbxColorProgressCircleTitleAmount: @sgColorPurple1;
}

.hpbx-initialize-variables-progress-circle(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-progress-circle('upc-business');
}

.hpbx-initialize-variables-progress-circle(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorProgressCircleTitle: @sgColorGrey1;
    @hpbxColorProgressCircleTitleAmount: @hpbxColorPrimary;
}

.hpbx-initialize-variables-progress-circle(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-progress-circle('ziggo');
}

.hpbx-initialize-variables-progress-circle(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-progress-circle('ziggo');
}

.hpbx-initialize-variables-progress-circle(@hpbxStyleguide);

.hpbx-progress-circle-container {
    text-align: center;

    .remMarginTop(4);
}

.hpbx-progress-circle {
    display: inline-block;
    position: relative;

    .remMarginBottom(0.6);

    &.hpbx-progress-circle-small {
        .hpbx-progress-circle-svg {
            .remHeight(8);
            .remWidth(8);
        }

        .hpbx-progress-circle-inner {
            font-family: @sgFontFamilyHeadings;
            
            .remFontSize(@sgFontSizeH2);
            .remTop(1);
        }
    }
}

.hpbx-progress-circle-svg {
    fill: @sgColorBodyBackground;
    overflow: visible;
    stroke: @hpbxColorPrimary;
    stroke-width: 2px;

    .remHeight(15);
    .remWidth(15);
}

.hpbx-progress-circle-background {
    fill: none;
    stroke: @sgColorGrey4;
    stroke-width: 1;
}

.hpbx-progress-circle-arc {
    fill: none;
}

.hpbx-progress-circle-inner {
    color: @sgColorGrey3;
    position: absolute;
    text-align: center;
    width: 100%;

    .remFontSize(3.6);
    .remTop(5.1);
}

.hpbx-progress-circle-title {
    color: @sgColorGrey2;
    font-family: @sgFontFamilyHeadings;
    text-align: center;

    .remFontSize(@sgFontSizeH5);
    .remMarginBottom(-0.4);
}

.hpbx-progress-circle-title-amount {
    color: @sgColorGrey2;
}

.hpbx-progress-circle-subtitle {
    color: @sgColorGrey2;
    text-align: center;

    .remFontSize(@sgFontSizeText);
}

.hpbx-progress-circle-md() {
    .hpbx-progress-circle-container {
        margin-top: 0;
    }
}

@media @MD {
    .hpbx-progress-circle-md();
}

.ie8 {
    .hpbx-progress-circle-md();
}
