/******************************* INFORMATION ***************************

Fonts and sizes
     
************************************************************************/

/********************************** START ******************************/ 

@baseFontPath: @sgBaseFontPath;

@font-face {
    font-family: 'trimm-icons';
    src: url('@{baseFontPath}trimm-icons.eot');
    src: url('@{baseFontPath}trimm-icons.eot?#iefix') format('embedded-opentype'),
         url('@{baseFontPath}trimm-icons.woff') format('woff'),
         url('@{baseFontPath}trimm-icons.ttf') format('truetype'),
         url('@{baseFontPath}trimm-icons.svg#trimm-icons') format('svg');
}

/* Fix for Chrome Font render issue http://www.adtrak.co.uk/blog/font-face-chrome-rendering/#fix */
@media screen and (-webkit-min-device-pixel-ratio:0) {
	@font-face {
		font-family: 'trimm-icons';
		src: url('@{baseFontPath}trimm-icons.svg#trimm-icons') format('svg');
	}
}