.sg-textfield-with-caption {
	display: inline-block;
	position: relative;

	.remHeight(@sgMiscHeightElements);
	.remWidth(18);

	.sg-label {
		display: inline-block;
		.remMarginTop(1.3);
	}

	input {
		position: absolute;

		.remMarginLeft(1.5);
		.remMarginRight(1.5);
		.remPaddingLeft(1.5);
		.remWidth(15);

		&:focus + .sg-caption {
			border-color: @sgColorTextfieldBorderFocus;
		}

		&.sg-error + .sg-caption {
			border-color: @sgColorTextfieldBorderError;
		}
	}

	.sg-caption {
		@innerRadius: @sgMiscBorderRadiusDefault - 0.1;
        /* TODO: Check less variables
		background-color: @sgColorBackground;
        */
		position: absolute;
		right: 0;

		.box-sizing(border-box);
        /* TODO: Check less variables
		.remBorderLeft(0.1, solid, @sgColorGrayLightLines);
        */
		.remBorderRightRadius(@innerRadius);
		.remHeight(@sgMiscHeightElements - 0.2);
		.remLineHeight(@sgMiscHeightElements - 0.1);
		.remPaddingLeft(1);
		.remTop(0.1);
		.remWidth(4.4);
		.transition(@sgMiscDurationTransitionDefault);
	}
}
