.hpbx-initialize-variables-tabs-inside-content(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorTabsInsideContentText:                  @sgColorPurple1;
    @hpbxColorTabsInsideContentActiveBackground:      @sgColorPurple1;
    @hpbxColorTabsInsideContentActiveText:            @sgColorPurple1;
    @hpbxColorTabsInsideContentActiveBackgroundHover: @sgColorPurple2;
}

.hpbx-initialize-variables-tabs-inside-content(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-tabs-inside-content('upc-business');
}

.hpbx-initialize-variables-tabs-inside-content(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorTabsInsideContentText:                  @hpbxColorPrimary;
    @hpbxColorTabsInsideContentActiveBackground:      @hpbxColorPrimary;
    @hpbxColorTabsInsideContentActiveText:            @hpbxColorPrimary;
    @hpbxColorTabsInsideContentActiveBackgroundHover: @sgColorOrangeExtra;
}

.hpbx-initialize-variables-tabs-inside-content(@styleguide) when (@styleguide = 'virgin') {
    @hpbxColorTabsInsideContentText:                  @hpbxColorPrimary;
    @hpbxColorTabsInsideContentActiveBackground:      @hpbxColorPrimary;
    @hpbxColorTabsInsideContentActiveText:            @hpbxColorPrimary;
    @hpbxColorTabsInsideContentActiveBackgroundHover: @sgColorRedHover;
}

.hpbx-initialize-variables-tabs-inside-content(@styleguide) when (@styleguide = 'vtr') {
    @hpbxColorTabsInsideContentText:                  @hpbxColorPrimary;
    @hpbxColorTabsInsideContentActiveBackground:      @hpbxColorPrimary;
    @hpbxColorTabsInsideContentActiveText:            @hpbxColorPrimary;
    @hpbxColorTabsInsideContentActiveBackgroundHover: @sgColorPinkExtra;
}

.hpbx-initialize-variables-tabs-inside-content(@hpbxStyleguide);


.hpbx-tabs.hpbx-tabs-inside-content {
    .r-tabs-accordion-title {
        background-color: @sgColorGrey6;

        .r-tabs-anchor {
            color: @sgColorGrey4;
        }

        &.r-tabs-state-active {
            background-color: @sgColorGrey7;
            border-left: 1px solid @sgColorGrey4;
            border-right: 1px solid @sgColorGrey4;
            border-top: 1px solid @sgColorGrey4;

            .r-tabs-anchor {
                color: @hpbxColorTabsInsideContentActiveText;
            }
        }
    }

    .r-tabs-panel {
        background-color: @sgColorGrey7;

        .remPadding(2, 2, 2, 2);
    }
}

.hpbx-tabs-inside-content-title {
    display: none;
    margin-top: 0;

    .remMarginBottom(0.7);
}

.hpbx-tabs-inside-content-table {
    width: 100%;

    &.hpbx-numbered {
        counter-reset: hpbx-tabs-inside-content-table;

        .hpbx-tabs-inside-content-table-cell {
            .remPaddingBottom(1.1);

            .remPaddingLeft(1.6);

            &:first-child {
                position: relative;


                &:before {
                    color: @hpbxColorPrimary;
                    content: counter(hpbx-tabs-inside-content-table) ".";
                    counter-increment: hpbx-tabs-inside-content-table;

                    position: absolute;
                    left: 0;

                    .remTop(0.1);
                }
            }
        }
    }
}

.hpbx-tabs-inside-content-buttons {
    .remMarginTop(0);
}

.hpbx-tabs-inside-content-xxs() {
    .hpbx-tabs-inside-content-table {
        &.hpbx-numbered {
            .hpbx-tabs-inside-content-table-cell {
                .remPaddingBottom(0.2);
                
                &:first-child {
                    &:before {
                        top: 0;
                    }
                }
            }
        }
    }
}

@media @XXS_MAX {
    .hpbx-tabs-inside-content-xxs();
}

.hpbx-tabs-inside-content-xs() {
    .hpbx-tabs-inside-content {
        .r-tabs-nav li, // Fallback
        .hpbx-tab-item {
            background-color: @sgColorGrey6;
            border-left: 1px solid @sgColorGrey4 !important;
            border-top: 1px solid @sgColorGrey4 !important;

            .r-tabs-anchor {
                color: @sgColorGrey4;
            }

            &:hover {
                background-color: @sgColorGrey5;
            }

            &.r-tabs-state-active {
                background-color: @sgColorGrey7;
                border-bottom: 1px solid @sgColorGrey7;

                &:hover {
                    background-color: @sgColorGrey7;
                }

                &:last-child {
                    border-right: 1px solid @sgColorGrey4;
                }

                .r-tabs-anchor {
                    color: @hpbxColorTabsInsideContentActiveText;
                }
            }
        }
    }

    .hpbx-tabs-inside-content-title {
        display: block;
    }
}


@media @XS {
    .hpbx-tabs-inside-content-xs();
}

.ie8 {
    .hpbx-tabs-inside-content-xs;
}