.hpbx-percentage-display {
    position: relative;
}

.hpbx-percentage-display-background-line {
    stroke: @sgColorGrey4;
}

.hpbx-percentage-display-svg {
    fill: @sgColorBodyBackground;
    overflow: visible;
    stroke: @hpbxColorPrimary;
    stroke-width: 2px;

    .remHeight(1.2);
}

