body.hpbx-page-help-edit {
    background-color: @sgColorGrey6;

    .remPadding(13.1);

    .hpbx-scrollable-container {
        .remTop(13.1);
    }
}

body.hpbx-page-help-edit-components {
    padding-top: 0;

    .hpbx-scrollable-container {
        top: 0;
    }
}

.hpbx-help-edit-page-title {
    color: @sgColorGrey2;

    .remMarginTop(5);
}

.hpbx-help-edit-container-title {
    color: @sgColorGrey2;

    .remMarginBottom(1.2);
    .remMarginTop(4);
}

.hpbx-help-files-container-title {
    .remMarginBottom(1.2);
    .remMarginTop(4);
}

.hpbx-help-edit-header {
    background-color: @sgColorGrey5;
    border-top: 1px solid @sgColorGrey4;

    .box-sizing(border-box);
    .remMinHeight(3.9);
}

.hpbx-help-edit-header-icon {
    color: @sgColorGrey3;
    display: inline-block;

    .remFontSize(1.6);

    &:hover {
        cursor: pointer;
    }
}

.hpbx-help-edit-header-icon-left {
    .remPaddingLeft(1.3);
    .remPaddingTop(1.1);
}

.hpbx-help-edit-header-icon-right {
    text-align: right;

    .remPaddingTop(1.1);
}

.hpbx-help-edit-header-title {
    color: @sgColorGrey3;

    .remPaddingTop(1);
}

.hpbx-help-edit-tabbed-content {
    .hpbx-tabs {
        margin: 0;

        .r-tabs-nav {
            position: fixed;

            .remTop(8.1);
            z-index: 1;
        }

        .r-tabs-panel {
            background-color: transparent;
            border: 0;
            padding: 0;

            &.hpbx-help-edit-details {
                .remPaddingBottom(1.1);
                .remPaddingTop(1.7);

                overflow: visible;
            }
        }
    }

    .hpbx-tabs-inside-content .hpbx-tab-item {
        background-color: @sgColorGrey7;
        border-top: none !important;
        border-radius: 0 !important;

        &:first-child {
            border-left: 0 !important;
        }

        &:last-child {
            border-right: 0;
        }

        &.r-tabs-state-active {
            background-color: @sgColorWhite;
            border-bottom: 1px solid @sgColorGrey4;

            &:hover {
                background-color: @sgColorWhite;
                cursor: default;

                .r-tabs-anchor {
                    cursor: default;
                }
            }

            &:last-child {
                background-color: @sgColorGrey5;

                &:hover {
                    background-color: @sgColorGrey5;
                }
            }
        }

        .r-tabs-anchor {
            color: @hpbxColorTabsInsideContentActiveText;
        }
    }

    .hpbx-tab-title {
        display: inline;

        .remMarginLeft(0.8);
    }

    .hpbx-tab-icon {
        .remFontSize(1.6);
    }
}

.hpbx-help-edit-sitemap {
    li:before {
        content: '';
    }
}

.jstree-node {
    font-weight: normal;
}

.jstree-container-ul {
    color: @sgColorGrey2;

    &> .jstree-node {
        font-weight: bold;
    }
}

.jstree-default {
    .jstree-clicked {
        background-color: @sgColorGrey5;
    }
}

.jstree-contextmenu {
    background-color: @sgColorWhite;
    border-color: @sgColorGrey4;
    padding: 0;

    .box-shadow(0 2px 2px rgba(0,0,0,.15));
    .remBorderRadiusAll(0.3);
    .remMinWidth(22);

    li {
        padding: 0;

        &:before {
            content: '';
        }

        &.vakata-context-hover {
            a {
                background-color: @sgColorGrey6;

                .box-shadow(none);
            }
        }

        a {
            color: @sgColorGrey2;
            
            .remPaddingLeft(0.4);
            .remPaddingRight(0.4);
        }
    }

    .vakata-contextmenu-sep {
        display: none !important;
    }
}

.hpbx-help-edit-details {
    background-color: @sgColorGrey5 !important;
    color: @sgColorGrey2;

    .remFontSize(1.2);
    .remLineHeight(2.5);

    .hpbx-help-edit-details-toggle-row {
        .remMinHeight(3.4);
    }

    .hpbx-help-edit-details-custom-select-row {
        .remMinHeight(@sgMiscHeightElements + 0.6);

        .chzn-container {
            width: 100% !important;
        }

        .chzn-drop {
            width: 100%;
            width: ~"calc(100% - 2px)" !important;
        }
    }

    .sg-custom-toggle {
        label {
            .remLineHeight(2.5);
        }
        
        .sg-caption {
            padding-left: 0;

            .remLineHeight(2.5);
        }

        .sg-alternative-display {
            left: auto;
            position: absolute;
            right: 0;
            top: -3px;
        }
    }
}

.hpbx-help-edit-details-left {
    font-weight: bold;
    text-align: right;
}

.hpbx-help-edit-edit-icon {
    cursor: pointer;
    display: inline-block;
    float: right;

    .remPaddingTop(2.3);

    &:hover {
        color: @hpbxColorPrimary;
    }
}

.hpbx-help-edit-add {
    cursor: default;
    position: relative;

    .remMinHeight(5);
    .transition(@sgMiscDurationTransitionDefault);

    &:hover {
        .opacity(1);
    }

    &+ .hpbx-help-content {
        padding-bottom: 0;
        padding-top: 0;
    }
}

.hpbx-help-edit-add-links-container {
    position: absolute;
    text-align: center;
    width: 100%;

    .remTop(1.5);
}

.hpbx-help-edit-add-links {
    background-color: @sgColorGrey6;

    .remPaddingLeft(2.4);
    .remPaddingRight(2.4);
}

.hpbx-help-edit-add-line {
    background-color: @sgColorGrey4;
    height: 1px;
    left: 0;
    position: absolute;
    top: 50%;
    width: 100%;
}

.hpbx-help-edit-add-link {
    color: @sgColorGrey3;
    cursor: pointer;
    font-family: @sgFontFamilyHeadings;
    text-transform: uppercase;

    .remFontSize(1.4);
    .remPaddingLeft(0.8);
    .remPaddingRight(0.8);

    &:hover {
        color: @hpbxColorPrimary;
    }
}

.hpbx-wysiwyg-toolbar {
    .remPaddingBottom(0.5);
    .remPaddingTop(0.5);

    .sg-btn {
        min-width: 0;
    }
}

.hpbx-wysiwyg-toolbar-dialog {
    .remPaddingBottom(0.5);
    .remPaddingTop(1);

    input[type="text"] {
        .remMinHeight(2.6);
    }
}

.hpbx-help-toolbar-dialog-row {
    .remMarginBottom(0.5);
    .remMarginTop(0.5);
}

.hpbx-help-edit-link-button {
    font-family: @sgFontFamilyHeadings;
    color: @sgColorGrey2;
    cursor: pointer;

    .remFontSize(1.6);
}

.hpbx-help-edit-footer {
    background-color: @sgColorGrey7;
    border-bottom: 1px solid @sgColorGrey4;

    .box-sizing(border-box);
    .remMinHeight(3.9);
    .remPaddingTop(0.5);

    .hpbx-help-edit-link-button {
        display: inline-block;

        .remMarginTop(0.4);
    }
}

.hpbx-help-edit-footer-left {
    text-align: left;

    .sg-btn {
        .remMarginRight(1.5);
    }
}

.hpbx-help-edit-footer-right {
    text-align: right;

    .hpbx-help-edit-link-button {
        .remMarginLeft(1.5);
    }
}

.hpbx-help-edit-main {
    background-color: @sgColorWhite;
    color: @sgColorGrey2;

    .remPaddingBottom(2.1);
    .remPaddingTop(2.6);

    & + .hpbx-help-edit-main {
        border-top: none !important;
    }

    .hpbx-file-upload-link {
        color: inherit;
    }
}

.hpbx-help-edit-main-icon-container {
    text-align: right;
}

.hpbx-help-edit-main-icon {
    color: @sgColorGrey3;

    &:hover {
        cursor: pointer;
    }
}

.hpbx-wysiwyg {
    &:focus {
        outline: 0;
    }

    &> *:first-child {
        margin-top: 0;
    }
}

.hpbx-help-edit-wysiwyg {
    .hpbx-help-edit-main {
        border-bottom: 1px solid @sgColorGrey5;
        border-top: 1px solid @sgColorGrey4;
    }
}

.hpbx-help-edit-video {
    .hpbx-help-edit-main {
        border-bottom: 1px solid @sgColorGrey5;
        border-top: 1px solid @sgColorGrey4;
    }
}

.hpbx-help-edit-photos {
    .hpbx-help-edit-main {
        border-bottom: 1px dashed @sgColorGrey5;
        border-top: 1px dashed @sgColorGrey4;
    }
}

.hpbx-help-edit-files {
    .hpbx-help-edit-main {
        border-bottom: 1px dashed @sgColorGrey5;
        border-top: 1px dashed @sgColorGrey4;
    }
}

.hpbx-help-edit-instruction {
    .hpbx-help-edit-main {
        border-bottom: 1px dashed @sgColorGrey5;
        border-top: 1px solid @sgColorGrey4;

        &.hpbx-saved {
            border-bottom: none;
        }
    }

    .hpbx-help-filelist-items {
        .remMarginTop(-0.3);
    }

    .hpbx-help-filelist-item {
        .remMarginBottom(1);
        .remMarginTop(1);
    }

    .hpbx-help-filelist-item-info {
        .remPaddingLeft(8.8);
    }

    .hpbx-help-step-substep {
        list-style-type: none;
        padding-left: 0;

        &:before {
            .remLeft(-0.9);
        }
    }
}


.hpbx-help-edit-open-media-library {
    color: @sgColorGrey3;
    float: right;
    line-height: 1;
    text-decoration: none;

    .remFontSize(1.6);
}

input[type="text"].hpbx-help-edit-saved-item-tag-input {
    min-height: 0;
    width: 100%;    

    .remHeight(2.8);
}


.hpbx-help-edit-instruction-step-number {
    color: @hpbxColorPrimary;

    &:after {
        content: '.';
    }
}