h1, .sg-header-1 {
	.sg-calculate-margins(@sgFontSizeH1, @sgLineHeightH1, @sgMarginTopH1);
	
	color: @sgColorH1;
	font-family: @sgFontFamilyH1;
	font-weight: normal;
	line-height: @sgLineHeightH1;
	text-transform: @sgTextTransformH1;

	.remFontSize(@sgFontSizeH1);

	@media @XS_MAX {
		.sg-calculate-margins(@sgFontSizeH2, @sgLineHeightH2, @sgMarginTopH2);
		
		.remFontSize(@sgFontSizeH2);
		word-break: break-all;
	}
}
