.sg-slider-button {
    background-color: @sgColorSliderButtonBackground;
    color: @sgColorSliderButtonText;
    overflow: hidden;
    position: relative;
    width: 100%;

    .remBorderRadiusAll(@sgMiscBorderRadiusDefault);
    .remHeight(@sgMiscHeightElements);

    &.sg-border-reached {
        .sg-slider-button-items {
            .transition-duration(0.5s);
        }
    }
}

.sg-slider-button-items {
    font-size: 0;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    white-space: nowrap;
    width: 100%;

    .transition(@sgMiscDurationTransitionDefault cubic-bezier(0.55, 0.055, 0.675, 0.19));
}

.sg-slider-button-item {
    display: inline-block;
    text-align: center;
    width: 100%;

    .remFontSize(@sgFontSizeCta);
    .remPaddingTop(@sgPaddingSliderButtonTop);
}

.sg-slider-button-control {
    background-color: @sgColorSliderButtonControlBackground;
    border: 1px solid @sgColorSliderButtonControlBorder;
    cursor: pointer;
    height: 100%;
    position: absolute;
    top: 0;

    .remBorderRadiusAll(@sgMiscBorderRadiusDefault);
    .remWidth(@sgMiscHeightElements);
    .transition(@sgMiscDurationTransitionDefault);

    &:hover {
        background-color: @sgColorSliderButtonControlBackgroundHover;
        border: 1px solid @sgColorSliderButtonControlBorderHover;

        &:before {
            color: @sgColorSliderButtonControlIconHover;
        }
    }

    &:before {
        color: @sgColorSliderButtonControlIcon;
        font-family: trimm-icons;
        position: absolute;

        .remLeft(1.3);
        .remTop(0.7);
    }
}

.sg-slider-button-control-back {
    left: 0;

    &:before {
        content: '\e649';
    }
}

.sg-slider-button-control-forward {
    right: 0;

    &:before {
        content: '\e64a';
    }
}
