.hpbx-initialize-variables-audio(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorAudioContainerBackgroundProgress:   @sgColorGrey3b;

    @hpbxPaddingBottomAudioControlIconContainer: 0.7;
    @hpbxPaddingBottomAudioTimeContainerHover: 0.7;
    @hpbxPaddingTopAudioControlIconContainer: 1.4;
    @hpbxPaddingTopAudioTimeContainerHover: 0.7;
    @hpbxRightAudioControlIcon: -0.7;
    @hpbxTopAudioControlIcon: 1.3;

    @hpbxRightAudioDeleteIcon: 2;
    @hpbxTopAudioDeleteIcon: 0.6;

    @hpbxAudioSelectorLineHeight: @sgLineHeightText;
}

.hpbx-initialize-variables-audio(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-audio('upc-business');
}

.hpbx-initialize-variables-audio(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorAudioContainerBackgroundProgress:   @sgColorGrey2;

    @hpbxPaddingBottomAudioControlIconContainer: 0.9;
    @hpbxPaddingBottomAudioTimeContainerHover: 1.6;
    @hpbxPaddingTopAudioControlIconContainer: 1.2;
    @hpbxPaddingTopAudioTimeContainerHover: 1.6;
    @hpbxRightAudioControlIcon: -1;
    @hpbxTopAudioControlIcon: 1.4;

    @hpbxRightAudioDeleteIcon: 2;
    @hpbxTopAudioDeleteIcon: 1.6;

    @hpbxAudioSelectorLineHeight: 0;
}

.hpbx-initialize-variables-audio(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-audio('ziggo');

    @hpbxAudioSelectorLineHeight: 0;
}

.hpbx-initialize-variables-audio(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-audio('ziggo');
}

.hpbx-initialize-variables-audio(@hpbxStyleguide);

.hpbx-tab-audio-container {
    background-color: transparent !important;
    position: relative;

    .hpbx-table {
        tbody {
            background-color: transparent;

            .hpbx-table-audio-container {
                background-color: @sgColorWhite;

                td {
                    .remPaddingBottom(0.9);
                    .remPaddingTop(1.2);

                    &.hpbx-audio-control-icon {
                        .remPaddingBottom(@hpbxPaddingBottomAudioControlIconContainer);
                        .remPaddingTop(@hpbxPaddingTopAudioControlIconContainer);
                    }
                }
            }
        }

        thead {
            th {
                .sg-custom-check {
                    margin: 0;
                }
            }
        }
    }
}

.hpbx-table-audio-container {
    &.hpbx-audio-is-active {
        background-color: transparent !important;
        position: relative;

        &:hover {
            .hpbx-audio-control-icon:after {
                color: @sgColorWhite;
            }
        }

        td {
            color: @sgColorWhite;
        }

        .hpbx-audio-control-icon:after {
            content: "\e61d";
        }
    }

    &.hpbx-audio-is-paused {
        .hpbx-audio-control-icon:after {
            content: "\e61c";
        }
    }

    td {
        .remBorderRadius(0) !important;
    }
}

.hpbx-audio-control-icon {
    position: relative;

    &:after {
        color: @sgColorWhite;
        content: '\e61c';
        position: absolute;

        .remFontSize(1.6);
        .remRight(@hpbxRightAudioControlIcon);
        .remTop(@hpbxTopAudioControlIcon);
        .sg-lgi-icon-font();
    }

    .sg-custom-check {
        .remMarginBottom(0.6);
        .remMarginTop(-0.1);
    }
}

.hpbx-audio-background-active {
    background-color: @sgColorGrey3;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -2;
}

.hpbx-audio-progress-bar {
    background-color: @hpbxColorAudioContainerBackgroundProgress;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 0;
    z-index: -1;
}

.hpbx-audio-content {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 3;
}

.hpbx-audio-title {
    color: @sgColorGrey2;
    font-family: @sgFontFamilyHeadings;

    .remFontSize(@sgFontSizeH1);
    .remPaddingLeft(5.3);
    .remPaddingRight(12);
    .remPaddingTop(1.1);
    .text-overflow();
}

.hpbx-audio-duration {
    color: @sgColorGrey2;
    font-family: @sgFontFamilyH1;
    position: absolute;

    .remFontSize(@sgFontSizeText);
    .remRight(7.5);
    .remTop(1.6);
}

.hpbx-audio-container {
    cursor: pointer;

    &:hover {
        .hpbx-audio-control-icon:after {
            color: @hpbxColorPrimary;
        }
    
        &.hpbx-audio-container-no-delete {
            .hpbx-audio-duration {
                display: block;
            }
        }

        .hpbx-table-voicemail-time {
            .remPaddingBottom(@hpbxPaddingBottomAudioTimeContainerHover);
            .remPaddingTop(@hpbxPaddingTopAudioTimeContainerHover);
        }
    }

    &.hpbx-audio-is-active {
        .hpbx-audio-background-active {
            display: block;
        }

        .hpbx-audio-title,
        .hpbx-audio-duration,
        .hpbx-audio-control-icon:after {
            color: @sgColorWhite;
        }

        .hpbx-audio-control-icon:after {
            content: "\e61d";
        }
    }

    &.hpbx-audio-is-paused {
        .hpbx-audio-control-icon:after {
            content: "\e61c";
        }
    }

    embed {
        display: none;
    }
}

.hpbx-upload-audio-container {
    &+ .hpbx-audio-selector-container {
        display: none;
    }

    &.hpbx-hidden {
        display: none;
    }

    &.hpbx-disabled {
        .hpbx-audio-container {
            cursor: default;
        }

        .hpbx-audio-title {
            color: @sgColorGrey4;

            .remPaddingLeft(2);
        }

        .hpbx-audio-control-icon,
        .hpbx-delete {
            display: none !important;
        }

        .hpbx-audio-duration {
            color: @sgColorGrey4;
            display: block !important;
        }
    }
}

.hpbx-audio-container-singular {
    .hpbx-audio-control-icon {
        position: absolute;
        top: 0;

        .remHeight(1.6);
        .remLeft(1.4);
        .remWidth(1.6);

        &:after {
            color: @sgColorGrey2;
        }
    }

    .hpbx-delete {
        position: absolute;

        .remRight(@hpbxRightAudioDeleteIcon);
        .remTop(@hpbxTopAudioDeleteIcon);
    }

    .hpbx-audio-is-replaced {
        display: none;
    }
}

.hpbx-audio-selector-container {
    &.hpbx-audio-is-selected {
        .sg-btn-file-input,
        .sg-btn-file-input + .sg-btn-icon {
            display: none;
        }

        .hpbx-delete {
            .remMarginTop(0.9);
        }
    }

    .hpbx-tab-form-label {
        .remPaddingTop(0.6);
    }

    .sg-btn-file-input-label {
        margin-left: 0;
    }

    .hpbx-delete {
        display: none;
        float: right;
    }
}

.hpbx-audio-selector {
    display: inline;

    .remLineHeight(3.4);

    .sg-btn {
        line-height: @hpbxAudioSelectorLineHeight;
    }
}

.hpbx-audio-selector-library-icon {
    color: @sgColorGrey3;

    .remFontSize(1.6);
    .remMarginLeft(0.6);
}

.hpbx-audio-selector-library-link {
    color: @sgColorGrey2;
}

.hpbx-audio-selector-no-audio-label {
    display: none;
}

@media @MD {
    .hpbx-audio-md();
}

.hpbx-audio-md() {
    .hpbx-audio-selector-container {
        &.hpbx-audio-is-selected {
            .hpbx-delete {
                display: block;
            }
        }
    }
}

.ie8 {
    .hpbx-audio-md();

    .hpbx-table-audio-container {
        background-color: @sgColorWhite !important;

        td {
            color: @sgColorGrey2;
            cursor: default;
        }

        embed {
            display: inline-block;
            margin-left: 20px;
        }
    }

    .hpbx-audio-fallback {
        display: block;
        position: absolute;

        .remPadding(1.5,0,1.5, 2.2);

        embed {
            display: block;
        }

        &~.hpbx-audio-title {
            .remPaddingLeft(15);
        }
    }

    .hpbx-audio-control-icon:after {
        display: none;
    }
}