.hpbx-download-category-title {
    color: @sgColorGrey2;

    .remMarginBottom(-0.5);
    .remMarginTop(3.4);
    .remPaddingLeft(4.4);
}

.hpbx-download-item {
    background-color: @sgColorWhite;
    border: solid 1px @sgColorGrey4;
    display: block;
    overflow: hidden;
    position: relative;
    text-decoration: none;

    .box-shadow(0 5px 10px rgba(204,208,218,.5));
    .remBorderRadiusAll(0.5);
    .remHeight(7);
    .remMarginBottom(2);

    &:hover {
        &.hpbx-download-mobile {
            .hpbx-download-item-icon {
                background-color: @hpbxColorPrimary;
            }
        }

        .hpbx-download-item-icon {
            color: @hpbxColorPrimary;
        }

        .hpbx-download-item-caption {
            color: @hpbxColorPrimary;
        }
    }

    &.hpbx-download-mobile {
        .hpbx-download-item-icon {
            background-color: @sgColorGrey3;
            color: @sgColorGrey7;
        }

        .hpbx-download-item-title {
            .remMarginTop(0.4);
        }

        .hpbx-download-item-subtitle {
            .remMarginTop(0.1);
        }
    }
}

.hpbx-download-item-icon {
    background-color: @sgColorGrey7;
    border-right: solid 1px @sgColorGrey4;
    color: @sgColorGrey3;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;

    .remWidth(6.9);
    .transition(@sgMiscDurationTransitionDefault);

    &:before {
        position: absolute;

        .remFontSize(3.9);
        .remLeft(1.5);
        .remTop(1.4);
    }
}

.hpbx-download-item-caption {
    color: @sgColorGrey3;

    .remPaddingLeft(8.8);
    .remPaddingTop(1.4);
}

.hpbx-download-item-title {
    font-family: @sgFontFamilyHeadings;
    line-height: 1;

    .remFontSize(@sgFontSizeH4);
}

.hpbx-download-item-subtitle {
    font-family: @sgFontFamilyText;
    line-height: 1;

    .remMarginTop(0.2);
}

