input[type="text"],
input[type="email"],
input[type="password"],
textarea {
    &.hpbx-incognito {
        background-color: transparent;
        border: none;
        color: @sgColorGrey2;
        font-weight: bold;
        padding-left: 0;
        padding-right: 0;

        &:hover {
            background-color: transparent;
            color: @sgColorGrey2;
        }
    }
}

.hpbx-label-after-text-field {
    .remLineHeight(3.5);
    .remMarginLeft(0.7);
}