.hpbx-initialize-variables-pagination(@styleguide) when (@styleguide = 'upc-business') {
    @hpbxColorPaginationInnerBorder:       @sgColorGrey5;
}

.hpbx-initialize-variables-pagination(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-initialize-variables-pagination('upc-business');
}

.hpbx-initialize-variables-pagination(@styleguide) when (@styleguide = 'ziggo') {
    @hpbxColorPaginationInnerBorder:       @sgColorGrey4;
}

.hpbx-initialize-variables-pagination(@styleguide) when (@styleguide = 'virgin') {
    .hpbx-initialize-variables-pagination('ziggo');
}

.hpbx-initialize-variables-pagination(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-initialize-variables-pagination('ziggo');
}

.hpbx-initialize-variables-pagination(@hpbxStyleguide);

@hpbxHeightPagination:                 4.5;
@hpbxRadiusPaginationOuterBorder:      0.4;
@hpbxPaddingTopPagination:             0.9;

.hpbx-pagination-container {
    text-align: center;
}

.hpbx-pagination {
    display: inline-block;
    overflow: hidden;

    .box-sizing(border-box);
    .remHeight(4.5);
}

.hpbx-pagination-item {
    border-bottom: 1px solid @sgColorGrey4;
    border-top: 1px solid @sgColorGrey4;
    display: none;
    float: left;
    font-family: @sgFontFamilyHeadings;
    height: 100%;
    margin: 0;
    padding: 0;
    text-align: center;

    .remFontSize(1.8);
    .remWidth(@hpbxHeightPagination);

    &:before {
        content: "";
    }

    &:nth-last-child(2) {
        border-right: none;
    }

    &:first-child {
        border-left: 1px solid @sgColorGrey4;

        .remBorderLeftRadius(@hpbxRadiusPaginationOuterBorder);

        .hpbx-pagination-item-content {
            border-left: none;
        }
    }

    &:last-child {
        border-right: 1px solid @sgColorGrey4;

        .remBorderRightRadius(@hpbxRadiusPaginationOuterBorder);
    }

    &:first-child,
    &:last-child {
        .hpbx-pagination-item-content {
            .remLineHeight(@hpbxHeightPagination + 0.3);
        }
    }

    &:hover {
        background-color: @sgColorWhite;
    }

    &.hpbx-pagination-item-start,
    &.hpbx-pagination-item-before-current,
    &.hpbx-pagination-item-current,
    &.hpbx-pagination-item-after-current,
    &.hpbx-pagination-item-end {
        display: list-item;
    }

    .hpbx-pagination-item-content {
        border-left: 1px solid @hpbxColorPaginationInnerBorder;
        color: @sgColorGrey3;
        display: block;
        text-decoration: none;

        .remLineHeight(@hpbxHeightPagination + 0.1);
    }
}


.hpbx-pagination-item-start,
.hpbx-pagination-item-end {
    .remFontSize(1.4);
}

.hpbx-pagination-item-unavailable {
    .hpbx-pagination-item-content {
        color: @hpbxColorPaginationInnerBorder;
    }

    &:hover {
        background-color: @sgColorBodyBackground;
    }
}

.hpbx-pagination-item-current {
    background-color: @sgColorGrey3;
    border: 1px solid @sgColorGrey3 !important;

    .hpbx-pagination-item-content {
        border: none;
        color: @sgColorWhite;
    }

    &:hover {
        background-color: @sgColorGrey3;
    }

    &+ .hpbx-pagination-item {
        .hpbx-pagination-item-content {
            border-left: none;
        }
    }
}

.hpbx-pagination-xs() {
    .hpbx-pagination-item {
        display: list-item;

        &:first-child {
            border-left: 1px solid @sgColorGrey4;

            .remBorderLeftRadius(@hpbxRadiusPaginationOuterBorder);

            .hpbx-pagination-item-content {
                border-left: none;
            }
        }

        &:nth-child(2) {
            border-left: 1px solid @hpbxColorPaginationInnerBorder;;
    
            .remBorderLeftRadius(0);
        }

        &:nth-last-child(2) {
            border-right: none;

            .remBorderRightRadius(0);
        }

        &:last-child {
            border-right: 1px solid @sgColorGrey4;

            .remBorderRightRadius(@hpbxRadiusPaginationOuterBorder);
        }
    }
}

@media @XS {
    .hpbx-pagination-xs();
}

.ie8 {
    .hpbx-pagination-xs();
}