.hpbx-apply-styleguide-specific-styling-header(@styleguide) when (@styleguide = 'upc-business') {
    .hpbx-header-sm() {
        .hpbx-header-logo {
            .remHeight(5.4);
            .remMarginTop(2.4);
        }

        .hpbx-header-background-image {
            background: url("../../dist/img/company-logo-background-@{hpbxStyleguide}.png") no-repeat center top;
            background-color: transparent;
            background-size: contain;
            display: block;
            height: 550px;
            left: 0;
            position: absolute;
            top: 0;
            width: 600px;
            z-index: -2;
        }
    }

    @media @SM {
        .hpbx-header-sm();
    }
}

.hpbx-apply-styleguide-specific-styling-header(@styleguide) when (@styleguide = 'unitymedia') {
    .hpbx-apply-styleguide-specific-styling-header('upc-business');
}

.hpbx-apply-styleguide-specific-styling-header(@styleguide) when (@styleguide = 'ziggo') {
    .hpbx-header-sm() {
        .hpbx-header-logo {
            .remHeight(5.4);
            .remMarginTop(2.4);
        }
    }

    @media @SM {
        .hpbx-header-sm();
    }
}

.hpbx-apply-styleguide-specific-styling-header(@styleguide) when (@styleguide = 'vtr') {
    .hpbx-apply-styleguide-specific-styling-header('ziggo');
}

.hpbx-apply-styleguide-specific-styling-header(@hpbxStyleguide);

