.hpbx-header-md(){
    .hpbx-header-logo{
        padding-bottom: 90px;    
        background: url("../../dist/img/company-logo-big-@{hpbxStyleguide}.png") 3% 8% no-repeat, url("../../dist/img/company-logo-@{hpbxStyleguide}.jpg") left bottom no-repeat;
