body {
    overflow-x: hidden;

    .transition(0.4s ease-in);

    &.hpbx-page-help {
        .transition(none);
    }
}

body.hpbx-page-help {
    .remPaddingTop(8.1);

    &.hpbx-help-prepare-animation {
        .hpbx-scrollable-container {
            .remTop(18.1);
        }
    }

    .row {
        margin-bottom: 0;
        margin-top: 0;
    }

    .hpbx-scrollable-container {
        position: fixed;
        bottom:0;
        width: 100%;

        .remTop(8.1);
    }

    &> .hpbx-scrollable-container {
        .transition(top 0.4s ease-out);
    }
}

body.hpbx-help-opened {
    .transition(0.5s ease-out);

    .hpbx-help {
        display: block;

        .transition(left 0.5s ease-out);
    }

    .hpbx-menu {
        .transition(z-index 0s);
    }
}

body.hpbx-page-wizard {
    .hpbx-help-open-handle-content-section {
        color: @sgColorWhite;

        .remFontSize(2.8);
        .remRight(@hpbxRightHelpOpenIconWizard+1);
        .remTop(@hpbxTopHelpOpenIconWizard);
    }

    .hpbx-help-edit-url-popup-trigger {
        color: @sgColorGrey7;

        .remFontSize(1.6);
        .remRight(@hpbxRightHelpOpenIconWizard + 4.5);
        .remTop(@hpbxTopHelpOpenIconWizard + 1);
    }
}

body.hpbx-no-menu {
    .hpbx-help-open-handle-content-section {
        .remRight(@hpbxRightHelpOpenIconNoMenuPage);
    }

    .hpbx-help-edit-url-popup-trigger {
        .remRight(@hpbxRightHelpOpenIconNoMenuPage + 4.8);
    }
}

.hpbx-help-open-handle-content-section {
    color: @sgColorWhite;
    cursor: pointer;
    display: none;
    position: absolute;
    z-index: 1100;

    .remTop(0.6);
    .remFontSize(2.4);
    .remRight(2);

    .transition(@sgMiscDurationTransitionDefault);

    &:hover {
        color: @sgColorWhite;

        .scale(1.1, 1.1);
    }
}

.hpbx-help-edit-url-popup-trigger {
    color: @sgColorWhite;
    cursor: pointer;
    display: none;
    position: absolute;
    z-index: 1100;

    .remTop(1.3);
    .remFontSize(1.4);
    .remRight(6);

    .transition(@sgMiscDurationTransitionDefault);

    &:hover {
        color: @sgColorWhite;

        .scale(1.1, 1.1);
    }
}

.hpbx-help {
    background-color: @sgColorWhite;
    border-right: 1px solid @hpbxColorHelpBorder;
    bottom: 0;
    display: none;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1104;

    .box-sizing(border-box);
    .transition(0.4s ease-in);
}

.hpbx-help-morph-shape {
    fill: @sgColorWhite;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    z-index: -1;
}

.hpbx-help-iframe {
    border: none;
}

.hpbx-help-content {
    .remFontSize(@sgFontSizeText);
    .remPaddingBottom(3);
    .remPaddingTop(4.3);
}

input[type="text"].hpbx-help-edit-input-incognito {
    border: none;
    color: @sgColorGrey2;
    padding: 0;
    width: 100%;

    .placeholder(@sgColorGrey2);
}

.hpbx-help-xxs() {
    .hpbx-help-content {
        padding-bottom: 0;
    }

    .hpbx-help-category-container {
        .remMargin(4, 2, 4, 2);
    }
}

@media @XXS_MAX {
    .hpbx-help-xxs();
}

.hpbx-help-xs() {
    .hpbx-help-open-handle-content-section,
    .hpbx-help-edit-url-popup-trigger {
        display: block;
    }
}

@media @XS {
    .hpbx-help-xs();
}

.hpbx-help-sm() {
    .hpbx-help-open-handle-content-section {
        .remTop(0.2);
        .remFontSize(3.0);
        .remRight(2);
    }

    .hpbx-help-edit-url-popup-trigger {
        .remTop(1.5);
        .remFontSize(1.5);
        .remRight(6);
    }

    body.hpbx-page-wizard {
        .hpbx-help-open-handle-content-section {
            .remRight(@hpbxRightHelpOpenIconWizard + 2.2);
            .remTop(@hpbxTopHelpOpenIconWizard + 1);
        }

        .hpbx-help-edit-url-popup-trigger {
            .remRight(@hpbxRightHelpOpenIconWizard + 6);
            .remTop(@hpbxTopHelpOpenIconWizard + 2.2);
        }
    }

    body.hpbx-no-menu {
        .hpbx-help-open-handle-content-section {
            .remRight(@hpbxRightHelpOpenIconNoMenuPage + 2.2);
        }

        .hpbx-help-edit-url-popup-trigger {
            .remRight(@hpbxRightHelpOpenIconNoMenuPage + 7.2);
        }
    }
}

@media @SM {
    .hpbx-help-sm();
}

.hpbx-help-md() {
    .hpbx-help-open-handle-content-section {
        .remRight(2);
    }
    
    .hpbx-help-edit-url-popup-trigger {
        .remRight(6);
    }

    body.hpbx-help-opened {
        .hpbx-help {
            left: 0;
        }
    }
}

@media @MD {
    .hpbx-help-md();
}

.hpbx-help-md-only() {
    body.hpbx-help-opened {
        .container {
            margin: 0 !important;
        }
    }
}

@media @MD_ONLY {
    .hpbx-help-md-only();
}

.hpbx-help-lg() {
    .hpbx-help {
        display: block;

        .remLeft(-@hpbxHelpWidth);
        .remWidth(@hpbxHelpWidth);
    }

    body.hpbx-help-opened:not(.hpbx-page-wizard) {
        .remMarginLeft(@hpbxHelpWidth);
        .remMarginRight(-@hpbxHelpWidth);

        &.hpbx-too-little-space {
            margin-right: 0;
            overflow-x: scroll;

            &.hpbx-no-menu {
                .hpbx-help-open-handle-content-section {
                    .remLeft(166.2);
                }

                .hpbx-help-edit-url-popup-trigger {
                    .remLeft(163.1);
                }

                .container {
                    .remPaddingRight(2);
                }
            }

            .container {
                .remPaddingRight(29);
            }

            .hpbx-menu {
                position: absolute;

                .remLeft(148.1);
                .remRight(-25);
            }

            .hpbx-help-open-handle-content-section {
                .remLeft(141.2);
            }

            .hpbx-help-edit-url-popup-trigger {
                .remLeft(138.1);
            }
        }

        .hpbx-body-background-image {
            .remLeft(@hpbxHelpWidth);
        }

        .hpbx-menu {
            .transition(none);
        }
    }

    body:not(.hpbx-no-menu) {
        .hpbx-help-open-handle-content-section {
            .remRight(2);
        }

        .hpbx-help-edit-url-popup-trigger {
            .remRight(6);
        }
    }

    body.hpbx-help-opened:not(.page-hpbx-devices) {
        margin-right: 0;
    }    
}

@media @LG {
    .hpbx-help-lg();
}

.ie8 {
    .hpbx-help-xs();
    .hpbx-help-sm();
    .hpbx-help-md();
    .hpbx-help-lg();

    body.hpbx-help-opened {
        .remMarginLeft(@hpbxHelpWidthIe8);
        .remMarginRight(-@hpbxHelpWidthIe8);

        &.hpbx-too-little-space {
            .hpbx-menu {
                .remLeft(174);
            }

            .hpbx-help-open-handle-content-section {
                .remLeft(168.1);
            }

            .hpbx-help-edit-url-popup-trigger {
                .remLeft(171.1);
            }
        }
    }

    .hpbx-help {
        .remLeft(-@hpbxHelpWidthIe8);
        .remWidth(@hpbxHelpWidthIe8);
    }
}

